import React, { Component } from 'react'
import { Dropdown, Tab, Tabs } from "react-bootstrap";
import { useNavigate } from 'react-router';
import { CREATE_FEED } from '../../../../../../../actions/moderationActions';
import FkTab from './fkTab';
import { BASE_URL } from '../../../../../../../actions/api';

const Flickr = (props) => {
  const navigation = useNavigate();

  return <Flkr {...props} navigation={navigation} />;
}
class Flkr extends Component {
  state = {
    text: "",
    manuallyApprove: false,
    error: false,
    loading: false,
    filterType: "Hashtag"
  }

  componentDidMount() {
    this.props.onFilterNameChange(this.state.filterType)
}

  onTextChange = (value) => this.setState({ text: value })
  onManuallyApproveChange = (value) => this.setState({ manuallyApprove: value })

  clearValues = (e) => {
    const {filterType} =this.state;
    
    this.onTextChange("")
    this.onManuallyApproveChange(false)
    
    this.props.onFilterNameChange(e)
   

  }

  onSubmit = () => {
    const {
      text,
      manuallyApprove,
      error,
      filterType
    } = this.state
    const { navigation, feedInfo, hideAddfeed } = this.props
    if (!text || text == "") this.setState({ error: true })
    else {

      this.setState({ error: false, loading : true })

      const ftr = feedInfo && feedInfo.length && feedInfo[0].networkTab && feedInfo[0].networkTab.length ? feedInfo[0].networkTab.findIndex(f => f.feedtype == filterType) : null
      const filterId = feedInfo[0]?.networkTab[ftr]?.filter


      var obj = {
        wallId: localStorage.getItem('wallId'),
        networkId: 6,
        // username: "",
        moderation: manuallyApprove ? 1 : 0,
        // field1: text,
        // field12boardurl: text,
        // authRequire: feedInfo[0].authRequire,
        connetedAccount: 0,
        feedType: filterId,
        account: 0,
        // networkToken: authObj.access_token || "",
        // validity: authObj.expires_in || "",
        // listname,
        // multiplePhoto: multiplePhoto ? 1 : 0,
        // filterType,
        filter: filterId,

        hashtagIGTV: 0,
        onlyReels: 0,
      }
      // SET_CREATE_FEED_OBJ(obj)
      if (filterId == 1) obj['field1'] = text
      if (filterId == 2) {
        obj['field2'] = text
      }
      localStorage.setItem("createFeedObj", JSON.stringify(obj))
      localStorage.setItem("networkId", 6)

      // navigation('/walls/authcallback')
            CREATE_FEED(obj, navigation, true, () => {hideAddfeed();this.setState({ loading: false })})

    }
  }

  render() {
    const {
      text,
      manuallyApprove,
      error,
      filterType,
      loading
    } = this.state
    const { feedInfo } = this.props
    return (
      <div className="form-body create_feed_wt">
        <div className="tabbable-tabdrop newRadio d-flex align-items-center justify-content-between flex-wrap feeds_details createFeedInsta personalizePopup">
          <Tabs defaultActiveKey="Hashtag" id="VimeoFeedTab"  onSelect={(e) => this.setState({ filterType: e },() =>this.clearValues(e))} className="nav-tabs-line nav-tabs-bold w-100 tabDrop mb-5">
            {feedInfo && feedInfo.length && feedInfo[0].networkTab && feedInfo[0].networkTab.length ? feedInfo[0].networkTab.map(filter =>
              <Tab eventKey={filter.feedtype} className="nav-link p-0" title={<span>{filter.feedName}</span>}>
                <FkTab
                  tab={filter.feedtype}
                  textValue={text}
                  onTextChange={this.onTextChange}
                  checkBox={manuallyApprove}
                  onCheckBoxChange={this.onManuallyApproveChange}
                  error={error}
                />
              </Tab>
            ) : null}
          </Tabs>
        </div>
        <div className="form-actions">
          <div className="d-flex justify-content-between align-items-center flex-wrap border-top pt-lg-8 mt-lg-10 pt-5 mt-6">
            <div className="need_help__">
              <p className="d-flex align-items-center flex-wrap mb-sm-0 mb-6">
                Need help in adding feeds?
                <a href='https://vimeo.com/912907290?share=copy' className="video_btn btn btn-text p-0 ml-3 text-dark-75 btn-clean video_support_link" data-lity >
                  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                    <g id="Group_6606" data-name="Group 6606" transform="translate(-1 -1)">
                      <path id="Path_6626" data-name="Path 6626" d="M9.5,16.281V8.461A.545.545,0,0,1,10.34,8l6.083,3.909a.545.545,0,0,1,0,.919L10.34,16.74a.545.545,0,0,1-.84-.458Z" transform="translate(0.773 0.629)" fill="#A5A5A5"></path>
                      <path id="Path_6627" data-name="Path 6627" d="M13,2.636A10.364,10.364,0,1,0,23.364,13,10.364,10.364,0,0,0,13,2.636ZM1,13A12,12,0,1,1,13,25,12,12,0,0,1,1,13Z" fill="#A5A5A5" fillRule="evenodd"></path>
                    </g>
                  </svg>
                  <span className="ms-1">Watch Video</span>
                </a>
              </p>
            </div>
            <div className="hideforBSearch">
              <button type="button" className="btn btn-secondary" onClick={this.props.onBack}>Back</button>
              <span className="create_feed_hideshow">
                <button type="button" className={`commanBtn btn btn-primary ml-4 ${loading ? 'spinner spinner-white disabled spinner-right' : ""} `} id="create_feed" onClick={this.onSubmit}>Create Feed</button>
              </span>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default Flickr