import React, { Component } from 'react'
import Form from 'react-bootstrap/Form'
import { connect } from 'react-redux'
import { AUTOCOMPLETE } from '../../../../../../../actions/manageFeedsAction'
import AccountPicker from '../accountPicker'
import { Dropdown } from 'react-bootstrap'

class FacebookTab extends Component {
    state = { handles: [], allAccounts: [{ ConnectedAccounts: { id: 0, value2: 'loading...' } }], openSelect: false, selectValue: "", selectedAccount: null }

    componentDidMount() {
        const { connectedAcc, onAccountChange } = this.props
        setTimeout(() => { this.getAccounts() }, 1000);
    }

    getAccounts = () => {
        const { connectedAcc, onAccountChange } = this.props
        var allAccounts = []
        if (connectedAcc && connectedAcc.length) {
            connectedAcc && connectedAcc.length && connectedAcc.map(acc => {
                if (acc.ConnectedAccounts.child && acc.ConnectedAccounts.child.length) { allAccounts = [...allAccounts, ...acc.ConnectedAccounts.child] }
                else{ this.setState({ allAccounts : []}) }
            })
            if (allAccounts && allAccounts.length) {
                onAccountChange(allAccounts[0].ConnectedAccounts.id)
                this.setState({ allAccounts, selectValue: allAccounts[0].ConnectedAccounts.value2, selectedAccount: allAccounts[0] })
            }
        }
        else {
            onAccountChange("")
            this.setState({ allAccounts: [] })
        }
    }



    autoComplete = (action, value) => {
        const obj = { action: action, networkId: 3, handle: value, filterType: 8, showHandle: false, }
        AUTOCOMPLETE(obj).then(res => {
            const { data } = res
            if (data.responseCode == 200) { this.setState({ handles: data.response.data }) }
            else this.setState({ handles: [], lists: [] })
        })
    }

    handleTextChange = value => {
        this.autoComplete(1, value)
        this.props.onTextChange(value)
        this.setState({ showHandle: true })
    }

    onSelectHandle = (item) => {
        const { onTextChange, onAccountChange, tab } = this.props
        onTextChange(item.link)
        document.querySelector(`#${tab}-text`).value = item.link
        // onUserIdChange(item.id)
        this.setState({ showHandle: false })
    }

    onAccChange = (childId) => {
        const { onAccountChange } = this.props
        const { allAccounts } = this.state
        var parent = null
        if (allAccounts && allAccounts.length) { 
            parent = allAccounts.find(acc => acc.ConnectedAccounts.id == childId)
            if (parent) { parent = parent.ConnectedAccounts.parent }
        }
        onAccountChange(childId, parent)
    }


    render() {
        const { tab, connectedAcc, text, onTextChange, account, onAccountChange, onAddAccount, album, moderation, onModerationChange, onAlbumChange, connetedAccount,
            onConnectedAccountChange,
            authenticate,
            onAuthenticateChange, error } = this.props
        const { handles, showHandle, allAccounts, openSelect, selectValue, selectedAccount } = this.state

        return (
            <>
                {tab == 'Page' || tab == "Albums" ?
                    <div className="row">
                        {tab == 'Albums' ?
                            <div className="col-md-4">
                                <div className="form-group">
                                    <select name="" className="form-control" value={album} onChange={e => onAlbumChange(e.target.value)}>
                                        <option value={'65'}>Single Album</option>
                                        <option value={'67'}>All Facebook Page Albums</option>
                                    </select>
                                </div>
                            </div> : null}
                        <div className={tab == "Albums" ? "col-md-8" : "col-md-12"}>
                            <div className="form-group">
                                <div className="input-group">
                                    <div className="input-group-prepend">
                                        <span className="input-group-text">
                                            <i className="fa fa-flag"></i>
                                        </span>
                                    </div>
                                    <input autoComplete="off" data-limit="100" placeholder={`${tab == 'Albums' ? album == '65' ? 'enter album URL' : 'enter Page URL' : 'Enter Page Name'}`} value={text} className="autosearch mycheck form-control " id={`${tab}-text`} onChange={(e) => tab == "Page" ? this.handleTextChange(e.target.value) : onTextChange(e.target.value)} type="text" />
                                    <div className="handlelist" id="" style={showHandle ? { display: "block" } : { display: "none" }}>
                                        <ul className="list-unstyled">
                                            {handles && handles.length ? handles.map((item) => (

                                                <li key={item.id} onClick={() => this.onSelectHandle(item)}> 
                                                    <div className="content-section"> {item.name} <p>{item.link}</p> </div>
                                                </li>
                                            )) : null}
                                        </ul>
                                    </div>
                                </div>
                                {error ? <span id="" className="errorMessage required">This field is required</span> : null}
                            </div>
                        </div>
                    </div>
                    : tab == "My Profile Posts" ? null :
                        <div className="form-group text-center mb-6">
                            {allAccounts && allAccounts.length > 0 ?
                                <div className="col-md-12">
                                    <div className="form-group position-relative">
                                    <Dropdown className="w-100 connected_acc__ nexttoinput" id="connectedAll">
                                                <Dropdown.Toggle className="btn w-100 border d-flex justify-content-between align-items-center mt-5">
                                                    <span className="navi-link d-flex align-items-center justify-content-between cursor-pointer m-0 w-100" >
                                                        <div className="user_id d-flex align-items-center justify-content-start conndAcc_popuphover accountProfile">
                                                            <span className="symbol symbol-circle symbol-30 mr-3">
                                                                {
                                                                    selectedAccount && selectedAccount.ConnectedAccounts && selectedAccount.ConnectedAccounts.profilePicture ?
                                                                        <span className="symbol-label" style={{ backgroundImage: `url('${selectedAccount?.ConnectedAccounts?.profilePicture}')` }}></span>
                                                                        :
                                                                        <span className="symbol-label">
                                                                            <svg xmlns="http://www.w3.org/2000/svg" width="70" height="70.06" viewBox="0 0 70 70.06">
                                                                                <path id="Path_6644" data-name="Path 6644" d="M63.917,59.371a35.025,35.025,0,1,0-53.788-.006l-.045.049c.175.21.375.39.555.6.225.258.468.5.7.751.7.761,1.421,1.491,2.177,2.177.23.21.468.405.7.605.8.69,1.624,1.346,2.477,1.956.11.075.21.173.32.25v-.03a34.777,34.777,0,0,0,40.029,0v.03c.11-.078.208-.175.32-.25q1.276-.919,2.477-1.956c.233-.2.47-.4.7-.605.756-.688,1.476-1.416,2.177-2.177.233-.25.473-.493.7-.751.178-.208.38-.388.555-.6ZM37.025,17.016A11.258,11.258,0,1,1,25.767,28.274,11.258,11.258,0,0,1,37.025,17.016Zm-20,42.355A12.5,12.5,0,0,1,29.52,47.037H44.53A12.5,12.5,0,0,1,57.022,59.371,29.871,29.871,0,0,1,17.028,59.371Z" transform="translate(-2 -1.997)" fill="#c6c6c6"></path>
                                                                            </svg>
                                                                        </span>
                                                                }
                                                            </span>
                                                            <div className="font-weight-bold conn_user_name text-dark">{selectedAccount ? selectedAccount.ConnectedAccounts && selectedAccount.ConnectedAccounts.value2 : 'Add Another Account'}</div>
                                                        </div>
                                                        <input type="radio" id="" className="accountOption hidden" />
                                                    </span>
                                                </Dropdown.Toggle>

                                                <Dropdown.Menu className="w-100 navi flex-column navi-hover py-2 connectedAllAccounts">
                                                    <div className="navi flex-column navi-hover py-2 px-5 connectedAllAccounts">Select Account:</div>
                                                    {allAccounts.map(acc => (
                                                        <Dropdown.Item className="navi-item" key={acc.ConnectedAccounts.id} onClick={() => { this.setState({ selectedAccount: acc }, () => this.onAccChange(acc.ConnectedAccounts.id)); onAuthenticateChange(false) }}>
                                                            <span className="navi-link d-flex align-items-center justify-content-between cursor-pointer connected_user m-0 w-100" >
                                                                <div className="user_id d-flex align-items-center justify-content-start conndAcc_popuphover accountProfile">
                                                                     <span className="symbol symbol-circle symbol-30 mr-3">
                                                                     {acc.ConnectedAccounts.profilePicture ?  <span className="symbol-label" style={{ backgroundImage: `url('${acc.ConnectedAccounts.profilePicture}')` }}></span>
                                                                        :
                                                                        <span className="symbol-label">
                                                                            <svg xmlns="http://www.w3.org/2000/svg" width="70" height="70.06" viewBox="0 0 70 70.06">
                                                                                <path id="Path_6644" data-name="Path 6644" d="M63.917,59.371a35.025,35.025,0,1,0-53.788-.006l-.045.049c.175.21.375.39.555.6.225.258.468.5.7.751.7.761,1.421,1.491,2.177,2.177.23.21.468.405.7.605.8.69,1.624,1.346,2.477,1.956.11.075.21.173.32.25v-.03a34.777,34.777,0,0,0,40.029,0v.03c.11-.078.208-.175.32-.25q1.276-.919,2.477-1.956c.233-.2.47-.4.7-.605.756-.688,1.476-1.416,2.177-2.177.233-.25.473-.493.7-.751.178-.208.38-.388.555-.6ZM37.025,17.016A11.258,11.258,0,1,1,25.767,28.274,11.258,11.258,0,0,1,37.025,17.016Zm-20,42.355A12.5,12.5,0,0,1,29.52,47.037H44.53A12.5,12.5,0,0,1,57.022,59.371,29.871,29.871,0,0,1,17.028,59.371Z" transform="translate(-2 -1.997)" fill="#c6c6c6"></path>
                                                                            </svg>
                                                                        </span>}
                                                                    </span>
                                                                    <div className="font-weight-bold conn_user_name text-dark">{acc.ConnectedAccounts.value2}</div>
                                                                </div>
                                                               
                                                            </span>
                                                        </Dropdown.Item>
                                                    ))
                                                    }
                                                    <Dropdown.Item className="navi-item" onClick={() => { this.onAccChange(0); this.setState({ selectedAccount: 0 }) }}>
                                                        <span className="navi-link d-flex align-items-center justify-content-between flex-wrap cursor-pointer m-0 forAddNew accountProfile w-100" htmlFor="otherAccount">
                                                            <div className="user_id d-flex align-items-center justify-content-start conndAcc_popuphover accountProfile">
                                                                <span className="symbol symbol-circle symbol-30 mr-3">
                                                                    <span className="symbol-label">
                                                                        <svg xmlns="http://www.w3.org/2000/svg" width="70" height="70.06" viewBox="0 0 70 70.06">
                                                                            <path id="Path_6644" data-name="Path 6644" d="M63.917,59.371a35.025,35.025,0,1,0-53.788-.006l-.045.049c.175.21.375.39.555.6.225.258.468.5.7.751.7.761,1.421,1.491,2.177,2.177.23.21.468.405.7.605.8.69,1.624,1.346,2.477,1.956.11.075.21.173.32.25v-.03a34.777,34.777,0,0,0,40.029,0v.03c.11-.078.208-.175.32-.25q1.276-.919,2.477-1.956c.233-.2.47-.4.7-.605.756-.688,1.476-1.416,2.177-2.177.233-.25.473-.493.7-.751.178-.208.38-.388.555-.6ZM37.025,17.016A11.258,11.258,0,1,1,25.767,28.274,11.258,11.258,0,0,1,37.025,17.016Zm-20,42.355A12.5,12.5,0,0,1,29.52,47.037H44.53A12.5,12.5,0,0,1,57.022,59.371,29.871,29.871,0,0,1,17.028,59.371Z" transform="translate(-2 -1.997)" fill="#c6c6c6"></path>
                                                                        </svg>
                                                                    </span>
                                                                </span>
                                                                <div className="font-weight-bold conn_user_name text-dark">Add Another Account</div>
                                                            </div>
                                                            <span className="symbol symbol-circle symbol-40 mr-1">
                                                                <span className="svg-icon svg-icon-2x svg-icon-info">
                                                                    <svg xmlns="http://www.w3.org/2000/svg" width="70" height="70" viewBox="0 0 70 70">
                                                                        <path id="Path_6645" data-name="Path 6645" d="M59,24A35,35,0,1,0,94,59,35,35,0,0,0,59,24Zm0,64.615A29.615,29.615,0,1,1,88.615,59,29.615,29.615,0,0,1,59,88.615ZM75.154,59a2.692,2.692,0,0,1-2.692,2.692H61.692V72.462a2.692,2.692,0,0,1-5.385,0V61.692H45.538a2.692,2.692,0,1,1,0-5.385H56.308V45.538a2.692,2.692,0,0,1,5.385,0V56.308H72.462A2.692,2.692,0,0,1,75.154,59Z" transform="translate(-24 -24)" fill="#c6c6c6"></path>
                                                                    </svg>
                                                                </span>
                                                            </span>
                                                            <input type="radio" id="otherAccount" className="accountOption hidden" />
                                                        </span>
                                                    </Dropdown.Item>
                                                </Dropdown.Menu>
                                            </Dropdown>
                                        {/* <select  className="form-control" value={account} disabled={allAccounts[0].ConnectedAccounts.id == 0} onChange={e => this.onAccChange(e.target.value)}>
                                        {allAccounts.map(acc => (
                                            <option value={acc.ConnectedAccounts.id} key={acc.ConnectedAccounts.id}>{acc.ConnectedAccounts.value2}</option>
                                        ))}
                                        <option value={0}>other</option>
                                    </select> */}
                                    </div>
                                </div>
                                :
                                <div className="py-5">
                                    <div className="btn btn-outline-gray btn-lg w-auto btn-clean d-inline-flex align-items-center btn-lg px-6" onClick={onAddAccount}>
                                        <span className="symbol symbol-25 mr-3">
                                            <img src="https://cloud.taggbox.com/taggshop/app/assets/media/social-icons/facebook.svg" />
                                        </span>
                                        <span className="text-dark-50 font-weight-bolder font-size-base">Connect Facebook</span>
                                    </div>
                                </div>
                            }
                        </div>}
                <div className="hideforBSearch">
                    <div className="checkbox-inline">
                        <label htmlFor="feedscreatemoderation" className="checkbox checkbox-outline checkbox-primary mr-2">
                            <input type="checkbox" id="feedscreatemoderation" checked={moderation} onChange={(e) => onModerationChange(e.target.checked)} className="checkbox checkbox-outline checkbox-outline-2x checkbox-primary mr-2" />
                            <span></span>
                            Manually approve posts before making them public?
                        </label>
                    </div>
                </div>
                {connectedAcc && connectedAcc.length && !["Page Reviews", "Page Mentions"].includes(tab) ? <AccountPicker connectedAcc={connectedAcc} networkId={'3'} onConnectedAccountChange={onConnectedAccountChange} connetedAccount={connetedAccount} onAuthenticateChange={onAuthenticateChange} authenticate={authenticate} /> : null}
            </>
        )
    }
}

const mapStateToProps = (state) => { return { } }
const mapDispatchToProps = dispatch => { return { } }

export default connect(mapStateToProps, mapDispatchToProps)(FacebookTab)
