import { useState } from "react";
import { useEffect } from "react"; 
import { CLOUDPATH_TAGGSHOP } from "../../../../../../../actions/api";
import { SEND_UGC } from "../../../../../../../actions/moderationActions";
import CardPreview from "./CardPreview"; 
import CopyUrl from "./Tabs/copyUrl";
import GeneralRequest from "./Tabs/generalRequest";
import SocialAccounts from "./Tabs/SocialAccounts";
// import './ugc-rights.css';
// import '../../../assets/css/ugc-rights.css';
import './ugc-rights-new.css';
import ChooseMethod from "./Tabs/chooseMethod";
import { useDispatch } from "react-redux";
import { TAGGSHOP_SOCIAL_ACCOUNT_DATA } from "../../../../../../../actions/sceneActions";
import ErrorFound from "../../../../../../ErrorFound";


const UgcModal = (props) => {
  const { hideUgc, ugcData } = props
  const [ugcTabKey, setUgcTabKey] = useState('general')
  const [custom_msg, setCustom_msg] = useState("")
  const [selectedTemplate, setSelectedTemplate] = useState(1)
  const [acceptTnc, setAcceptTnc] = useState(false)
  const [tncError, setTncError] = useState(false)
  const [step, setStep] = useState(4)
  const [isAccount, setIsAccount] = useState('')
  const [userName, setUserName] = useState('')
  const [brand_link, setBrand_link] = useState('')
  const [termslink, setTermslink] = useState('')
  const [termstype, setTermstype] = useState('') 
  const [requestMode, setRequestMode] = useState() 
  const dispatch = useDispatch()


  useEffect(() => {
    dispatch(TAGGSHOP_SOCIAL_ACCOUNT_DATA())
  }, [])
 
  const sendRequest=()=>{
    if(isAccount){
      setStep(3)
      onRaiseRequest()
    }
  }

  const getPostIds = () => {
    const { template, data, unique_code, bitlyurl } = ugcData
    let postIds = []
    if (data.length > 0) {
      data.map((item, index) => {
        postIds.push(item.referenceId)
      })
    }
    return postIds.join(',')
  }


  const onRaiseRequest = (hash) => {
    const { template, data, unique_code, bitlyurl } = ugcData
    const index = template.findIndex(t => t.id == selectedTemplate)
    console.log('userName', userName)
    var obj = {
      custom_message: custom_msg,
      feedId: data[0].feedId,
      networkId: data[0].networkId,
      selectedImage: data[0].postFile,
      selectedVideo: '',
      selectedUsername: data[0].username,
      handle:userName,
      postId: data.length > 1 ? getPostIds() : data[0].referenceId,
      brand_link: brand_link ? brand_link :template[index].client_logo,
      termslink: termslink ? termslink:template[index].custom_terms,
      termstype: termstype ? termstype:template[index].custom_terms_name,
      bitlylink: bitlyurl,
      unique_code: unique_code,
      rightsVia: requestMode,
      hashtag: hash,
      networkPostId: data[0].postId
    }
    SEND_UGC(obj)
  }

  return (
    <>
      <div id="ugcstatusUGC" >
        <div className="ugc-modal" id="ugcModal">
          <div className="modal-dialog m-w-100 m-0" role="document">
            <div className="modal-content rounded-0">
              <div className="modal-header py-3">
                <div className="d-flex align-items-center flex-nowrap">
                  <span className="svg-icon-col mr-2">
                    <svg xmlns="http://www.w3.org/2000/svg" width="28" height="28" viewBox="0 0 24 24">
                      <g id="Group_5456" data-name="Group 5456" transform="translate(9565 7108)">
                        <rect id="Rectangle_2321" data-name="Rectangle 2321" width="24" height="24" transform="translate(-9565 -7108)" fill="none"></rect>
                        <g id="Group_5455" data-name="Group 5455" transform="translate(-9562.424 -7106.873)">
                          <g id="Group_4836" data-name="Group 4836">
                            <path id="Path_5298" data-name="Path 5298" d="M-125.457-53.668l-8.2-3.4a1.089,1.089,0,0,0-.848,0l-8.2,3.4a1.1,1.1,0,0,0-.682,1.02v4.139A14.225,14.225,0,0,0-134.5-35.284a1.112,1.112,0,0,0,.418.082,1.1,1.1,0,0,0,.417-.082,14.225,14.225,0,0,0,8.889-13.226v-4.139A1.1,1.1,0,0,0-125.457-53.668Z" transform="translate(143.384 57.159)" fill="#6ac259"></path>                                             <path id="Path_5297" data-name="Path 5297" d="M-136.916-48.121a1.1,1.1,0,0,0-1.414,0,1.109,1.109,0,0,0-.125,1.573l2.339,2.333a1.1,1.1,0,0,0,.778.319,1.1,1.1,0,0,0,.78-.32l4.171-4.172a1.139,1.139,0,0,0,.139-.139,1.106,1.106,0,0,0-.14-1.556,1.1,1.1,0,0,0-1.539.123l-3.408,3.407Z" transform="translate(143.715 57.644)" fill="#fff"></path>
                          </g>
                        </g>
                      </g>
                    </svg>
                  </span>
                  <h5 className="modal-title">UGC Request</h5>
                </div>
                <button type="button" className="close btn ugc_close" data-bs-dismiss="modal" aria-label="Close" onClick={() => hideUgc()}>
                  <i className="ki ki-close icon-md text-muted"></i>
                </button>
              </div>

              <div className="modal-body p-0">
                <form className="noLoader maintainPopUp" id="get_ugc" method="POST" acceptCharset="utf-8">
                  <div className="container-fluid">
                    <div className="cardStyleSec">
                      <div className="row">
                        <div className="ugc_card_preview rights_modal_inner d-flex flex-column flex-grow-1 p-lg-4 p-xxl-5 position-relative mb-2 mb-lg-0">
                          {ugcData ? <CardPreview ugcData={ugcData} /> : null}
                        </div>
                        <div className="rights_modal_sidebar position-relative p-0">
                          <div className="pb-0 modal_sec_right">
                            <div className="ugc_req card card-custom shadow-none" id="myTabContentMD">
                              <ul className="nav nav-tabs md-tabs" id="myTabMD" role="tablist">
                              {![1,2,3].includes(step) ? <li className="nav-item waves-effect waves-light">
                                  {/* <span className={`nav-link  px-0 py-5 ${ step ==4 ? 'active' : ""}`} id="home-tab-md" onClick={()=>setStep(4)}>
                                    <div className="ugcCheck_icon">
                                      <div className="ts__icon ts-check"></div>
                                    </div>
                                    Choose Method
                                  </span> */}
                                </li> : <></>}
                                {step != 4 ? <><li className="nav-item waves-effect waves-light">
                                  <span className={`nav-link  px-0 py-5 ${ [1,2,3].includes(step) ? 'active' : ""}`} id="home-tab-md" onClick={()=>setStep(1)}>
                                    {/* <img src={`${CLOUDPATH_TAGGSHOP}/assets/images/ugc-rights/check-icon.svg`} alt="check" className="img-responsive" />  */}
                                    <div className="ugcCheck_icon">
                                      <div className="ts__icon ts-check"></div>
                                    </div>
                                    General Request
                                  </span>
                                </li>
                                <li className="nav-item waves-effect waves-light">
                                  <span className={`nav-link  px-0 py-5 ${((acceptTnc && requestMode == 0) || requestMode == 1) && [2,3].includes(step) ? 'active' : ""}`} id="profile-tab-md"  onClick={()=>acceptTnc ? setStep(2):''}>
                                    {/* <img src={`${CLOUDPATH_TAGGSHOP}/assets/images/ugc-rights/check-icon.svg`} alt="check" className="img-responsive" />  */}
                                    <div className="ugcCheck_icon">
                                      <div className="ts__icon ts-check"></div>
                                    </div>
                                    Social Account
                                  </span>                                                 
                                </li>                                                 
                                <li className="nav-item waves-effect waves-light">                                                     
                                  <span className={`nav-link  px-0 py-5 ${[3].includes(step) ? 'active' : ""}`} id="contact-tab-md"> 
                                    {/* <img src={`${CLOUDPATH_TAGGSHOP}/assets/images/ugc-rights/check-icon.svg`} alt="check" className="img-responsive" /> */}
                                    <div className="ugcCheck_icon">
                                      <div className="ts__icon ts-check"></div>
                                    </div>
                                    Preview
                                  </span>                                                 
                                </li> </> : <></>}                                            
                              </ul>

                              {ugcData && step ==4? <ErrorFound><ChooseMethod ugcData={ugcData} requestMode={requestMode}  setStep={setStep} setRequestMode={setRequestMode} hideUgc={hideUgc}/></ErrorFound> : null}

                              {ugcData && step ==1?  <ErrorFound><GeneralRequest ugcData={ugcData} requestMode={requestMode}  setStep={setStep} setAcceptTnc={setAcceptTnc} acceptTnc={acceptTnc} tncError={tncError} setTncError={setTncError} selectedTemplate={selectedTemplate} setSelectedTemplate={setSelectedTemplate} setCustom_msg={setCustom_msg} custom_msg={custom_msg} setBrand_link={setBrand_link} brand_link={brand_link} setTermslink={setTermslink} setTermstype={setTermstype}  termstype={termstype} hideUgc={hideUgc}/></ErrorFound> : null}

                              {((acceptTnc && requestMode == 0) || requestMode == 1) && step ==2?  <ErrorFound><SocialAccounts  setStep={setStep} setUserName={setUserName} setIsAccount={setIsAccount} networkId={ugcData.data[0].networkId} /></ErrorFound>:""}

                              {custom_msg && ugcData && step ==3 ? <ErrorFound><CopyUrl requestMode={requestMode} onRaiseRequest={onRaiseRequest} setUserName={setUserName} networkId={ugcData.data[0].networkId} setUgcTabKey={setUgcTabKey} setStep={setStep} ugcData={ugcData} custom_msg={custom_msg}  /></ErrorFound> : null}
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </form>
              </div>


            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default (UgcModal);
