import React, { Component } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { useCookies, withCookies } from 'react-cookie';
import { TAGGSHOP_SOCIAL_ACCOUNT_DATA } from '../../../../../../../../actions/sceneActions';
import { Dropdown } from 'react-bootstrap';
import { showAddAccounntAlert } from '../utils';
import { useNavigate } from 'react-router';
import { addAccountFromId } from '../../../../../../../../utils/addAccount';
import { GET_LOCAL } from '../../../../../../../../utils';

// eslint-disable-next-line import/no-anonymous-default-export

const SocialAccounts = (props) => {
  const [cookies, setCookie] = useCookies([]);
  const navigation = useNavigate()
  const dispatch = useDispatch()
  const socialAccountsData = useSelector(state => state.userAccountData.socialAccounts)
  return <SAccounts {...props} cookies={cookies} navigation={navigation} dispatch={dispatch} socialAccountsData={socialAccountsData} />
}
class SAccounts extends Component {
  constructor() {
        super();
        this.controller = new AbortController();

    }
  state = { socialAccount: 0, authorName: null, authorAvatar: null, errorMsg: false, otherAc: false, accountDetails: {}, accountList: [], selectedPersonalAccount: null }


  handleMessage = async (event) => {
    const { error, code } = event.data;
    if ((event.origin.includes("tagshop.ai") || event.origin.includes("localhost"))) {
        if (code) {
          const networkId = GET_LOCAL("networkId")
            console.log('event', event)
            // await addAccountFromId(this.props.networkId, code, this.props.navigation)
            await addAccountFromId(networkId, code, () => this.props.dispatch(TAGGSHOP_SOCIAL_ACCOUNT_DATA(null, () => this.filterAccountData(networkId) )))
            
        }
    }
};


  componentDidMount() {
    this.filterAccountData(18)
    const signal = this.controller.signal;
    document.cookie = "callBackUrl=React";
    window.addEventListener("message", this.handleMessage, { signal });
    if (window.Application) window.Application.reInit()
    if (this.props.networkId == parseInt(JSON.parse(localStorage.getItem('instagram_ugc'))?.networkId)) {
      this.setState({ accountDetails: JSON.parse(localStorage.getItem('instagram_ugc')) })
    }
    else this.setState({ accountDetails: null })
    console.log(this.state.accountDetails, JSON.parse(localStorage.getItem('instagram_ugc')))

  }

  componentWillUnmount() {
    this.controller.abort()
    localStorage.removeItem("networkId")
   }

  filterAccountData = (id) => {
    const { socialAccountsData, setUserName } = this.props 
    var allAcc = []
    if(socialAccountsData && socialAccountsData.accountInformations && socialAccountsData.accountInformations.length) {
      socialAccountsData.accountInformations.filter(acc => {
        if(acc.Network.id == id){
          if(acc.ConnectedAccounts.child && acc.ConnectedAccounts.child.length) allAcc = [...allAcc, ...acc.ConnectedAccounts.child]
        }
      })
      this.setState({accountList : allAcc, selectedPersonalAccount: allAcc && allAcc[0] ? allAcc[0] :''}, () => setUserName(allAcc && allAcc[0] ? allAcc[0].ConnectedAccounts.value2:''))
    }
  }

  sendRequest = () => {
    const { setStep, onRaiseRequest, setUserName, networkId } = this.props
    if (this.state.accountDetails) {
      // if(networkId == 7) setUserName(this.state.accountDetails.email)
      // else 
      setUserName(this.state.accountDetails.username)
      console.log(this.state.accountDetails.username)
      setStep(3)
      // onRaiseRequest()
    }
    else { this.setState({ errorMsg: true }) }
  }
  
  nextStep = () => {
    const { setStep } = this.props
    if(this.state.selectedPersonalAccount) setStep(3)
    else { this.setState({ errorMsg: true }) }
  }



  render() {
    const { setStep, networkId, setUserName } = this.props
    const { authorName, authorAvatar, socialAccount, errorMsg, otherAc, accountList , selectedPersonalAccount} = this.state
    return (
      <>
        <div id="profile-md">
          <div className="ugc_steps ugc-logged-ac modal-body">

            <Dropdown className="w-100 connected_acc__ nexttoinput" id="connectedAll">
              <Dropdown.Toggle className="btn w-100 border d-flex justify-content-between align-items-center mt-5">
                <span className="navi-link d-flex align-items-center justify-content-between cursor-pointer m-0 w-100" >
                  <div className="user_id d-flex align-items-center justify-content-start conndAcc_popuphover accountProfile">
                    <span className="symbol symbol-circle symbol-30 mr-3">
                      {
                        selectedPersonalAccount && selectedPersonalAccount.ConnectedAccounts && selectedPersonalAccount.ConnectedAccounts.profilePicture ?
                          <span className="symbol-label" style={{ backgroundImage: `url('${selectedPersonalAccount?.ConnectedAccounts?.profilePicture}')` }}></span>
                          :
                          <span className="symbol-label">
                            <svg xmlns="http://www.w3.org/2000/svg" width="70" height="70.06" viewBox="0 0 70 70.06">
                              <path id="Path_6644" data-name="Path 6644" d="M63.917,59.371a35.025,35.025,0,1,0-53.788-.006l-.045.049c.175.21.375.39.555.6.225.258.468.5.7.751.7.761,1.421,1.491,2.177,2.177.23.21.468.405.7.605.8.69,1.624,1.346,2.477,1.956.11.075.21.173.32.25v-.03a34.777,34.777,0,0,0,40.029,0v.03c.11-.078.208-.175.32-.25q1.276-.919,2.477-1.956c.233-.2.47-.4.7-.605.756-.688,1.476-1.416,2.177-2.177.233-.25.473-.493.7-.751.178-.208.38-.388.555-.6ZM37.025,17.016A11.258,11.258,0,1,1,25.767,28.274,11.258,11.258,0,0,1,37.025,17.016Zm-20,42.355A12.5,12.5,0,0,1,29.52,47.037H44.53A12.5,12.5,0,0,1,57.022,59.371,29.871,29.871,0,0,1,17.028,59.371Z" transform="translate(-2 -1.997)" fill="#c6c6c6"></path>
                            </svg>
                          </span>
                      }
                    </span>
                    <div className="font-weight-bold conn_user_name text-dark">{selectedPersonalAccount ? selectedPersonalAccount.ConnectedAccounts && selectedPersonalAccount.ConnectedAccounts.value2 : 'Add Another Account'}</div>
                  </div>
                  <input type="radio" id="" className="accountOption hidden" />
                </span>
              </Dropdown.Toggle>

              <Dropdown.Menu className="w-100 navi flex-column navi-hover py-2 connectedAllAccounts">
                <div className="navi flex-column navi-hover py-2 px-5 connectedAllAccounts">Select Account:</div>
                {accountList.map(acc => (
                  <Dropdown.Item className="navi-item" key={acc.ConnectedAccounts.id} onClick={() => { this.setState({ selectedPersonalAccount: acc },() => setUserName(acc.ConnectedAccounts.value2))}}>
                    <span className="navi-link d-flex align-items-center justify-content-between cursor-pointer connected_user m-0 w-100" >
                      <div className="user_id d-flex align-items-center justify-content-start conndAcc_popuphover accountProfile">
                        <span className="symbol symbol-circle symbol-30 mr-3">
                          <span className="symbol-label" style={{ backgroundImage: `url('${acc.ConnectedAccounts.profilePicture}')` }}></span>
                        </span>
                        <div className="font-weight-bold conn_user_name text-dark">{acc.ConnectedAccounts.value2}</div>
                      </div>

                    </span>
                  </Dropdown.Item>
                ))
                }
                <Dropdown.Item className="navi-item" onClick={() => { showAddAccounntAlert(networkId) }}>
                  <span className="navi-link d-flex align-items-center justify-content-between flex-wrap cursor-pointer m-0 forAddNew accountProfile w-100" htmlFor="otherAccount">
                    <div className="user_id d-flex align-items-center justify-content-start conndAcc_popuphover accountProfile">
                      <span className="symbol symbol-circle symbol-30 mr-3">
                        <span className="symbol-label">
                          <svg xmlns="http://www.w3.org/2000/svg" width="70" height="70.06" viewBox="0 0 70 70.06">
                            <path id="Path_6644" data-name="Path 6644" d="M63.917,59.371a35.025,35.025,0,1,0-53.788-.006l-.045.049c.175.21.375.39.555.6.225.258.468.5.7.751.7.761,1.421,1.491,2.177,2.177.23.21.468.405.7.605.8.69,1.624,1.346,2.477,1.956.11.075.21.173.32.25v-.03a34.777,34.777,0,0,0,40.029,0v.03c.11-.078.208-.175.32-.25q1.276-.919,2.477-1.956c.233-.2.47-.4.7-.605.756-.688,1.476-1.416,2.177-2.177.233-.25.473-.493.7-.751.178-.208.38-.388.555-.6ZM37.025,17.016A11.258,11.258,0,1,1,25.767,28.274,11.258,11.258,0,0,1,37.025,17.016Zm-20,42.355A12.5,12.5,0,0,1,29.52,47.037H44.53A12.5,12.5,0,0,1,57.022,59.371,29.871,29.871,0,0,1,17.028,59.371Z" transform="translate(-2 -1.997)" fill="#c6c6c6"></path>
                          </svg>
                        </span>
                      </span>
                      <div className="font-weight-bold conn_user_name text-dark">Add Another Account</div>
                    </div>
                    <span className="symbol symbol-circle symbol-40 mr-1">
                      <span className="svg-icon svg-icon-2x svg-icon-info">
                        <svg xmlns="http://www.w3.org/2000/svg" width="70" height="70" viewBox="0 0 70 70">
                          <path id="Path_6645" data-name="Path 6645" d="M59,24A35,35,0,1,0,94,59,35,35,0,0,0,59,24Zm0,64.615A29.615,29.615,0,1,1,88.615,59,29.615,29.615,0,0,1,59,88.615ZM75.154,59a2.692,2.692,0,0,1-2.692,2.692H61.692V72.462a2.692,2.692,0,0,1-5.385,0V61.692H45.538a2.692,2.692,0,1,1,0-5.385H56.308V45.538a2.692,2.692,0,0,1,5.385,0V56.308H72.462A2.692,2.692,0,0,1,75.154,59Z" transform="translate(-24 -24)" fill="#c6c6c6"></path>
                        </svg>
                      </span>
                    </span>
                    <input type="radio" id="otherAccount" className="accountOption hidden" />
                  </span>
                </Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown>
            {errorMsg ? <span id="social_account_error" className="errorMessage required">
                Please connect account
              </span> : ''}
          </div>

          <span id="reSearch_cookies" onClick={() => this.getCookieData()}></span>
          <div className="ugc-peingdone modal-footer text-right">
            <input className="btn btn-secondary mr-3" type="button" value="Back" onClick={() => setStep(1)} />
            <input className="btn btn-primary" type="button" value="Next Step" onClick={() => this.nextStep()} />
          </div>
        </div>


        {/* <LoginSocialFacebook
          // isOnlyGetToken
          appId={`117029975354072`}
          // onLoginStart={onLoginStart}
          onResolve={({ provider, data }) => {
            console.log('{ provider, data }', { provider, data })
          }}
          onReject={(err) => {
            console.log(err)
          }}
        >
          <button id='ugc_fb'>fb</button>
        </LoginSocialFacebook> */}


      </>
    )
  }
}
export default withCookies(SocialAccounts)