
import React, { Component } from 'react'
import { Modal } from "react-bootstrap";
import { Tabs, Tab } from 'react-bootstrap'
import Dropdown from 'react-bootstrap/Dropdown';
import Facebookfeed from './facebook/facebookfeed';
import Instagramfeed from './instagram/instagramfeed';
import Pinterest from './pinterest/pinterest';
import TwitterFeed from './twitter/twitterFeed';
import Airbnb from './airbnb/airbnb';
import Youtube from './youtube/youtube';
import Tumblr from './tumblr/tumblr';
import Rss from './rss/rss';
import Flickr from './flickr/flickr';
import Googlefeed from './googlereviews/googlefeed';
import Onsitefeed from './onsiteupload/onsitefeed';
import Vimeofeed from './vimeo/vimeofeed';
import Tiktokfeed from './tiktok/tiktokfeed';
import Vkontaktefeed from './vkontakte/vkontaktefeed';
import { ACTION_ADD_AUTH_OBJ } from '../../../../../../actions/addFeedActions';
import { connect } from 'react-redux';
import LoadingScreen from '../../../../../pages/LoadingScreen';
import { clickToRefresh, getModeration } from '../../../../../../actions/moderationActions';
import { GET_FEEDS } from '../../../../../../actions/manageFeedsAction';
import withRouter from '../../../../../routes/WithRouter';
// import Connectaccount from './connectaccount';

class createFeed extends Component {
    state = {
        modalShow: true,
        filterName: ""
    }
    clickToToggleModal = () => {
        this.setState({
            modalShow: !this.state.modalShow
        })
    }

    onFilterNameChange = (value) => this.setState({ filterName: value })

    createFeed = (id) => {
        const { feedInfo, ACTION_ADD_AUTH_OBJ, hideAddfeed, getModeration, onBack,GET_FEEDS, navigate } = this.props
        const onHideFeed = () => {
            setTimeout(() => {
                getModeration();
                GET_FEEDS()
            }, 1000);
            // hideAddfeed()
            navigate(`/content/moderation/${localStorage.getItem('wallId')}`)
        }
        switch (id) {
            case 1:
                return feedInfo ? <TwitterFeed feedInfo={feedInfo} networkId={id} hideAddfeed={onHideFeed} onBack={onBack} onFilterNameChange={this.onFilterNameChange} /> : null

            case 18:
                return feedInfo ? <Instagramfeed feedInfo={feedInfo} networkId={id} hideAddfeed={onHideFeed} onBack={onBack} onFilterNameChange={this.onFilterNameChange} ACTION_ADD_AUTH_OBJ={ACTION_ADD_AUTH_OBJ} /> : null

            case 3:
                return feedInfo ? <Facebookfeed feedInfo={feedInfo} networkId={id} hideAddfeed={onHideFeed} onBack={onBack} onFilterNameChange={this.onFilterNameChange} ACTION_ADD_AUTH_OBJ={ACTION_ADD_AUTH_OBJ} /> : null

            case 5:
                return feedInfo ? <Pinterest feedInfo={feedInfo} networkId={id} hideAddfeed={onHideFeed} onBack={onBack} onFilterNameChange={this.onFilterNameChange} /> : <LoadingScreen />

            case 23:
                return feedInfo ? <Airbnb feedInfo={feedInfo} networkId={id} hideAddfeed={onHideFeed} onBack={onBack} onFilterNameChange={this.onFilterNameChange} /> : null

            case 7:
                return feedInfo ? <Youtube feedInfo={feedInfo} networkId={id} hideAddfeed={onHideFeed} onBack={onBack} onFilterNameChange={this.onFilterNameChange} /> : null

            case 11:
                return feedInfo ? <Tumblr feedInfo={feedInfo} networkId={id} hideAddfeed={onHideFeed} onBack={onBack} onFilterNameChange={this.onFilterNameChange} /> : null

            case 12:
                return feedInfo ? <Rss feedInfo={feedInfo} networkId={id} hideAddfeed={onHideFeed} onBack={onBack} onFilterNameChange={this.onFilterNameChange} /> : null

            case 6:
                return feedInfo ? <Flickr feedInfo={feedInfo} networkId={id} hideAddfeed={onHideFeed} onBack={onBack} onFilterNameChange={this.onFilterNameChange} /> : null

            case 4:
                return feedInfo ? <Googlefeed feedInfo={feedInfo} networkId={id} hideAddfeed={onHideFeed} onBack={onBack} onFilterNameChange={this.onFilterNameChange} ACTION_ADD_AUTH_OBJ={ACTION_ADD_AUTH_OBJ} /> : null

            case 29:
                return feedInfo ? <Onsitefeed feedInfo={feedInfo} networkId={id} hideAddfeed={onHideFeed} onBack={onBack} onFilterNameChange={this.onFilterNameChange} /> : null

            case 8:
                return feedInfo ? <Vimeofeed feedInfo={feedInfo} networkId={id} hideAddfeed={onHideFeed} onBack={onBack} onFilterNameChange={this.onFilterNameChange} /> : null

            case 28:
                return feedInfo ? <Tiktokfeed feedInfo={feedInfo} networkId={id} hideAddfeed={onHideFeed} onBack={onBack} onFilterNameChange={this.onFilterNameChange} /> : null

            case 30:
                return feedInfo ? <Vkontaktefeed feedInfo={feedInfo} networkId={id} hideAddfeed={onHideFeed} onBack={onBack} onFilterNameChange={this.onFilterNameChange} /> : null



            default:
                return <>network id - {id}</>
        }
    }

    render() {
        const { network, addFeedData } = this.props
        const { filterName } = this.state

        return (
            <>
                <div className={`add-feed-modal-body ${network.networkId == 29 ? 'snapUp_feed_modal' : ''} `}>
                    <div className="container">
                        <div id="feed_content__">
                            <form className="maintainPopUp noLoader" id="feedPostUrlForm">
                                <div className="small_container">
                                    <div className="middle_content">
                                        <div id="create_feed_section" className="card card-custom card-body">
                                            <div className="pt-0">
                                                <div className="d-flex flex-wrap align-items-center mb-10 modal_head">
                                                    <div className="symbol symbol-60 flex-shrink-0 mr-4">
                                                        <div className="symbol-label" style={{ backgroundImage: `url(https://cloud.taggbox.com/taggshop/app/assets/media/square-icons/${network.networkCode}.svg?ver=1)` }}></div>
                                                    </div>
                                                    <h3 className="d-flex flex-column flex-grow-1 my-lg-0 my-2 pr-3">
                                                        <span className="card-label font-weight-bolder text-dark networkTitle">{network.networkName}</span>
                                                        <span className=" mt-2 font-weight-bold font-size-sm networkDesc">Add your {network.networkName} <span id="changeTxt">{filterName}</span></span>
                                                    </h3>
                                                </div>
                                            </div>
                                            {this.createFeed(network.networkId)}
                                        </div>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </>
        )
    }
}

const mapStateToProps = (state) => {
    return {
    }
}
const mapDispatchToProps = dispatch => {
    return {
        ACTION_ADD_AUTH_OBJ: (data) => dispatch(ACTION_ADD_AUTH_OBJ(data)),
        getModeration: () => dispatch(getModeration()),
        GET_FEEDS:() => dispatch(GET_FEEDS())

    }
}
export default connect(mapStateToProps, mapDispatchToProps)(withRouter(createFeed));