import React, { Component } from 'react'
import { connect } from 'react-redux'
import OnsiteUpload from '../../OnsiteUpload/OnsiteUpload'

class Onsitefeed extends Component {

    render() {
        return <OnsiteUpload onBack={() => {}} {...this.props} onUGCUploadCollection={null} />
        //   return <UGCUploader title={'SnapUp'} HideAppsMore={this.props.closeModal} appsMore={this.props.appsMore} getNewData={() => this.props.getAppsMOreDetails(getWallID())} fonts={this.props.fonts} />
    }
  }
  
  const mapStateToProps = (state) => {
      return {
          appsMore:state.appsMore,
          fonts: state.appsMore.fonts,
      }
  }
  
  const mapDispatchToProps = dispatch => {
      return {
          closeModal: () => {},
      }
  }
  
  export default connect(mapStateToProps, mapDispatchToProps)(Onsitefeed);
  