import React, { Component } from 'react'
import { connect } from 'react-redux'
import { CLEAR_UGC_DATA, CLEAR_UGC_REQUEST, ugcModal } from '../../../../../../actions/moderationActions'
import FilterBody from './filterBody'
import FilterTab from './filterBody/filterTab'
import ModerationPost from './moderationPost'
import UgcModal from './UGC'
import ApprovedModal from './UGC/ugcStatus/approvedModal'
import Pendingng from './UGC/ugcStatus/pending'
import RejectModal from './UGC/ugcStatus/rejectModal'
class View extends Component {
 state={ statusPopup:0}
    onHideUgc = () => {
        this.props.ugcModal(false)
        this.props.clearUgcData()
        // this.setState({ugcPopup: false})
    }
    openugcModel=()=>{
        this.props.ugcModal(true)
    }
    onShow=(id)=>{
        const { ugcData, ugcPopup, ugcRequestData } = this.props
        this.setState({statusPopup:id});
    }
    // onHide=()=>{this.setState({statusPopup:0})}
    render() {
        const { ugcData, ugcPopup, ugcRequestData, CLEAR_UGC_REQUEST_DATA } = this.props
        const { statusPopup } = this.state
        return <>
            <div className="position-relative">
                <div className="d-flex flex-column-fluid main_content dual_subheader_content">
                    <FilterBody {...this.props} />
                    <div className="container-fluid">
                        <FilterTab  {...this.props} />
                        <ModerationPost {...this.props} onShow={this.onShow}/>
                    </div>
                </div>
            </div>
            {statusPopup==1 && ugcRequestData && ugcRequestData.username ? <Pendingng ugcData={ugcData} ugcRequestData={ugcRequestData} onHide={()=>{this.setState({statusPopup:0}); CLEAR_UGC_REQUEST_DATA()}}/> : null}
            {statusPopup==3 && ugcRequestData && ugcRequestData.username ? <RejectModal ugcRequestData={ugcRequestData} ugcData={ugcData} onHide={()=>{this.setState({statusPopup:0}); CLEAR_UGC_REQUEST_DATA()}} openugcModel={this.openugcModel}/> : null}
            {statusPopup==2 && ugcRequestData && ugcRequestData.username ? <ApprovedModal ugcRequestData={ugcRequestData} onHide={()=>{this.setState({statusPopup:0}); CLEAR_UGC_REQUEST_DATA()}} /> : null}
            {ugcPopup && ugcData && ugcData.template && ugcData.template.length ?<UgcModal ugcData={ugcData} hideUgc={this.onHideUgc} />:''}
        </>
    }
}

const mapStateToProps = (state) => {
    return {
        ugcData: state.moderation.ugcData,
        ugcPopup: state.moderation.ugcPopup,
        ugcRequestData: state.moderation.ugcRequestData,
        feeds: state.manageFeeds.feeds
    }
}
const mapDispatchToProps = dispatch => {
    return {
        ugcModal: (state) => dispatch(ugcModal(state)),
        clearUgcData: () => dispatch(CLEAR_UGC_DATA()),
        CLEAR_UGC_REQUEST_DATA: () => dispatch(CLEAR_UGC_REQUEST()),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(View);