import React, { Component } from 'react'

export default class PtTabs extends Component {
    state={feedTextCharError: false}

    handleFeedTextChange = (e) => {
        const value = e.target.value
        if(value[value.length-1] != '@' && value[value.length-1] != "#" && !value.includes("@") && !value.includes("#")) this.setState({feedTextCharError: false}, () =>this.props.onTextChange(value))
        else this.setState({feedTextCharError : true})
    }
    render() {
        const {
            tab,
            textValue,
            onTextChange,
            checkBox,
            onCheckBoxChange,
            error
        } = this.props

        return (
            <>
                <div className="form-group">
                    <div className="input-group">
                        <div className="input-group-prepend">
                            <span className="input-group-text">
                                <i className={`fas fa-${tab == "Handle" ? "at" : "flag"}`}></i>
                            </span>
                        </div>
                        <input data-limit="100" value={textValue?textValue:""} onChange={(e) => this.handleFeedTextChange(e)} placeholder={`${tab == "Handle" ? "Enter user handle" : "https://in.pinterest.com/username/board-name/"}`} className="autosearch mycheck form-control" id="" type="text" />
                    </div>
                    {error && tab == "Handle" ? <span id="feedsfield1error" className="errorMessage required mt-2">This field is required</span> : null}
                    {this.state.feedTextCharError ? <span id="" className="errorMessage required">@ and # not allowed</span> : null}
                    {tab != "Handle" && error ? <span className="help-block errorMessage required mt-2">Enter Board Url</span> : null}
                </div>
                <div className="hideforBSearch">
                    <div className="checkbox-inline">
                        <label htmlFor={`checkbox_${tab}`} className="checkbox checkbox-outline checkbox-primary mr-2">
                            <input type="checkbox" id={`checkbox_${tab}`} checked={checkBox} onChange={(e) => onCheckBoxChange(e.target.checked)} className="checkbox checkbox-outline checkbox-outline-2x checkbox-primary mr-2" />
                            <span></span>
                            Manually approve posts before making them public?
                        </label>
                    </div>
                </div>
            </>
        )
    }
}
