import React, { Component } from 'react'
import TagProduct from '../../modal/tagProduct'
import { connect } from 'react-redux';
import { REMOVE_TAGGED_PRODUCT_ACTION } from '../../../../../../../actions/moderationActions';
import reactImageSize from 'react-image-size';

class ProductTags extends Component {
    constructor(props) {
        super(props);
        this.state = { height: '', error: '', width:'', productTagPopup: false };
      }

    OpenTagPopup = (firstTime = false, popUpClosed = false) => {
        const { productTagPopup } = this.state
        // console.log('first', firstTime, popUpClosed )
        this.setState({ productTagPopup: !productTagPopup })
        if (firstTime && popUpClosed) {
            var evt = new CustomEvent('tagged_product', { detail: true });

            window.dispatchEvent(evt);
        }
    }

    removeTaggProduct= (productId, referenceId) => { this.props.REMOVE_TAGGED_PRODUCT_ACTION(productId, referenceId); };


  componentWillMount() {
    const { item } = this.props
    reactImageSize(item.newMedia)
      .then(({ width, height }) =>{
      this.setState({
        height: (height * 100) / width,
        width: (width * 100) / height
      })
    })
      .catch((errorMessage) =>
        this.setState({
          height: 100,
          width: 100,
          error: errorMessage
        })
      )
  }



    render() {
        const { productTagPopup, width , height } = this.state;
        const { item, listView } = this.props;
        return (<>
            {item.postproduct && item.postproduct.length > 0 ?
                <div className={`m_tagged_pro_action tag-section-${item.id} ${listView ? 'ts_list_tagged_pro_action' : ''}`}>
                    <div className="m_tagged_pro d-flex align-items-center justify-content-left flex-wrap flex-sm-nowrap border-0">
                        <ul className={`d-flex align-items-center list-inline mb-0 justify-content-start post-prodyct-${item.id}`}>
                            {item.postproduct.map((taggedPrd, inx) => {
                                return <li>
                                    <div style={{  backgroundColor :'#f3f6f9' }}>
                                    <img src={taggedPrd?.product?.product_image} onError={e => e.target.src ='https://cloud.taggbox.com/taggshop/app/assets/media/products/no-product.svg'} style={{  width :'98%', padding:'1px' }}/>
                                        <button type="button" className="btn remove_pro" onClick={() => this.removeTaggProduct(taggedPrd.ugc_product,taggedPrd.post_id)}>
                                            <i className="fas fa-times"></i>
                                        </button>
                                    </div>
                                </li>
                            })}
                        </ul>
                        <button type="button" className="btn text-center addnewproduct border-0" onClick={this.OpenTagPopup}>
                            <span className="svg-icon svg-icon-md">
                                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                    <g id="Group_4424" data-name="Group 4424" transform="translate(10922 -4685)">
                                        <rect id="Rectangle_2094" data-name="Rectangle 2094" width="24" height="24" transform="translate(-10922 4685)" fill="none" opacity="0"></rect>
                                        <path id="Path_5219" data-name="Path 5219" d="M-15.382,10.935h-6.172V4.765A1.065,1.065,0,0,0-22.618,3.7a1.065,1.065,0,0,0-1.064,1.064v6.17h-6.174a1.058,1.058,0,0,0-.751.317,1.056,1.056,0,0,0-.305.756,1.068,1.068,0,0,0,1.058,1.055h6.172v6.169a1.059,1.059,0,0,0,.311.754,1.021,1.021,0,0,0,.755.313,1.055,1.055,0,0,0,.749-.309,1.059,1.059,0,0,0,.313-.756V13.063h6.175a1.066,1.066,0,0,0,1.057-1.072A1.069,1.069,0,0,0-15.382,10.935Z" transform="translate(-10887.383 4685)" fill="#3F7AF2"></path>
                                    </g>
                                </svg>
                            </span>
                        </button>
                    </div>
                </div>
                :
                <div className={`tag_products border-0 d-flex align-items-center  ${listView ? 'ts_list_tagg_btn justify-content-start me-5' : 'justify-content-between'}`}>
                    {/* {listView ? '' :<span>No products tagged</span>} */}
                    <button type="button" className={`btn tag_btn btn-block tagproductbutton ${listView ? 'listtagpdbtn' : ''}`} id="attach5834" data-post-id="1574986120" onClick={this.OpenTagPopup}>
                        <span className="svg-icon">
                            <svg xmlns="http://www.w3.org/2000/svg" width="13.016" height="13.016" viewBox="0 0 13.016 13.016">
                                <path data-name="Path 5150" d="M9.187,2.4a1.358,1.358,0,0,0-.96-.4H3.358A1.358,1.358,0,0,0,2,3.358V8.227a1.358,1.358,0,0,0,.4.96l5.431,5.431a1.358,1.358,0,0,0,1.92,0l4.869-4.869a1.358,1.358,0,0,0,0-1.92L9.187,2.4ZM5.394,6.752A1.358,1.358,0,1,1,6.753,5.394,1.358,1.358,0,0,1,5.394,6.752Z" transform="translate(-2 -2)" fill="#6D7175"></path>
                            </svg>
                        </span>
                        <span className="font-weight-bolder">Tag Products</span>
                    </button>
                    
                </div>}
                {productTagPopup ? <TagProduct {...this.props} OpenTagPopup={this.OpenTagPopup} imgHeight={height} imgWidth={width}/> : ''}

            </>
        )
    }
}


const mapStateToProps = state => {
    return { moderation: state.moderation, }
}
const mapDispatchToProps = dispatch => {
    return { REMOVE_TAGGED_PRODUCT_ACTION:(productId, referenceId) => dispatch(REMOVE_TAGGED_PRODUCT_ACTION(productId, referenceId)), }
}

export default connect(mapStateToProps, mapDispatchToProps)(ProductTags)