import React, { Component } from 'react'
import { connect } from "react-redux";
import "./assets/css/moderation.css"
import "./assets/css/skeleton-loader.css"
import "./assets/css/custom.css"
import "./assets/css/ugc-rights.css"
import SubHeader from './components/subheader'
import View from './components/view'
import CTAButton from '../../website/ctaButton';
import AllFeeds from './components/feeds/allFeeds'
import CreateFeed from './components/feeds/createFeed'
import { getModeration, updateCheckAll, selectPostUpdate, activeTabActionFilter, dateRangeFilter, clickToPostStatusALL, clickToPostDeleteALL, searchTagsFilter, limitUpdate, onNextNavigateClick, onPrevNavigateClick, onUpdatePage, editpost, updateCtaPost, clickToRefresh, removeAllFilterBody, searchPostFilter, onUpdateTags, onDeleteTags, removeCheckAll, updateCheckStatusAll, getProduct, GET_CTA_ACTION, GET_ADD_FEED } from '../../../../actions/moderationActions'
import { IMG_LOAD_AFTER_COMPLETE_LAYOUT, fadeOutEffect } from '../../../../utils'
import HeaderPart from './headerPart';
import Managefeeds from './components/managefeeds';
import { GET_FEEDS } from '../../../../actions/manageFeedsAction';
import ErrorFound from '../../../ErrorFound';
import { CLOUDPATH, HOST_URL, HOST_URL_TAGGSHOP } from '../../../../actions/api';
import { toggleAddFeedsSection, toggleCtaSection, toggleManageSection } from '../../../../actions/show_modal_Action';
import { Link } from 'react-router-dom';
import { TOGGLE_ADD_GALLERY } from '../../../../reducer/keys';
import Swal from 'sweetalert2';
import { getUserInfo_afterChanges } from '../../../../actions/loginActions';
import withRouter from '../../../routes/WithRouter';
class Moderation extends Component {
  allImageQueue = [];
    state = {
        listData: [],
        total: 0,
        isCheckAll: false,
        isCheckedPostCount: 0,
        renderedView: 1,
        perPageCount: 10,
        activePage: 1,
        activeTab: 0,
        filters: [],
        ctaPopup: false,
        // showAddFeed: false,
        // manageFeed: false,
        addFeed: false,
    allFirstImage: false,
    openPublishPopup: false
    }

    componentWillMount() {
        const { getModeration, moderation } = this.props;
        if(!moderation.listData.length) getModeration(this.clickToAddFeed);
    }
 
    componentDidUpdate(prevProps, prevState) {
        if (prevProps.moderation.changeModeration_wall !== this.props.moderation.changeModeration_wall && this.props.moderation.changeModeration_wall){
            this.setState({ activeTab: 0}, this.clickToUncheckAll) 
        }
        if(!this.props.moderation.listData.length && this.props.moderation.loding) getModeration();
      }
      
    componentDidMount() {
        const { moderation, manageFeedData } = this.props;
        if(window.location.href.includes("ob_success")) this.onBoardingSuccess()
      //  if(moderation.taggProductChange) this.props.getProduct()
      //  if(!moderation.addFeedNetworks) this.props.GET_ADD_FEED()
      //  if(!manageFeedData.totalFeed) this.props.GET_FEEDS()
        document.cookie = "callBackUrl=React";
        localStorage.removeItem('createFeedObj')
        // document.cookie = "callBackUrl=Php";
        //   localStorage.removeItem('createFeedObj')
        // if (window.isManageFeed) { this.setState({ manageFeed: true }) }
        // if (window.isAddFeed) { this.setState({ addFeed: true }) }
        // if (window.isCtaOpen) { this.setState({ ctaPopup: true }) }
        // if (window.isCtaOpen) { this.setState({ ctaPopup: true }) }
        if (window.isPublish) { this.setState({ openPublishPopup: true }) }
        if(!moderation.listData.length && !moderation.loding) getModeration();
        
    }

    onBoardingSuccess = () => {
        setTimeout(() => {
            Swal.fire({
                // title: "Twitter",
                // text:'Connect to Twitter to pull the content for this feed.',
                imageUrl: `${CLOUDPATH}taggshop/app/assets/media/onboarding-icons/success-icon.svg`,
                title: `<strong>You are almost there!</strong>`,
                //   icon: 'info',
                html:
                    `Tag products to curate a Shoppable Gallery & publish it on your pages.`,
                showCloseButton: true,
                showCancelButton: false,
                focusConfirm: true,
                confirmButtonText:
                    `Got it!`,
                confirmButtonAriaLabel: `Got it!`,
                cancelButtonText:
                    '<i class="fa fa-thumbs-down"></i>',
                cancelButtonAriaLabel: 'Thumbs down'
            }).then(result => {
                if(result.isConfirmed) {this.props.getUserInfo_afterChanges(); this.props.navigate(`/content/moderation/${localStorage.getItem('wallId')}`)}
            })
        }, 1000);
    } 

    onClickToViewFilter = (renderedView) => event => this.setState({ renderedView })

    clickToCheckAll = event => {
        const { isCheckAll } = this.state;
        this.setState({ isCheckAll: !isCheckAll }, () => {
            this.onActionUpdateOpcity()
            this.props.updateCheckAll(this.state.isCheckAll)
        })
    }
    clickToCheckStatusAll = (status) => event => {
        this.onActionUpdateOpcity()
        this.props.updateCheckStatusAll(status)
    }
    clickToUncheckAll = event => {
        this.setState({ isCheckAll: false }, () => {
            this.onActionUpdateOpcity()
            this.props.removeCheckAll(false)
        })
    }

    clickPostChecked = (index) => event => {

        const { isCheckAll, isCheckedPostCount } = this.state;
        this.props.selectPostUpdate(index, this.onActionUpdateOpcity())
    }

    onActionUpdateOpcity = () => {
        const { isCheckAll, isCheckedPostCount } = this.state;
        if (isCheckAll || (isCheckedPostCount > 0)) {
            if (document.getElementsByClassName("modSelectNo") && document.getElementsByClassName("modSelectNo")[0])
                fadeOutEffect(document.getElementsByClassName("modSelectNo")[0])
            if (document.getElementsByClassName("filter-bar") && document.getElementsByClassName("filter-bar")[0]) fadeOutEffect(document.getElementsByClassName("filter-bar")[0])
        }
        else {
            // if (document.getElementsByClassName("modSelectNo") && document.getElementsByClassName("modSelectNo")[0]) document.getElementsByClassName("modSelectNo")[0].style.display = "none";
            // if (document.getElementsByClassName("filter-bar") && document.getElementsByClassName("filter-bar")[0]) document.getElementsByClassName("filter-bar")[0].style.display = "none";
        }

    }

    clickToTab = (activeTab) => event => {
        const { activeTabActionFilter } = this.props;
        this.setState({ activeTab })
        activeTabActionFilter(activeTab)
    }
    onDateRangeFilter = (startDate, endDate) => {
        const { dateRangeFilter } = this.props;
        dateRangeFilter(startDate, endDate)
    }
    onClickToFilterUpdateStatus = (status) => event => {
        const { clickToPostStatusALL } = this.props;
        clickToPostStatusALL(status)

    }
    onClickToFilterDeleteAll = event => {
        const { clickToPostDeleteALL } = this.props;
        clickToPostDeleteALL()
    }

    onClickNextPage = event => {
        this.props.onNextNavigateClick()
    }
    onClickPrevPage = event => {
        this.props.onPrevNavigateClick()
    }
    onChangePage = (page) => event => {
        this.props.onUpdatePage(page)
    }
    onAppliedFilter = (key, value) => {

        let { filters } = this.state;

        if (filters.length > 0) {
            let findKey = filters.filter((checkF) => checkF.key == key);
            if (findKey && findKey.length > 0) {
                filters.map((item) => {
                    if (item.key == key) {
                        item.value = value;
                    }

                })
            }
            else {
                filters.push({ key, value })
            }
        }
        else {
            filters.push({ key, value })
        }
        this.setState({ filters }, () => this.props.clickToRefresh())
    }

    onClickReset = (item) => {
        let tempFilter = this.state.filters.filter(i => i.key === item.key)
        tempFilter = tempFilter.filter((it) => {
            if (
                it.key === "postType" ||
                it.key === "socialFeed" ||
                it.key === "other"
            )
                return it.value.filter((j) => !item.value.includes(j));
        });
        this.setState({ filters: tempFilter }, () => this.props.clickToRefresh())

    }

    onClickAllReset = event => {
        this.setState({ filters: [] }, () => this.props.getModeration())
    }

    clickToOpenPublish = () => { this.setState({ openPublishPopup: true, ctaPopup: false, manageFeed: false, addFeed: false }) }
    clickToOpenCta = () => { this.setState({ openPublishPopup: false, ctaPopup: true, manageFeed: false, addFeed: false }) }
    clickToManageFeed = () => { this.setState({ openPublishPopup: false, ctaPopup: false, manageFeed: true, addFeed: false }) }
    clickToAddFeed = () => { this.setState({ openPublishPopup: false, ctaPopup: false, manageFeed: false, addFeed: true });  }


    handleAllImageError = (e) => {
        try {
          const src = e.target.getAttribute("data-id");
          if (!this.allImageQueue.includes({ src, e })) this.allImageQueue.push({ src, e });
            if (!this.state.allFirstImage) this.processAllImageQueue();
        } catch (error) {
            console.log('error', error)
        }
    
      };
    
      processAllImageQueue = async () => {
        if (this.allImageQueue.length) {
          this.setState({ allFirstImage: true })
          const { src, e } = this.allImageQueue[0];
          if (src) {
              IMG_LOAD_AFTER_COMPLETE_LAYOUT(e, this.nextProcessAllImageQueue)
          }
        }
      };
      nextProcessAllImageQueue = () => {
        try {
          this.allImageQueue.shift();
          this.processAllImageQueue()
        } catch (error) {
        console.log('error', error)
        }
      }


    clickToCloseCta = () => { this.setState({ ctaPopup: false }); window.isCtaOpen = false; }

    clickToClosePublish = () => {
        this.setState({ openPublishPopup: false }); window.isPublish = false;
        window.location.replace(`${HOST_URL_TAGGSHOP}/website/publish/${localStorage.getItem('website_id')}`)
    }

    render() {
        const { isCheckAll, renderedView, total, perPageCount, activePage, activeTab, filters, ctaPopup,  addFeed } = this.state;
        const { moderation, searchTagsFilter, limitUpdate, loader, editpost, clickToRefresh, updateCtaPost,showAddFeed,showCta, showManageFeed, removeAllFilterBody, searchPostFilter, onUpdateTags, onDeleteTags, ctaData, allFont, addFeedData, feeds, allWalls, addGalleryNow } = this.props;

        return <>
            <HeaderPart allFont={allFont} />
     
            {(allWalls && allWalls.length > 0) ? moderation ? 
                <div className="moderateFilterFixed">

                    <SubHeader onUpdateTags={onUpdateTags} onDeleteTags={onDeleteTags} clickToCheckAll={this.clickToCheckStatusAll} clickToUncheckAll={this.clickToUncheckAll} isCheckAll={isCheckAll} isCheckedPostCount={moderation.isCheckedPostCount} searchPostFilter={searchPostFilter} renderedView={renderedView} onClickToViewFilter={this.onClickToViewFilter} total={moderation.total} perPageCount={perPageCount} activePage={activePage} moderation={moderation} clickToTab={this.clickToTab} onDateRangeFilter={this.onDateRangeFilter} onClickToFilterUpdateStatus={this.onClickToFilterUpdateStatus} onClickToFilterDeleteAll={this.onClickToFilterDeleteAll} searchTagsFilter={searchTagsFilter} limitUpdate={limitUpdate} onNextNavigateClick={this.onClickNextPage} onPrevNavigateClick={this.onClickPrevPage} onChangePage={this.onChangePage} onAppliedFilter={this.onAppliedFilter} clickToRefresh={clickToRefresh} activeTab={activeTab} />
                    <View {...moderation} clickPostChecked={this.clickPostChecked} clickToUncheckAll={this.clickToUncheckAll} removeAllFilterBody={removeAllFilterBody} renderedView={renderedView} loader={loader} activeTab={activeTab} searchTagsFilter={searchTagsFilter} onAppliedFilter={this.onAppliedFilter} handleAllImageError={this.handleAllImageError} filters={filters} onClickAllReset={this.onClickAllReset} onClickReset={this.onClickReset} editpost={editpost} updateCtaPost={updateCtaPost} />
                    
                    { showAddFeed && addFeedData ? <ErrorFound><AllFeeds addFeedData={addFeedData} hideAddfeed={() => this.props.toggleAddFeedsSection(false)} /></ErrorFound> : null }

                    {this.props.showManageFeed && feeds && !showAddFeed ? <Managefeeds open={showManageFeed} onClose={() => this.props.toggleManageSection(false)} feeds={feeds} showAddFeed={() => this.props.toggleAddFeedsSection(true)} hideAddfeed={() => this.props.toggleAddFeedsSection(false)} /> : null}
                </div> : null
                :<div className="table-scrollable globalTblList "  style={{height: 'Calc(100vh - 45px)'}}>
                <div className="d-flex align-items-center justify-content-center flex-column h-100 error_Message">
                    <svg xmlns="http://www.w3.org/2000/svg" width="110" height="108" viewBox="0 0 110 108" fill="none"> <circle cx="76.4844" cy="73.9844" r="33.5156" fill="#C4A2FC" /> <path d="M74.0449 55.2075C74.0449 53.8602 75.1371 52.7679 76.4844 52.7679C77.8317 52.7679 78.924 53.8602 78.924 55.2075V93.981C78.924 95.3283 77.8317 96.4205 76.4844 96.4205C75.1371 96.4205 74.0449 95.3283 74.0449 93.981V55.2075Z" fill="white" /> <path d="M95.871 72.1548C97.2183 72.1548 98.3105 73.247 98.3105 74.5943C98.3105 75.9416 97.2183 77.0338 95.871 77.0338L57.0975 77.0338C55.7502 77.0338 54.658 75.9416 54.658 74.5943C54.658 73.247 55.7502 72.1548 57.0975 72.1548L95.871 72.1548Z" fill="white" /> <path d="M55 75L41.2888 65.2926C41.0123 65.0969 40.6389 65.1108 40.3778 65.3265L31.4495 72.702C31.1572 72.9435 30.7307 72.9288 30.4557 72.6679L14.5996 57.6249C14.3522 57.3902 13.9776 57.352 13.6879 57.5318L1.71875 64.9609" stroke="#14166C" strokeWidth="3" /> <rect x="3" y="44" width="30" height="3" fill="#14166C" /> <rect x="31.5781" y="2.4375" width="53.7188" height="54.5781" rx="3.75" stroke="#14166C" strokeWidth="3" /> <path d="M48.125 40.9707V18.4824C48.125 16.7735 49.9552 15.6884 51.4546 16.5084L72.0152 27.7525C73.5758 28.6059 73.5758 30.8472 72.0152 31.7006L51.4546 42.9448C49.9552 43.7647 48.125 42.6796 48.125 40.9707Z" stroke="#14166C" strokeWidth="3" /> <path fillRule="evenodd" clipRule="evenodd" d="M30 30H6.04545C2.70664 30 0 32.713 0 36.0597V81.9403C0 85.287 2.70664 88 6.04546 88H50.9545C54.2934 88 57 85.287 57 81.9403V59H54V82.5C54 83.9343 52.84 85.097 51.4091 85.097H5.59091C4.15999 85.097 3 83.9343 3 82.5V35.597C3 34.1627 4.15999 33 5.59091 33H30V30Z" fill="#14166C" /> </svg>
                        <h5>Create a Gallery, to add feed! </h5>
                        <p>Oops! Looks like you have deleted your gallery, create one by simply clicking on the button below.</p>
                        <div className="buttonUpgred">
                        <button className="btn btn-primary" onClick={() => { this.props.TOGGLE_ADD_GALLERY(!addGalleryNow)}}>Create Gallery</button>
                    </div>
                </div>
             </div> }</>
    }
}
//display:'none'
// position:'abslute', zIndex:'999', left:'0px'

const mapStateToProps = (state) => {
    return {
        moderation: state.moderation,
        loader: state.loader,
        ctaData: state.ctaData.data,
        allFont: state.allFont,
        addFeedData: state.moderation.addFeedNetworks,
        feeds: state.manageFeeds.feeds,
        showAddFeed: state.modalReducer.showAddFeed,
        showManageFeed: state.modalReducer.showManageFeed,
        showCta:state.modalReducer?.showCta,
        allWalls: state.dashboardData?.dashboarddata?.allWalls,
        addGalleryNow: state.dashboardData?.addGalleryNow,
        manageFeedData: state.manageFeeds
    }
}
const mapDispatchToProps = dispatch => {
    return {
        getModeration: (callback) => dispatch(getModeration(callback)),
        activeTabActionFilter: (status) => dispatch(activeTabActionFilter(status)),
        updateCheckAll: (status) => dispatch(updateCheckAll(status)),
        selectPostUpdate: (index, callBack) => dispatch(selectPostUpdate(index, callBack)),
        dateRangeFilter: (startDate, endDate) => dispatch(dateRangeFilter(startDate, endDate)),
        clickToPostStatusALL: (status) => dispatch(clickToPostStatusALL(status)),
        clickToPostDeleteALL: () => dispatch(clickToPostDeleteALL()),
        searchTagsFilter: (searchTags) => dispatch(searchTagsFilter(searchTags)),
        searchPostFilter: (searchPost) => dispatch(searchPostFilter(searchPost)),
        limitUpdate: (limit) => dispatch(limitUpdate(limit)),
        onNextNavigateClick: () => dispatch(onNextNavigateClick()),
        onPrevNavigateClick: () => dispatch(onPrevNavigateClick()),
        onUpdatePage: (page) => dispatch(onUpdatePage(page)),
        editpost: (index, data, callback) => dispatch(editpost(index, data, callback)),
        updateCtaPost: (index, data, callback) => dispatch(updateCtaPost(index, data, callback)),
        clickToRefresh: () => dispatch(clickToRefresh()),
        removeAllFilterBody: () => dispatch(removeAllFilterBody()),
        onUpdateTags: (data, callBack) => dispatch(onUpdateTags(data, callBack)),
        onDeleteTags: (data, callBack) => dispatch(onDeleteTags(data, callBack)),
        removeCheckAll: (data) => dispatch(removeCheckAll(data)),
        updateCheckStatusAll: (data) => dispatch(updateCheckStatusAll(data)),
        getProduct: () => dispatch(getProduct()), 
        GET_ADD_FEED: () => dispatch(GET_ADD_FEED()),
        GET_FEEDS: () => dispatch(GET_FEEDS()),
        toggleAddFeedsSection: (state) => dispatch(toggleAddFeedsSection(state)),
        toggleManageSection: (state) => dispatch(toggleManageSection(state)),
        toggleCtaSection: (state) => dispatch(toggleCtaSection(state)),
        TOGGLE_ADD_GALLERY: (status) => dispatch({type:TOGGLE_ADD_GALLERY , payload:status}),
        getUserInfo_afterChanges: () => dispatch(getUserInfo_afterChanges()),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(React.memo(withRouter(Moderation)));

