import React, { Component } from 'react'
import NetworkData from '../../../../../../containers/networkData'
class ProfileIMG extends Component {
    constructor(props) {
        super(props)
        this.state = {
            picture: props.item.picture.replace(/\s/g, ''),
            network: this.findNetwork(props)
        }
    }
    findNetwork = (props) => {
        const { item } = props
        if (NetworkData && NetworkData.length > 0) {
            const data = NetworkData.filter((nItem) => String(nItem.name).toLowerCase() == String(item.netname).toLowerCase())
            if (data && data.length > 0) return data[0];
            else return "#000";
        }
        else return "#000";
    }
    render() {
        const { item, cssClass } = this.props;
        const { picture, network } = this.state;
        return <>
            <div className={cssClass} data-original={item.picture} data-name={item.name}
                style={{ backgroundImage: `url(${picture.replace(/\s/g, '')})` }}>
            </div>
            <img src={item.picture.replace(/\s/g, '')} onError={event => this.setState({ picture: `https://ui-avatars.com/api/?name=${item.username.replace(/\s/g, '')}&background=${String(network.color).replace("#", '')}&color=fff&length=1` })} style={{ display: 'none' }} />
        </>

    }
}

export default ProfileIMG;