import React, { Component } from 'react'
import { Dropdown, Tab, Tabs } from "react-bootstrap";
import TiktokTab from './tiktokTab';
import Swal from 'sweetalert2'
import { LoginSocialTiktok } from 'reactjs-social-login'
import { CLOUDPATH, REDIRECT_URI } from '../../../../../../../actions/api';
import { TIKTOK_CLIENT_KEY } from '../../../../../../../actions/constants';
import { GET_FLITER_ID } from '../../../../../../../utils';
import { CREATE_FEED } from '../../../../../../../actions/moderationActions';
import { getTiktokCode } from '../../../../../../../utils/auth';
import { useNavigate } from 'react-router-dom';
import { connect } from 'react-redux';


const Tiktokfeed = (props) => {
    const navigation = useNavigate();
    return <TikFeed {...props} navigation={navigation} />;
}


class TikFeed extends Component {
    state = {
        postUrl: "",
        manuallyApprove: 0,
        filterType: 'Hashtag',
        filterId: 72,
        error: false,
        connetedAccount: null,
        authenticate:false,
        loading: false,
    }


    componentDidMount() {
        this.setState({ authRequired: this.props.feedInfo[0].authRequire })
        this.props.onFilterNameChange(this.state.filterType)

    }


    onPostUrlChange = (value) => this.setState({ postUrl: value , error:false})
    onAccountChange = (value) => this.setState({ account: value })
    onManuallyApproveChange = (value) => this.setState({ manuallyApprove: value })
    onConnectedAccountChange = (value) => this.setState({ connetedAccount: value })
    onAuthenticateChange = (value) => this.setState({ authenticate: value })

    clearField = (e) => this.setState({ postUrl: "", manuallyApprove: 0 }, () => this.props.onFilterNameChange(e))


    onSubmit = () => {
        // const { authRequired, connetedAccount } = this.state
        // authRequired ? (connetedAccount == null ? this.onAddAccount() : this.onAddFeed()) : this.onAddFeed()
        this.onAddFeed()
    }


    onAddAccount = () => {
        const { postUrl, manuallyApprove, filterId, authRequired } = this.state
        localStorage.setItem("addAcc", false)

        if (filterId == 70) {
            this.onAddFeed()
        } else {
            this.setState({ loading: true })
            localStorage.setItem("authRequired", authRequired)

            var objj = {
                wallId: localStorage.getItem('wallId'),
                networkId: 28,
                filter: filterId,
                moderation: Number(manuallyApprove),
                feedText: '',
                accountId: ""
            }

            localStorage.setItem("createFeedObj", JSON.stringify(objj))
            localStorage.setItem("networkId", 28)

            Swal.fire({
                imageUrl: 'https://cloud.taggbox.com/taggshop/app/assets/media/square-icons/tiktok.svg',
                title: '<strong>Tiktok</strong>',
                html: 'Connect to tiktok to pull the content for this feed.',
                showCloseButton: true,
                showCancelButton: false,
                focusConfirm: true,
                confirmButtonText: 'Continue with Tiktok',
                confirmButtonAriaLabel: 'Continue with Tiktok',
                cancelButtonText: '<i class="fa fa-thumbs-down"></i>',
                cancelButtonAriaLabel: 'Thumbs down'
            }).then((result) => {
                if (result.value) {
                    getTiktokCode()
                }
                else if (result.dismiss === "cancel") {
                    this.setState({ loading: false })
                 }
            });
        }
    }


    onAddFeed = () => {
        const { manuallyApprove, postUrl, filterId, authRequired, userId, connetedAccount } = this.state
        const { navigation, hideAddfeed } = this.props
        this.setState({ loading: true })


        var obj
        if (filterId == 70) {
            obj = { wallId: localStorage.getItem('wallId'), networkId: 28, filter: filterId, moderation: Number(manuallyApprove), tiktok_post_url: postUrl }
            if (!postUrl || postUrl == "") this.setState({ error: true })
            else {
                this.setState({ error: false })
                CREATE_FEED(obj, navigation, true, hideAddfeed)
            }
        }
        else {
            obj = { wallId: localStorage.getItem('wallId'), networkId: 28, filter: filterId, moderation: Number(manuallyApprove), feedText: postUrl, accountId: connetedAccount }
            CREATE_FEED(obj, navigation, true, hideAddfeed)
        }

    }



    render() {
        const { connetedAccount, postUrl, manuallyApprove, error, filterType , authenticate, loading} = this.state
        const { feedInfo, connectedAcc } = this.props
        return (
            <div className="row">
                <div className="col-md-12">
                    <div className="form-body create_feed_wt">
                        <Tabs defaultActiveKey="Hashtag" value={filterType} id="TiktokFeedTab" className="nav-tabs-line nav-tabs-bold w-100 tabDrop mb-5" onSelect={e => this.setState({ filterType: e }, this.setState({ filterId: GET_FLITER_ID(e, feedInfo) }, () => this.clearField(e)))}>
                            {feedInfo && feedInfo.length && feedInfo[0].networkTab && feedInfo[0].networkTab.length ? feedInfo[0].networkTab.map(filter =>
                                <Tab eventKey={filter.feedtype} key={filter.filter} className="nav-link className= p-0" title={<span>{filter.feedName}</span>} >
                                    <TiktokTab 
                                    tab={filter.feedtype}   
                                    onValueChange={this.onAccountChange} 
                                    error={error} 
                                    checkBox={manuallyApprove} 
                                    onCheckBoxChange={this.onManuallyApproveChange} 
                                    onAddAccount={this.onAddAccount} 
                                    connectedAcc={connectedAcc}
                                    onConnectedAccountChange={this.onConnectedAccountChange}
                                    onAuthenticateChange={this.onAuthenticateChange}
                                    connetedAccount={connetedAccount}
                                    authenticate={authenticate}
                                    onPostUrlChange={this.onPostUrlChange}
                                    postUrl={postUrl}
                                    />
                                </Tab>
                            ) : null}
                        </Tabs>
                    </div>
                    <div className="form-actions">
                        <div className="d-flex justify-content-between align-items-center flex-wrap border-top pt-lg-8 mt-lg-10 pt-5 mt-6">
                            <div className="need_help__"></div>
                            <div className="hideforBSearch">
                                <button type="button" className="btn btn-secondary" onClick={this.props.onBack}>Back</button>
                                <span className="create_feed_hideshow">
                                    <button type="button" className={`commanBtn submit_create_feed btn btn-primary ml-4 ${loading ? 'spinner spinner-white disabled spinner-right' : ""}`} id="create_feed" onClick={this.onSubmit}>
                                        Create Feed
                                    </button>
                                </span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        connectedAcc: state.addFeedReducer.connectedAcc
    }
}

const mapDispatchToProps = dispatch => {
    return {
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Tiktokfeed)