import React, { Component } from 'react'

export default class InputGroup extends Component {
    render() {
        const { title, setValue, error, value, icon} = this.props
        return (
            <div className="form-group">
                <label htmlFor="FeedFeedsfieldtitle13">{title}</label>
                <div className="input-group">
                    <div className="input-group-prepend">
                        <span className="input-group-text">
                            <i className={`fas fa-${icon}`}></i>
                        </span>
                    </div>
                    <input className="form-control" value={value} onChange={(e) => setValue(e.target.value)} type="text" id="" />
                </div>
                {error ? <span id="" className="errorMessage required">{title == "Feed Url" ? "This field must contain a url." : "This field is required."}</span> : null}
            </div>
        )
    }
}
