import React, { Component } from 'react'
import HTMLReactParser from 'html-react-parser';
import moment from 'moment'
import { CLOUDPATH, CLOUDPATH_TAGGSHOP } from '../../../../../../../../actions/api'; 
import { DOWNLOAD_CERTIFICATE } from '../../../../../../../../actions/ugcAction';
import {saveAs} from "file-saver";
class UgcModalApproved extends Component {
    onDownloadMedia = (data) => {
        if (data.type != 5 && data.type != 3) {
            saveAs(data.postFile, `${data.id}`);
        }
        else{
            saveAs(data.mediaUrl, `${data.id}`);

        }

    }
    render() {
        const { ugcRequestData, ugcData, onHide } = this.props;
        return ugcRequestData ? <>
            <div className="ugc-modal">
                <div className="modal-dialog m-w-100 m-0" role="document">
                    <div className="modal-content rounded-0">
                        <div className="modal-header py-3">
                            <div className="d-flex align-items-center flex-nowrap">
                                <span className="svg-icon-col mr-3">
                                    <svg xmlns="http://www.w3.org/2000/svg" width="34" height="34" viewBox="0 0 24 24">
                                        <g id="Group_5456" data-name="Group 5456" transform="translate(9565 7108)">
                                            <rect id="Rectangle_2321" data-name="Rectangle 2321" width="34" height="34" transform="translate(-9565 -7108)" fill="none"></rect>
                                            <g id="Group_5455" data-name="Group 5455" transform="translate(-9562.424 -7106.873)">
                                                <g id="Group_4836" data-name="Group 4836">
                                                    <path
                                                        id="Path_5298"
                                                        data-name="Path 5298"
                                                        d="M-125.457-53.668l-8.2-3.4a1.089,1.089,0,0,0-.848,0l-8.2,3.4a1.1,1.1,0,0,0-.682,1.02v4.139A14.225,14.225,0,0,0-134.5-35.284a1.112,1.112,0,0,0,.418.082,1.1,1.1,0,0,0,.417-.082,14.225,14.225,0,0,0,8.889-13.226v-4.139A1.1,1.1,0,0,0-125.457-53.668Z"
                                                        transform="translate(143.384 57.159)"
                                                        fill="#6ac259"
                                                    ></path>
                                                    <path
                                                        id="Path_5297"
                                                        data-name="Path 5297"
                                                        d="M-136.916-48.121a1.1,1.1,0,0,0-1.414,0,1.109,1.109,0,0,0-.125,1.573l2.339,2.333a1.1,1.1,0,0,0,.778.319,1.1,1.1,0,0,0,.78-.32l4.171-4.172a1.139,1.139,0,0,0,.139-.139,1.106,1.106,0,0,0-.14-1.556,1.1,1.1,0,0,0-1.539.123l-3.408,3.407Z"
                                                        transform="translate(143.715 57.644)"
                                                        fill="#fff"
                                                    ></path>
                                                </g>
                                            </g>
                                        </g>
                                    </svg>
                                </span>
                                <h5 className="modal-title">Access Image</h5>
                            </div>
                            <button type="button" className="btn-close ugc_close" data-bs-dismiss="modal" aria-label="Close" onClick={onHide} ></button>
                        </div>
                        <div className="modal-body p-0">
                            <div className="container-fluid">
                                <div className="row">
                                    <div className="col ugc_col_left mb20 ugc_card_preview ugc_modal_pending_first_box border-right-lg">
                                        <div className="my-5 ugc_card_wrap">
                                            <div className="alert alert-custom alert-outline-1x alert-outline-approved fade show mb-6 text-center p-4 mx-5" role="alert">
                                                <div className="alert-text"><h5 className="m-0 text-center font-weight-bold">Congratulation! Your UGC Request has been approved</h5></div>
                                            </div>
                                            <div className="card_slider">
                                                <div className="m_card_style">
                                                    <div className="m_card_img">
                                                        <div className="post-content-image">
                                                            <div className="m_media_holder">
                                                                <img className="img-fluid ex-coverimg" src={ugcRequestData.postFile} />
                                                                {(ugcRequestData.type == 3 || ugcRequestData.type == 5) ? <div className="play_icon_wrap">
                                                                    <img className="pay_icon" src={`${CLOUDPATH_TAGGSHOP}/app/assets/media/ugc/play.png`} />
                                                                </div> : null}
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div className="m_card_cont">
                                                        <div className="d-flex align-items-center justify-content-between mb-4">
                                                            <div className="d-flex align-items-center m_grid_author">
                                                                <div className="flex-shrink-0 mr-4 mt-lg-0 mt-3">
                                                                    <div className="symbol symbol-circle symbol-lg-75 m_profile_img" style={{
                                                                        backgroundImage: `url(${ugcRequestData.picture ? ugcRequestData.picture : ""})`
                                                                    }}></div>
                                                                </div>
                                                                <div className="d-flex flex-column post-link m_user_content">
                                                                    <a href={ugcRequestData.link ? parseInt(ugcRequestData.link) ? 'https://twitter.com/' + ugcRequestData.username + '/status/' + ugcRequestData.link : ugcRequestData.link : 'javascript:;'} target="_blank" className="m_post_title ng-binding">{ugcRequestData.name}</a>
                                                                    <div className="d-flex align-item-center m_grid_userinfo">
                                                                        <span className="m_grid_uername text-nowrap">@<span>{ugcRequestData.username}</span></span>
                                                                        <span className="sepratedot"></span>
                                                                        <span className="timePost text-nowrap">{moment(ugcRequestData.modified, "YYYYMMDD").fromNow()}</span>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                            <div className="d-flex flex-column m_social_icon postNetwork" data-network="Instagram Business">
                                                                <i className="fab fa-instagram"></i>
                                                            </div>
                                                        </div>
                                                        <div className="postedCon setFont font5">
                                                            <p className="m-0" style={{ lineHeight: '22px', wordBreak: 'break-all' }}></p>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col ugc_col_right d-flex flex-column p-0">
                                        <div className="modal_wrapper">
                                            <div className="modal-body ugc_approved">
                                                <div className="ugc-information">
                                                    <h2 className="mb-5">Information</h2>
                                                    <div className="form-group">
                                                        <div className="d-flex justify-content-between flex-wrap align-items-center border-bottom bg-light px-5 py-4">
                                                            <span className="text-muted mr-2">User handle</span><span className="text-dark m_grid_uername">{`@${ugcRequestData.username}`}</span>
                                                        </div>
                                                        <div className="d-flex justify-content-between flex-wrap align-items-center border-bottom bg-light px-5 py-4">
                                                            <span className="text-muted mr-2">User name</span><span className="text-dark m_grid_name">{ugcRequestData.name}</span>
                                                        </div>
                                                        <div className="d-flex justify-content-between flex-wrap align-items-center border-bottom bg-light px-5 py-3 ugc-approved-non-insta" style={ugcRequestData.networkId == 18 ? { display: 'none' } : {}}>
                                                            <span className="text-muted mr-2">Device</span>
                                                            <span className="text-dark ugc-deviceupdate">
                                                                {ugcRequestData.userAgent ?
                                                                    HTMLReactParser(ugcRequestData.userAgent)
                                                                    : null
                                                                }
                                                            </span>
                                                        </div>
                                                        <div className="d-flex justify-content-between flex-wrap align-items-center border-bottom bg-light px-5 py-3 ugc-approved-non-insta" style={ugcRequestData.networkId == 18 ? { display: 'none' } : {}}>
                                                            <span className="text-muted mr-2">IP Address</span><span className="text-dark ugc-userIP"> {
                                                                ugcRequestData.userIP
                                                            }</span>
                                                        </div>
                                                        <div className="d-flex justify-content-between flex-wrap align-items-center border-bottom bg-light px-5 py-3 ugc-approved-non-insta" style={ugcRequestData.networkId == 18 ? { display: 'none' } : {}}>
                                                            <span className="text-muted mr-2">Location</span><span className="text-dark ugc-userLocation">
                                                                {
                                                                    ugcRequestData.userLocation
                                                                }
                                                            </span>
                                                        </div>
                                                        <div className="d-flex justify-content-between flex-wrap align-items-center border-bottom bg-light px-5 py-4">
                                                            <span className="text-muted mr-2">Post Approval Date</span><span className="text-dark ugc-actionTime">{moment(ugcRequestData.actionTime).utcOffset(0).format('YYYY-MM-DD  hh:mm:ss a')}</span>
                                                        </div>
                                                        <div className="d-flex justify-content-between flex-wrap align-items-center bg-light px-5 py-3 ugc-approved-non-insta" style={ugcRequestData.networkId == 18 ? { display: 'none' } : {}}>
                                                            <span className="text-muted mr-2">TimeZone</span>
                                                            <span className="text-dark ugc-timezone">{ugcRequestData.timezone ? HTMLReactParser(ugcRequestData.timezone) : null}</span>
                                                        </div>
                                                        <div className="d-flex justify-content-between flex-wrap align-items-center border-bottom bg-light px-5 py-4">
                                                            <span className="text-muted mr-2">Terms &amp; Condition</span>
                                                            <span className="text-dark ugc-tncdownload">
                                                                <a href={(ugcRequestData.custom_terms) ? `${CLOUDPATH}test/${ugcRequestData.custom_terms}` : `${CLOUDPATH}test/${ugcRequestData.custom_term_type}`} target="_blank"><span className="ugc-terms overflow-hidden text-ellipsis text-nowrap">{ugcRequestData.custom_term_type}</span>
                                                                    <span><img src="https://app.taggbox.com/widget/img/ugc-download.svg" alt="download" className="img-fluid" /></span></a>
                                                            </span>
                                                        </div>
                                                        <div className="d-flex justify-content-between flex-wrap align-items-center border-top bg-light px-5 py-3 ugc-approved-non-insta" style={ugcRequestData.networkId == 18 ? { display: 'none' } : {}}>
                                                            <span className="text-muted mr-2">Approval Certificate</span>
                                                            <span className="text-dark ugc-download">
                                                                Rights Management <a onClick={() => DOWNLOAD_CERTIFICATE(ugcRequestData.ugcid)}>Download
                                                                    <span><img src="https://cloud.taggbox.com/taggshop/app/assets/media/svg/ugc-download.svg" alt="download" className="img-fluid" /></span>
                                                                </a>
                                                            </span>
                                                        </div>
                                                    </div>
                                                </div>
                                                {ugcRequestData.comment ? <div className="ugc-user-comment mt-7">
                                                    <h2 className="mb-5">User Comment</h2>
                                                    <div className="user-comment-box bg-light p-5">
                                                        <p>{ugcRequestData.comment}</p>
                                                    </div>
                                                </div> : null}
                                            </div>
                                            <div className="ugcAuthBtn ugc-peingdone modal-footer text-right">
                                                <button type="button" className="btn btn-secondary mr-3 btn-min-w" onClick={onHide} >Close</button>
                                                <a className="btn btn-primary download_ugc_link btn-min-w" onClick={() => this.onDownloadMedia(ugcRequestData)} target="_blank"> Download </a>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </> : null

    }
}

export default UgcModalApproved