import { Modal } from "react-bootstrap";
import { CLOUDPATH_TAGGSHOP } from "../../../../../../../actions/api";

const PermissionError = ({show, onHide, fromSnapup}) => {
  return (
    <>
      <Modal className="permission_error_popup" show={show} onHide={onHide}>
        <Modal.Header>
          <Modal.Title>Permission Error</Modal.Title>
            <button type="button" className="close" aria-hidden="true" onClick={onHide}>
              <i className="ki ki-close-xmark"></i>
            </button>
        </Modal.Header>
        <Modal.Body className="text-center">
          <img src={`${CLOUDPATH_TAGGSHOP}/app/assets/media/error/ugc-grey.svg`} height="" width="" alt="ugc icon"/>
            <div className="permissionMsg mt-4">
              <span className="ErrorMsg">
                {
                  fromSnapup ? 'You cannot send request on SnapUp post!!' : 'You cannot send request on approved/pending post!!'
                }</span>
              <p className="msgSec"></p>
            </div>
        </Modal.Body>
        <Modal.Footer>
            <div className="buttonUpgrade">
              <a href="javascript:;" className="commanBtn btn btn-primary" onClick="javascript:$('#PermissionError').modal('hide');$('#morePopupId').hide();showpricing(2,'Commerce','commerce');" style={{display: "none"}}>
                Upgrade Now
              </a>
            </div>
        </Modal.Footer>
      </Modal>
    </>
  );
};

export default PermissionError;
