import React, { Component } from 'react'
import {Helmet} from "react-helmet";
import { CLOUDPATH_TAGGSHOP } from '../../../../actions/api';

const TAGGSHOP_FONT_URL = `${CLOUDPATH_TAGGSHOP}/assets/taggshop-fonts`
export default class HeaderPart extends Component {

    render() {

        const { allFont } = this.props;
        return <Helmet>
            {allFont.allFonts ? allFont.allFonts.map((item, i)=>{
                return <link key={i} href={`${TAGGSHOP_FONT_URL}/${(item.family).replaceAll(' ','+')}.css`} rel="stylesheet" data-type="1" />
            }):'' }
        </Helmet>
    }
}



