import React, { Component } from 'react'
import { autoCompleteSearch } from '../../../../../../../actions/moderationActions'

class PostTagSearch extends Component {
    state = { searchData: [], searchPost: "", selected: [], loading: false, selection: "", textSearch: "" }
    componentDidMount() { this.isActive() ; this.onSearchText() }
    onLoad = () => {
        const { textSearch } = this.state;
        if (textSearch.length > 0) {
            autoCompleteSearch(textSearch).then((response) => {
                const { tags, status } = response.data;
                if (status) this.setState({ searchData: tags, loading: false })
                else this.setState({ searchData: [], loading: false })
            })
        }
        else { this.setState({ searchData: [], loading: false }) }
    }

    onSearchText = () => {
        let textInput = document.getElementById('searchTag');
        let timeout = null;
        textInput.addEventListener('input', (e) => {
            clearTimeout(timeout);
            timeout = setTimeout(() => {
                this.setState({ loading: true, textSearch: textInput.value }, () => this.onLoad())
            }, 1000);
        });
    }

    onClickToSearch = (selectSearch, status) => event => {
        if (!status) {
            let textInput = document.getElementById('searchTag');
            textInput.value = selectSearch;
            let { selected } = this.state;
            selected.push(selectSearch)
            this.setState({ searchPost: selectSearch, selected: selected }, () => { this.props.updateFilterPostTags(this.state.searchPost) ; this.isActive() })
        }
        else {
            let textInput = document.getElementById('searchTag');
            textInput.value = "";
            this.setState({ searchData: [], searchPost: "", textSearch: "" })
            this.props.removeFilterPostTags(selectSearch)
        }
    }
    isActive = () => {
        var selection = "";
        const { filters } = this.props;
        if (filters && filters.length > 0) { filters.map((item) => { if (item.key == "tags") { selection = item.value } }) }
        this.setState({ selection })
    }
    componentWillReceiveProps(nextProps) {
        const { filterBody } = nextProps
        if (filterBody.postTags.length == 0 && this.state.searchData.length > 0) this.setState({ searchData: [], searchPost: "", textSearch: "" })
    }
    render() {
        const { searchData, selected, searchPost, loading, selection, textSearch } = this.state;
        const { imageTagList, filters, filterBody } = this.props; 
        return <>
            <div className="pt-4 checkbox-list">
                <div className="form-group tag_search mb-5">
                    <div className="input-group">
                        <div id="searchTagElements" className={`spinner-secondary spinner-right ${loading ? `spinner` : ``}`}>
                            <input type="text" className="form-control" placeholder="Search" id="searchTag" />
                        </div>
                        <div className="input-group-append">
                            <button className="btn" type="button">
                                <svg xmlns="http://www.w3.org/2000/svg" width="18.196" height="18.261" viewBox="0 0 20.196 20.261">
                                    <g id="searchtag" transform="translate(0.25 0.25)">
                                        <path id="Path_762" data-name="Path 762" d="M30.455,29.3,25.72,24.562a8.294,8.294,0,1,0-1.162,1.162l4.735,4.731A.82.82,0,0,0,30.455,29.3ZM19.3,25.949A6.651,6.651,0,1,1,25.953,19.3,6.657,6.657,0,0,1,19.3,25.949Z" transform="translate(-11 -11)" fill="#545454" stroke="#545454" strokeWidth="0.5"></path>
                                    </g>
                                </svg>
                            </button>
                        </div>
                    </div>
                </div>
                <div className="tags_all">
                    <ul className="list-unstyled m-0">
                        {
                            (searchData && (searchData.length > 0 || textSearch.length > 0)) ? <>{searchData.map((sItem, index) => {
                                const isChecked = Object.keys(filterBody).length > 0 ? filterBody.postTags.includes(sItem) : false
                                return <li className={`sideSearch${index} ${isChecked ? `img-filter-tags` : ``}`} onClick={this.onClickToSearch(sItem, isChecked)} key={index}><input type="checkbox" value={sItem} className="image-tags" tags={sItem} data-id={sItem} /> {sItem} <i className="ki ki-close"></i></li>
                            })}</> : imageTagList && imageTagList.map((item, index) => {
                                const isChecked = Object.keys(filterBody).length > 0 ? filterBody.postTags.includes(item) : false
                                return parseInt(index) < 9 ? <li key={`lI${index}`} className={`sideSearch${index} ${isChecked ? `img-filter-tags` : ``}`} onClick={this.onClickToSearch(item, isChecked)}><input type="checkbox" value={item} className="image-tags" /> {item} <i className="ki ki-close"></i></li> : null
                            })
                        }
                    </ul>
                </div>
            </div>
        </>
    }
}

export default PostTagSearch;