import React, { Component } from 'react'

class Tags extends Component {
    state = { isShow: false }
    onClickStatus = event => { const { isShow } = this.state; this.setState({ isShow: !isShow }) }
    render() {
        const { isShow } = this.state;
        const { item, onClickToPostTags, selection } = this.props;
        return <>
            {
                (item.tags && item.tags.length > 0 && String(item.tags).split(",").length > 0) ? String(item.tags).split(",").map((iTags, tagsIndex) => iTags && iTags.length > 0 ?
                    (iTags !== 'Tagalog' && iTags !== '') ? <span key={`tag_${tagsIndex}`} data-value="0" data-toggle="tooltip" tooltip="" data-theme="dark" title="" data-original-title="Click to Filter Post" className={`${tagsIndex > 4 && !isShow ? 'extra-tags d-none' : 'd-flex'} align-items-center ${selection.indexOf(iTags) > -1 ? 'active' : ''}`} onClick={onClickToPostTags(iTags)}>
                        <input type="checkbox" value={iTags} className={`image-tags ${item.id}`} defaultChecked={selection.indexOf(iTags) > -1} />
                        {iTags}
                    </span> : null : null) : null
            }

            {!isShow ? <a href="javascript:;" className={`tags-show-more align-item-center ${(item.tags && item.tags.length > 0 && String(item.tags).split(",").length > 5) ? '' : 'd-none'}`} data-id={item.id} onClick={this.onClickStatus}>Show More</a> : <a href="javascript:;" className="tags-show-less align-item-center" data-id={item.id} onClick={this.onClickStatus}>Show Less</a>}
        </>
    }
}
export default Tags