import React from 'react'
import { Link } from "react-router-dom";
import Tabs from '../tabs'
import Filters from './filters'
const SubHeaderTop = (props) => {
    return <div className="sub_header_top">
        <div className="container-fluid">
            <div className="row d-flex align-items-center justify-content-between flex-sm-nowrap">

                <div className="d-flex align-items-center ps-3"> 
                    {props.moderation.details && Object.keys(props.moderation.details).length > 0 ? <Tabs totalpost={6333} publicpost={5645} privatepost={689} upcomingpost={0} details={props.moderation.details} clickToTab={props.clickToTab} activeTab={props.activeTab} /> : null}
                </div>
                <Filters {...props} />
            </div>

        </div>
    </div>

}
export default SubHeaderTop;