import React, { Component } from 'react'
import { toast } from 'react-toastify';
import moment from 'moment'
import { Modal } from "react-bootstrap";
import { fetchPostData, autoCompleteSearch } from '../../../../../../actions/moderationActions'
import { checkIfStringHasSpecialChar, checkWordCount } from '../../../../../../utils'

class AddTag extends Component {
    state = { searchData: [], searchPost: "", selected: "", submitTags: [], isLoading: false, isRequest: false, isRemoved: 0, postTypes: "0", postTagAction: "2", totalCountTag: "0", tagRemoveAction: "0" }

    componentDidMount() {
        const { tags } = this.props;
        let submitTags = [];
        if (tags && String(tags.tags).split(",").length) {
            String(tags.tags).split(",").map((iTag) => { if (iTag && iTag.length > 0) { submitTags.push(iTag) } })
            this.setState({ submitTags: submitTags })
        }
        this.onSearchText()
    }

    onClickToSave = event => {

        let { submitTags, isRemoved, tagRemoveAction, postTagAction, totalCountTag } = this.state;
        const { data, filter, moderation } = this.props
        if (submitTags && submitTags.length > 0) {
            this.setState({ isRequest: true })

            let spostId = []
            let id = []
            let postTypes = []
            if (moderation.check_uncheck_all && moderation.check_uncheck_all.data) {
                moderation.check_uncheck_all.data.map((item) => {
                    id.push(item.id)
                    spostId.push(item.spostId)
                    postTypes.push(item.type)
                })
            }
            let saveObj = { selectedTags: submitTags.join(), postId: id.join(), postTypes: postTypes.join(), wallId: String(localStorage.getItem('wallId')), postTagAction: postTagAction, totalCountTag: String(totalCountTag), tagRemoveAction: tagRemoveAction, tags: submitTags.join(), db_table: moderation.filter.db_table, };
            this.props.onUpdateTags(saveObj, () => {
                this.setState({ isRequest: false })
                this.props.clickToCloseAddTag()
            });
        }
        else {
            toast.error('Please enter tags.');
        }
    }
    onClickToDelete = event => {
        let { submitTags, isRemoved, tagRemoveAction, postTagAction, totalCountTag } = this.state;
        const { data, filter, moderation } = this.props
        if (submitTags && submitTags.length > 0) {
            var isExists = false
            this.setState({ isRequest: true })

            let spostId = []
            let id = []
            let postTypes = []
            if (moderation.check_uncheck_all && moderation.check_uncheck_all.data) {
                moderation.check_uncheck_all.data.map((item) => {
                    id.push(item.id)
                    spostId.push(item.spostId)
                    postTypes.push(item.type)
                    if (!isExists && submitTags && submitTags.length > 0) {
                        submitTags.map((checkI) => {
                            if (!isExists && item.tags && String(item.tags).includes(checkI)) {
                                isExists = true;
                            }
                        })
                    }
                })
            }
            if (isExists) {
                let saveObj = { tags: submitTags.join(), postId: id.join(), wallId: String(localStorage.getItem('wallId')), db_table: moderation.filter.db_table, ownerId: String(localStorage.getItem('ownerId')) };
                this.props.onDeleteTags(saveObj, () => {
                    this.setState({ isRequest: false })
                    this.props.clickToCloseAddTag()
                });
            }
            else { toast.error(`Tag doesn't exist.`); }
        }
        else { toast.error('Please enter tags.'); }
    }
    onLoad = () => {
        const { searchPost } = this.state;
        if (searchPost.length > 0) {
            this.setState({ isLoading: true })
            autoCompleteSearch(searchPost).then((response) => {
                const { tags, status } = response.data;
                if (status) this.setState({ searchData: tags, isLoading: false })
                else this.setState({ searchData: [], isLoading: true })
            })
        }
        else { this.setState({ searchData: [], isLoading: false }) }
    }
    onSearchText = () => {
        let textInput = document.getElementById('searchTagpost');
        let timeout = null;
        textInput.addEventListener('input', (e) => {
            clearTimeout(timeout);
            timeout = setTimeout(() => { this.setState({ searchPost: textInput.value }, () => this.onLoad()) }, 1000);
        });
    }
    onClickToSearch = (selectSearch) => event => {
        let textInput = document.getElementById('searchTagpost');
        textInput.value = selectSearch;
        this.setState({ searchPost: selectSearch, selected: selectSearch, searchData: [] })
    }
    onClickToAddTags = event => {
        let { submitTags, selected, searchPost, totalCountTag } = this.state;
        let updateTag = selected && selected.length > 0 ? selected : searchPost;
        if (checkWordCount(updateTag)) {
            if (!checkIfStringHasSpecialChar(updateTag)) {
                if (!submitTags.includes(updateTag)) {
                    submitTags.push(updateTag)
                    let textInput = document.getElementById('searchTagpost');
                    textInput.value = "";
                    totalCountTag = parseInt(totalCountTag) + 1;
                    this.setState({ selected: "", searchPost: "", submitTags, totalCountTag })
                }
                else { toast.error('Tag already exists.'); }
            }
            else { toast.error('Special character not allow.'); }
        }
        else { toast.error('Please enter minimum 3 characters.'); }

    }
    onClickToRemoveTags = (index) => event => {
        let { submitTags, tagRemoveAction } = this.state;
        submitTags.splice(index, 1)
        tagRemoveAction = parseInt(tagRemoveAction) + 1;
        this.setState({ submitTags });
    }

    render() {
        const { open, clickToCloseAddTag, tags, data } = this.props
        const { searchData, submitTags, isLoading, searchPost, isRequest } = this.state;
        return (
            <Modal show={open} onHide={clickToCloseAddTag} backdrop="static" keyboard={false} centered size={"lg"}>
                <Modal.Header closeButton> <h5 className="m-0">Add Tags</h5></Modal.Header>
                <Modal.Body>
                    <div className="row">
                        <div className="col-lg-12 col-12" id="multipleTagSection">
                            <div className="tags-form-main mt-lg-0 mt-5">
                                <div className="form-group d-flex">
                                    <div className="w-100 ">
                                        <div className={`position-relative spinner-right formtagarea ${isLoading ? `spinner` : ``}`}>
                                            <input type="text" className="form-control postTagFilter" id="searchTagpost" autoComplete="off" placeholder="Enter your tag." />
                                            <div className="autoSuggestionList postTagsSugg">
                                                {
                                                    (searchData && searchData.length > 0) ? <div className="dropdown-menu-list">
                                                        <ul className="list-unstyled navi navi-hover">
                                                            {
                                                                searchData.map((sItem, indexItem) => <li key={indexItem} tags={sItem} data-id={sItem} className="image-tags navi-item" onClick={this.onClickToSearch(sItem)}>
                                                                    <a className="navi-link">{sItem}</a>
                                                                </li>)
                                                            }
                                                        </ul>
                                                    </div> : null
                                                }
                                            </div>
                                        </div>
                                    </div>
                                    <div className="ml-3">
                                        <button type="button" className={`btn-add addTagsPost btn btn-primary ${String(searchPost).length > 0 ? '' : 'disabled'}`} onClick={this.onClickToAddTags}><i className="fa fa-plus"></i></button>
                                    </div>
                                </div>
                                <div className="showTagsNote" style={submitTags && submitTags.length > 0 ? { display: 'none' } : {}}>
                                    <ul>
                                        <li>Add tags using keywords relevant to the content and click on the + Button.</li>
                                        <li>Tags will help you improve content filtering and ease moderation process.</li>
                                    </ul>
                                </div>
                                <div className="tags-list">
                                    <ul className="postTags">
                                        {
                                            submitTags && submitTags.length > 0 && submitTags.map((tag, tagKey) => {
                                                return (tag && tag.length > 0) ? <li className={tagKey}><span>{tag}</span><a className="removeSelectedTag" href="javascript:void(0)" alt={tagKey} wallId={localStorage.getItem('wallId')} postId={data[tagKey].postId} removeTag={tags} onClick={this.onClickToRemoveTags(tagKey)}><i className="ki ki-close"></i></a></li> : null
                                            })
                                        }
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                </Modal.Body>
                <Modal.Footer>
                    <button type="button" className="btn btn-secondary deleteAllPost px-5" onClick={this.onClickToDelete}>Delete All</button>
                    <button type="submit" className={`btn btn-primary savePost px-5 btn btn-primary savePost px-5 ${isRequest ? `spinner spinner-white spinner-right disabled` : ``}`} onClick={this.onClickToSave}>Save</button>
                </Modal.Footer>
            </Modal>
        )
    }
}

export default AddTag