import React, { Component } from 'react'
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import PinTopCard from './pinTopCard';

// fake data generator
const getItems = count => Array.from({ length: count }, (v, k) => k).map(k => ({ id: `item-${k}`, content: `item ${k}` }));

// a little function to help us with reordering the result
const reorder = (list, startIndex, endIndex) => {
  const result = Array.from(list);
  const [removed] = result.splice(startIndex, 1);
  result.splice(endIndex, 0, removed); return result; };
const grid = 8;
const getItemStyle = (isDragging, draggableStyle) => ({ ...draggableStyle });
const getListStyle = isDraggingOver => ({ });


export default class pinToTopPosts extends Component {
    constructor(props) {
        super(props);
        this.state = {  items: [] };
        this.onDragEnd = this.onDragEnd.bind(this);
      }
    
   componentDidMount() { this.onLoadData(this.props); }

   componentDidUpdate(prevProps) {
    if (prevProps.pinToTop !== this.props.pinToTop)  this.onLoadData(this.props) 
   }

    onLoadData = (props) => {
        const { pinToTop, setNewArray } = props;
        this.setState({ items: pinToTop });
        setNewArray(pinToTop);
    }

      onDragEnd(result) {
        if (!result.destination) {  return; }
        const items = reorder( this.state.items, result.source.index, result.destination.index );
        this.setState({ items });
        this.props.setNewArray(items);
      }

  render() { 
    return (
        <div className='pinPost_outer'>
            <div className="pintotoppost_main">
                <DragDropContext onDragEnd={this.onDragEnd} >
                    <Droppable droppableId="droppable">
                    {(provided, snapshot) => (
                        <div {...provided.droppableProps} ref={provided.innerRef} style={getListStyle(snapshot.isDraggingOver)}>
                        {this.state.items && this.state.items.length && this.state.items.map((item, index) => (
                            <Draggable key={`item-${index}`} draggableId={`item-${index}`} index={index}>
                            {(provided, snapshot) => (
                                <div ref={provided.innerRef} {...provided.draggableProps} {...provided.dragHandleProps} style={getItemStyle( snapshot.isDragging, provided.draggableProps.style)}>
                                    <PinTopCard item={item} indexNo={index}/>
                                </div>
                            )}
                            </Draggable>
                        ))}
                        {provided.placeholder}
                        </div>
                    )}
                    </Droppable>
                </DragDropContext>
            </div>
        </div>
    )
  }
}
