
import moment from 'moment'
import React, { Component } from 'react'
import RadialChart from './radialChart'

export default class Pending extends Component {
    state={days:0}

    componentDidMount(){ this.getNewdata() }

    getNewdata=()=>{
        const { ugcData, ugcRequestData, onHide } = this.props
        var new_date = moment().diff(moment(ugcRequestData.ugccreated, "YYYY-MM-DD"), 'days');
        var ago = 30 - new_date;
        this.setState({days:ago})
    }
    componentDidUpdate(prevProps){
        if(prevProps.ugcRequestData.ugccreated !== this.props.ugcRequestData.ugccreated){
            this.getNewdata();
        }
    }


    render() {
        const { ugcData, ugcRequestData, onHide } = this.props
        const { days} = this.state
        
        return (
            <div className="ugc-modal">
                <div className="modal-dialog m-w-100 m-0" role="document">
                    <div className="modal-content rounded-0">
                        <div className="modal-header py-3">
                            <div className="d-flex align-items-center flex-nowrap">
                                <span className="svg-icon-col mr-3">
                                    <svg xmlns="http://www.w3.org/2000/svg" width="32" height="32" viewBox="0 0 24 24" className="me-1">
                                        <g id="Group_5459" data-name="Group 5459" transform="translate(-0.327 -2.327)">
                                            <g id="Group_5314" data-name="Group 5314" transform="translate(0.327 2.327)">
                                                <path id="Path_5337" data-name="Path 5337" d="M-81.977,67.249a12,12,0,0,0-12,12,12,12,0,0,0,12,12,12,12,0,0,0,12-12A12,12,0,0,0-81.977,67.249Z" transform="translate(93.977 -67.249)" fill="#FF8100"></path>
                                                <path id="Path_5338" data-name="Path 5338" d="M-69.017,88.464l-3.96-3.96a1.225,1.225,0,0,1-.36-.84V77.687a1.2,1.2,0,0,1,1.2-1.2,1.2,1.2,0,0,1,1.2,1.2v5.5l3.6,3.6a1.188,1.188,0,0,1,0,1.68,1.188,1.188,0,0,1-1.68,0Z" transform="translate(84.113 -71.664)" fill="#fff"></path>
                                            </g>
                                        </g>
                                    </svg>
                                </span>
                                <h5 className="modal-title">Pending Request</h5>
                            </div>
                            <button type="button" className="btn-close ugc_close" data-bs-dismiss="modal" aria-label="Close" onClick={onHide}></button>
                        </div>
                        <div className="modal-body p-0">
                            <div className="container-fluid">
                                <div className="row ugcPendingRow">
                                    <div className="col ugc_col_left mb20 ugc_card_preview ugc_modal_pending_first_box border-right-lg">
                                        <div className="my-5 ugc_card_wrap">
                                            <div className="alert alert-custom alert-outline-1x alert-outline-pending fade show mb-6 text-center p-4 mx-5" role="alert">
                                                <div className="alert-text"><h5 className="m-0 text-center font-weight-bold">Oops! Your UGC Request is pending now</h5></div>
                                            </div>
                                            <div className="card_slider">
                                                <div className="m_card_style">
                                                    <div className="m_card_img">
                                                        <div className="post-content-image">
                                                            <div className="m_media_holder">

                                                                <img className="img-fluid ex-coverimg" src={ugcRequestData.postFile} />
                                                                {(ugcRequestData.type == 3 || ugcRequestData.type == 5) ? <div className="play_icon_wrap">
                                                                    <img className="pay_icon" src="https://app.taggbox.com/widget/img/play.png" />
                                                                </div> : null}
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div className="m_card_cont">
                                                        <div className="d-flex align-items-center justify-content-between mb-4">
                                                            <div className="d-flex align-items-center m_grid_author">
                                                                <div className="flex-shrink-0 mr-4 mt-lg-0 mt-3">
                                                                    <div className="symbol symbol-circle symbol-lg-75 m_profile_img" style={{
                                                                        backgroundImage: `url(${ugcRequestData.picture})`
                                                                    }}></div>
                                                                </div>
                                                                <div className="d-flex flex-column post-link m_user_content">
                                                                    <a href={ugcRequestData.link ? parseInt(ugcRequestData.link) ? 'https://twitter.com/' + ugcRequestData.username + '/status/' + ugcRequestData.link : ugcRequestData.link : 'javascript:;'} target="_blank" className="m_post_title ng-binding">{ugcRequestData.name}</a>
                                                                    <div className="d-flex align-item-center m_grid_userinfo">
                                                                        <span className="m_grid_uername text-nowrap">@<span>{ugcRequestData.username}</span></span>
                                                                        <span className="sepratedot"></span>
                                                                        <span className="timePost text-nowrap">{moment(ugcRequestData.modified, "YYYYMMDD").fromNow()}</span>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                            <div className="d-flex flex-column m_social_icon postNetwork" data-network="Instagram Business">
                                                                <i className={`fa ${ugcRequestData.icon}`}></i>
                                                            </div>
                                                        </div>
                                                        <div className="postedCon setFont font5">
                                                            <p className="m-0" style={{ lineHeight: '22px', wordBreak: 'break-all' }}>{ugcRequestData.postcontent}</p>

                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col ugc_col_right d-flex flex-column p-0">
                                        <div className="modal_wrapper">
                                            <div className="modal-body ugc_pending">
                                                <div className="ugc_modal_pending_second_inner_box flex-grow-1">
                                                    <div className="ugc-daysremain">
                                                        <div id="kt_mixed_widget_14_chart">{days ? <RadialChart ago={ days} />:''}</div>
                                                        <span>Remaining</span>
                                                    </div>
                                                    <div className="form-group ugc-pending-non-insta" style={ugcRequestData.networkId == 18 ? { display: 'none' } : {}}>
                                                        <div className="input-group input-group-lg input-group-gray">
                                                            <div className="input-group-prepend">
                                                                <span className="input-group-text"> <i className="fas fa-link"></i> </span>
                                                            </div>
                                                            <a href={ugcRequestData.bitlyurl} target="_blank" className="form-control openPendingStatus">{ugcRequestData.bitlyurl}</a>
                                                        </div>
                                                        <span id="ugc-copy-success-alert" style={{ display: "none" }} className="ugc_copy_success_alertt text-success">Copied to clipboard</span>
                                                    </div>
                                                    <span className="get-temp-msg d-none">{ugcRequestData.custom_message}</span>
                                                    <div className="form-group py-3 ugc-pending-non-insta" style={ugcRequestData.networkId == 18 ? { display: 'none' } : {}}>
                                                        <button className="btn btn-primary btn-lg ugc_copy_text_button w-100" onClick={this.copyText}>
                                                            Copy URL
                                            </button>
                                                    </div>
                                                    <div className="form-group ugc_link_share_copy_box ugc_modal_pending_link">
                                                        <div className="ugc-stepstoproceed">
                                                            <h3>Steps to proceed</h3>
                                                            <ul className="list-unstyled ugc-pending-insta" style={ugcRequestData.networkId == 18 ? {display: 'block'} : { display: 'none' }}>
                                                                <li>You have sent the "UGC Rights" request by posting the hashtag and content on the users post.</li>
                                                                <li>Now wait for the user to reply to your comment to get "UGC Rights".</li>
                                                                <li>In case the user does not approve your "UGC Rights" request, you have the option of modifying the comment and resending the UGC request. You can also offer a giveaway or incentive to make a better proposal. </li>
                                                            </ul>
                                                            <ul className="list-unstyled ugc-pending-non-insta" style={ugcRequestData.networkId == 18 ? { display: 'none' } : {display: 'block'}}>
                                                                <li>You have successfully generated a "UGC Request" for this post.</li>
                                                                <li>Quick review your Request before sending it to the owner.</li>
                                                                <li>Click on the "Done" button located in the right bottom corner of the screen and you will be redirected to the user's original post.</li>
                                                                <li>Paste the link of "UGC Request" in the comments section of the original post.</li>
                                                                <li>You will get UGC Rights asap post owner accept your request and allow you to use their post.</li>
                                                            </ul>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="ugcAuthBtn ugc-peingdone modal-footer text-right ugc_modal_pending_btn">
                                                <button type="button" className="btn btn-primary btn-min-w" onClick={onHide}>Done</button>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
