import React, { Component } from 'react'
import { connect } from "react-redux";
import parse from 'html-react-parser';
import { generateSocialName } from '../../../utils'
import PostTagSearch from './postTagSearch'
import { updateFilterPostTags, updateFilterSocialFeeds, updateFilterPostType, updateFilterOthers, removeFilterSocialFeeds, removeFilterPostType, removeFilterOthers, removeFilterPostTags, removeAllFilterBody } from '../../../../../../../actions/moderationActions'


const ReactPostType = ({ value, onClickPostType, moderation }) => {
    const selecteds = Object.keys(moderation.filterBody).length > 0 && moderation.filterBody.postType.length > 0 ? moderation.filterBody.postType.filter((fin) => fin == value) : []
    const isChecked = selecteds && selecteds.length > 0 ? true : false
    return <><input type="checkbox" name="data[postType][]" value="1" className="checkbox-custom feedsLabel md-check moderateFilters postTypeFilter" id="moderationpostType1" onChange={onClickPostType(value, isChecked)} checked={isChecked} /> <span></span></>
}

const ReactOtherInput = ({ id, value, onClickOther, moderation }) => {
    const selecteds = Object.keys(moderation.filterBody).length > 0 && moderation.filterBody.other.length > 0 ? moderation.filterBody.other.filter((fin) => fin.key == id) : []
    const isChecked = selecteds && selecteds.length > 0 ? true : false
    return <><input type="checkbox" name="data[highlightFilter]" value={value} className={`checkbox-custom feedsLabel ${id}`} id="moderationhighlightFilter" onChange={onClickOther(id, value, isChecked)} checked={isChecked} /> <span></span></>
}


class FilterBody extends Component {
    state = { socialFeeds: [], postType: [], other: [], feeds: [] }
    onSelectFeeds = (key, value, status) => event => {
        if (!status) this.props.updateFilterSocialFeeds({ key, value })
        else this.props.removeFilterSocialFeeds({ key, value })
    }
    onClickPostType = (value, status) => event => {
        if (!status) this.props.updateFilterPostType(value)
        else this.props.removeFilterPostType(value)
    }
    onClickOther = (key, value, status) => event => {
        if (!status) this.props.updateFilterOthers({ key, value })
        else this.props.removeFilterOthers({ key, value })
    }
    componentDidMount() {
        if (window.feedId && window.feedId != "") {
            const { details, filters, moderation } = this.props
            const feeds = details?.feeds;
 
            feeds && feeds.map((item, fIndex) => { if (window.feedId == item.id) { this.props.updateFilterSocialFeeds({ key: item.name, value: window.feedId }) } })
        }
    }

    render() {
        const { details, filters, moderation } = this.props
        const UserRuleEnableAI = false;
        const UserRuleManualTag = true;
        const imageTagList = details?.imageTagList;
        const feeds = details?.feeds; 

        return <div className="filter-body">
            <div className="filterSecHeight">
                <div id="filter-form">
                    <div id="filter_toggle" className="offcanvas offcanvas-right filterMed" tabIndex="-1" aria-labelledby="offcanvasRightLabel" >
                        <div className="filter_wrapper">
                            <div className="offcanvas-content">
                                <div className="offcanvas-header d-flex align-items-center justify-content-between ps-3 py-1 pe-0 border-bottom">
                                    <h3 className="font-weight-bold m-0">Filter </h3>
                                    <a href="#" className="btn btn-xs filter_toggle_close" id="filter_toggle_close" data-bs-dismiss="offcanvas" aria-label="Close">
                                        <i className="ki ki-close icon-xs text-muted"></i>
                                    </a>
                                </div>
                                {(UserRuleEnableAI || UserRuleManualTag) ? <div className="accordion accordion-light accordion-toggle-arrow pt-3 mx-5 pb-3 border-bottom" id="tagspost">
                                    <div className="card">
                                        <div className="card-header" id="headingOne2">
                                            <div className="card-title icon icon-solid" data-bs-toggle="collapse" data-bs-target="#tag_post_btn">
                                                <h6>POST TAGS
                                                    <p className="mb-0">
                                                        {UserRuleEnableAI && <>
                                                            <i>Powered by
                                                                <svg xmlns="http://www.w3.org/2000/svg" id="Group_7367" data-name="Group 7367" width="7.428" height="11.033" viewBox="0 0 7.428 11.033"> <path id="Path_5034" data-name="Path 5034" d="M249.762,185.454l-1.393,1.39a.23.23,0,0,1-.161.067V187l0-.094a.23.23,0,0,1-.162-.069l-.7-.727a.228.228,0,0,1,.328-.316l.542.56,1.229-1.227a.228.228,0,1,1,.322.322Z" transform="translate(-244.839 -182.349)" fill="#848484" /> <g id="noun_premium_426772" transform="translate(0 0)"> <path id="Path_7085" data-name="Path 7085" d="M25.285,5.76a.488.488,0,0,1-.128-.548l.07-.175a.482.482,0,0,0-.315-.652l-.186-.047a.487.487,0,0,1-.35-.443V3.7a.482.482,0,0,0-.559-.454l-.186.035a.461.461,0,0,1-.5-.245l-.093-.163a.48.48,0,0,0-.7-.163l-.151.1a.491.491,0,0,1-.559,0L21.475,2.7a.485.485,0,0,0-.711.163l-.093.163a.485.485,0,0,1-.513.245l-.186-.035a.492.492,0,0,0-.571.454l-.012.186a.487.487,0,0,1-.35.443l-.186.058a.482.482,0,0,0-.315.652l.07.175a.488.488,0,0,1-.128.548l-.14.128a.476.476,0,0,0,0,.722l.14.128a.488.488,0,0,1,.128.548l-.07.175a.482.482,0,0,0,.315.652l.186.047a.487.487,0,0,1,.35.443l.012.186a.482.482,0,0,0,.559.454l.186-.035a.461.461,0,0,1,.5.245l.093.163a.48.48,0,0,0,.7.163l.151-.1a.491.491,0,0,1,.559,0l.151.117a.485.485,0,0,0,.711-.163l.093-.163a.485.485,0,0,1,.513-.245l.186.035a.492.492,0,0,0,.571-.454l.012-.186a.487.487,0,0,1,.35-.443l.186-.047a.482.482,0,0,0,.315-.652l-.07-.175a.488.488,0,0,1,.128-.548l.14-.128a.476.476,0,0,0,0-.722Zm-3.4,2.68a2.2,2.2,0,1,1,2.2-2.2A2.2,2.2,0,0,1,21.883,8.44ZM18.225,12.7l1.142-2.855a1.216,1.216,0,0,0,.5.117,1.284,1.284,0,0,0,.21-.023H20.1l.012.023a1.181,1.181,0,0,0,1.049.606,1.157,1.157,0,0,0,.35-.058l-1.2,3.018a.114.114,0,0,1-.21.012l-.571-1a.078.078,0,0,0-.093-.035l-1.072.338A.108.108,0,0,1,18.225,12.7Zm5.231.862-1.212-3.029a1.22,1.22,0,0,0,.35.058,1.182,1.182,0,0,0,1.049-.618l.012-.023h.023a1.284,1.284,0,0,0,.21.023,1.27,1.27,0,0,0,.513-.117l1.142,2.878a.112.112,0,0,1-.14.151l-1.072-.338a.078.078,0,0,0-.093.035l-.571,1A.126.126,0,0,1,23.456,13.566Z" transform="translate(-18.175 -2.603)" fill="#848484" /> </g> </svg>
                                                                <b>TruSense</b></i></>
                                                        }
                                                    </p>
                                                </h6>
                                            </div>
                                        </div>
                                        <div id="tag_post_btn" className="collapse show" data-parent="#tagspost"> <PostTagSearch imageTagList={imageTagList} {...this.props} /> </div>
                                    </div>
                                </div> : null}


                                <div className="accordion accordion-light accordion-toggle-arrow pt-5 mx-5 pb-0" id="post_type_drop">
                                    <div className="card">
                                        <div className="card-header" id="headingOne2">
                                            <div className="card-title" data-bs-toggle="collapse" data-bs-target="#social_feed_btn">
                                                <h6>SOCIAL FEEDS</h6>
                                            </div>
                                        </div>
                                        <div id="social_feed_btn" className="collapse show" data-parent="#post_type_drop">
                                            <div className="pt-4 checkbox-list">
                                                {
                                                    feeds && feeds.map((item, fIndex) => {
                                                        const selecteds = Object.keys(moderation.filterBody).length > 0 && moderation.filterBody.socialFeed.length > 0 ? moderation.filterBody.socialFeed.filter((fin) => fin.value == item.id) : []
                                                        const isChecked = selecteds && selecteds.length > 0 ? true : false
                                                        return <label className="checkbox m_checkbox" key={`${fIndex}-${item.id}`}>
                                                                <input type="checkbox" name="data[feeds][]" id={`moderationfeed${item.id}`} value={item.id} data-filter={item.filterId} className="checkbox-custom feedsLabel feedsFilter md-check moderateFilters" onChange={this.onSelectFeeds(item.name, item.id, isChecked)} checked={isChecked} /> <span></span>
                                                            {item.Network.icon != 'fa-twitter' ?
                                                                <i className={`fab ${item.Network.icon}  mr-3`}></i>
                                                                :
                                                                <i className={`ts__icon ts-twitter  mr-3`} style={{fontSize: "15px"}}></i>
                                                                }
                                                                <p>{parse(generateSocialName(item))}</p>
                                                            </label>
                                                    })
                                                }

                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="separator separator-solid my-3 mx-5"></div>
                                <div className="accordion accordion-light accordion-toggle-arrow mx-5" id="post_type_drop">
                                    <div className="card">
                                        <div className="card-header" id="headingOne2">
                                            <div className="card-title icon icon-solid" data-bs-toggle="collapse" data-bs-target="#post_tytpe_btn"><h6>POST TYPE</h6></div>
                                        </div>
                                        <div id="post_tytpe_btn" className="collapse show" data-parent="#post_type_drop">
                                            <div className="pt-4 checkbox-list">
                                                <label className="checkbox m_checkbox" data-text="Text Only">
                                                    <ReactPostType value="1" onClickPostType={this.onClickPostType} {...this.props} /> Text Only
                                                </label>

                                                <label className="checkbox m_checkbox" data-text="With Image"> 
                                                <ReactPostType value="2,4" onClickPostType={this.onClickPostType} moderation={moderation} />
                                                    With Image
                                                </label>

                                                <label className="checkbox m_checkbox" data-text="With Video">
                                                     <ReactPostType value="3,5" onClickPostType={this.onClickPostType} moderation={moderation} />
                                                    With Video
                                                </label>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="separator separator-solid my-3 mx-5"></div>
                                <div className="accordion accordion-light accordion-toggle-arrow mx-5" id="other_fil">
                                    <div className="card">
                                        <div className="card-header" id="headingOne2">
                                            <div className="card-title icon icon-solid" data-bs-toggle="collapse" data-bs-target="#other_fil_btn"><h6>OTHER</h6></div>
                                        </div>
                                        <div id="other_fil_btn" className="collapse show" data-parent="#other_fil">
                                            <div className="pt-4 checkbox-list">
                                                <label className="checkbox m_checkbox" data-text="Highlight Post">
                                                    <ReactOtherInput id="highlightFilter" value="1" onClickOther={this.onClickOther} moderation={moderation} />  Highlight Post
                                                </label>
                                                <label className="checkbox m_checkbox" data-text="Pinned to top">
                                                    <ReactOtherInput id="pinFilter" value="1" onClickOther={this.onClickOther} moderation={moderation} /> Pinned to top
                                                </label>
                                                <label className="checkbox m_checkbox" data-text="Recent">
                                                    <ReactOtherInput id="recentFilter" value="1" onClickOther={this.onClickOther} moderation={moderation} /> Recent
                                                </label>
                                                <label className="checkbox m_checkbox" data-text="Retweets">
                                                    <ReactOtherInput id="retweetsFilter" value="1" onClickOther={this.onClickOther} moderation={moderation} /> Retweets
                                                </label>

                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="separator separator-solid my-3 mx-5"></div>
                                <div className="accordion accordion-light accordion-toggle-arrow mx-5" id="ugc_rights" style={{ display: 'none' }}>
                                    <div className="card">
                                        <div className="card-header" id="headingOne2">
                                            <div className="card-title" data-bs-toggle="collapse" data-bs-target="#ugc_rights_btn"><h6>UGC RIGHTS</h6></div>
                                        </div>
                                        <div id="ugc_rights_btn" className="collapse show" data-parent="#ugc_rights">
                                            <div className="pt-4 checkbox-list">
                                                <label className="checkbox m_checkbox" data-text="Pending Post">
                                                    <input type="checkbox" name="data[pending]" value="1" className="checkbox-custom feedsLabel pending ugcstatusFilter" id="moderationpending" /> <span></span>
                                                    Pending Post
                                                </label>
                                                <label className="checkbox m_checkbox" data-text="Approved Post">
                                                    <input type="checkbox" name="data[approved]" value="2" className="checkbox-custom feedsLabel approved ugcstatusFilter" id="moderationapproved" /> <span></span>
                                                    Approved Post
                                                </label>
                                                <label className="checkbox m_checkbox" data-text="Rejected Post">
                                                    <input type="checkbox" name="data[rejected]" value="3" className="checkbox-custom feedsLabel rejected ugcstatusFilter" id="moderationrejected" /> <span></span>
                                                    Rejected Post
                                                </label>
                                                <label className="checkbox m_checkbox" data-text="Expired Post">
                                                    <input type="checkbox" name="data[expired]" value="4" className="checkbox-custom feedsLabel expired ugcstatusFilter" id="moderationexpired" /> <span></span>
                                                    Expired Post
                                                </label>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="d-flex flex-column mx-5">
                                    <button type="submit" className="btn btn-primary font-weight-bold d-none mt-0"><span className="title">Submit</span></button>
                                    <button type="button" onClick={this.props.removeAllFilterBody} className="btn btn-link font-weight-bold text-dark m_reset_btn mt-0">Reset Results</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    }
}
const mapStateToProps = (state) => {
    return { moderation: state.moderation }
}
const mapDispatchToProps = dispatch => {
    return {
        updateFilterPostTags: (data) => dispatch(updateFilterPostTags(data)),
        updateFilterSocialFeeds: (data) => dispatch(updateFilterSocialFeeds(data)),
        updateFilterPostType: (data) => dispatch(updateFilterPostType(data)),
        updateFilterOthers: (data) => dispatch(updateFilterOthers(data)),
        removeFilterSocialFeeds: (data) => dispatch(removeFilterSocialFeeds(data)),
        removeFilterPostType: (data) => dispatch(removeFilterPostType(data)),
        removeFilterOthers: (data) => dispatch(removeFilterOthers(data)),
        removeFilterPostTags: (data) => dispatch(removeFilterPostTags(data)),
        removeAllFilterBody: () => dispatch(removeAllFilterBody()),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(FilterBody);