import React, { Component } from 'react'

export default class TbTab extends Component {
    render() {
        const {
            tab,
            textValue,
            onTextChange,
            checkBox,
            onCheckBoxChange,
            error
        } = this.props
        return (
            <>
                <div className="form-group">
                    <div className="input-group">
                        <div className="input-group-prepend">
                            <span className="input-group-text">
                                <i className={`fas fa-${tab == "Hashtag" ? "hashtag" : "at"}`}></i>
                            </span>
                        </div>
                        <input autoComplete="off" value={textValue} onChange={(e) => onTextChange(e.target.value)} placeholder={`Enter ${tab == "Hashtag" ? "hashtag" : "user handle"}`} className="autosearch mycheck form-control" id="" type="text" />
                    </div>
                    {error ? <span id="feedsfield2error" className="errorMessage required">This field is required</span> : null}
                </div>
                <div className="hideforBSearch">
                    <div className="checkbox-inline">
                        <label htmlFor={`checkbox_${tab}`} className="checkbox checkbox-outline checkbox-primary mr-2">
                            <input type="checkbox" id={`checkbox_${tab}`} checked={checkBox} onChange={(e) => onCheckBoxChange(e.target.checked)} className="checkbox checkbox-outline checkbox-outline-2x checkbox-primary mr-2" value="1" />
                            <span></span>
                            Manually approve posts before making them public?
                        </label>
                    </div>
                </div>
            </>
        )
    }
}
