import React, { Component } from 'react'
import AccountPicker from '../accountPicker'

export default class TiktokTab extends Component {
    render() {
        const { tab,  onPostUrlChange,  postUrl, onValueChange, checkBox, error, onCheckBoxChange, onAddAccount, connectedAcc, connetedAccount, onConnectedAccountChange , onAuthenticateChange, authenticate} = this.props
        console.log(tab)
        return (
            <>
                {
                    <div className="form-group">
                        <div className="input-group">
                            <div className="input-group-prepend">
                                <span className="input-group-text">
                                    <i className={`fas ${tab == 'Hashtag' ? 'fa-hashtag' : tab == 'Handle' ? 'fa-at' : 'fa-link'}`}></i>
                                </span>
                            </div>
                            <input className="form-control" value={postUrl} onChange={e => onPostUrlChange(e.target.value)} placeholder={`Enter Tiktok ${tab == 'Hashtag' ? 'Hashtag' : tab == 'Handle' ? 'Handle' : 'Post Url'}`} type="text" id="FeedTiktokPostUrl" />
                        </div>
                        {error ? <span id="feedstiktok_post_urlerror" className="errorMessage required">This field is required.</span> : null}
                    </div>
                 }
                <div className="hideforBSearch">
                    <div className="checkbox-inline">
                        <label htmlFor="feedscreatemoderation" className="checkbox checkbox-outline checkbox-primary mr-2">
                            <input type="checkbox" id="feedscreatemoderation" checked={checkBox} onChange={e => onCheckBoxChange(e.target.checked)} className="checkbox checkbox-outline checkbox-outline-2x checkbox-primary mr-2" />
                            <span></span>
                            Manually approve posts before making them public?
                        </label>
                    </div>
                </div>
            </>
        )
    }
}
