import React, { Component } from 'react'
import { autoCompleteSearch } from '../../../../../../../../actions/moderationActions'

class QuickSearch extends Component {
    state = {
        searchData: [],
        searchPost: "",
        selected: ""
    }
    componentDidMount() {
        this.onSearchText()
    }
    onLoad = () => {
        const { searchPost } = this.state;
        if (searchPost.length > 0) {
            autoCompleteSearch(searchPost).then((response) => {
                const { tags, status } = response.data;
                if (status) this.setState({ searchData: tags })
                else this.setState({ searchData: [] })

            })
        }
        else {
            this.setState({ searchData: [] })
        }
    }
    onSearchText = () => {
        let textInput = document.getElementById('searchPost');
        let timeout = null;
        textInput.addEventListener('input', (e) => {
            clearTimeout(timeout);
            timeout = setTimeout(() => {
                this.setState({ searchPost: textInput.value }, () => this.onLoad())
            }, 1000);
        });
    }
    onClickToSearchTags = (selectSearch) => event => {
        let textInput = document.getElementById('searchPost');
        textInput.value = selectSearch;
        this.setState({ searchPost: selectSearch, selected: selectSearch, searchData: [] }, () => this.props.searchTagsFilter(this.state.selected))
    }
    onClickToSearchPost = (selectSearch) => event => {
        if (selectSearch && String(selectSearch).length > 0) this.setState({ searchPost: selectSearch, selected: selectSearch, searchData: [] }, () => this.props.searchPostFilter(this.state.searchPost))
    }

    render() {
        const { searchData, selected, } = this.state;
        return <>
            <div className="quick_search d-flex align-items-center">
                <div className="quick-search quick-search-dropdown fade collapse me-2" id="kt_quick_search_dropdown">
                    <div id="top-search-form" className="quick-search-form position-relative">
                        <div className="input-group input-group-sm">
                            <input name="searchIt" id="searchPost" className="form-control" placeholder="Search..." type="text" />
                            <input type="hidden" name="status" id="moderationstatus" autoComplete="off" />
                            <input type="hidden" name="load_posts" id="load_posts" autoComplete="off" />
                            <div className="input-group-append">
                                <button type="submit" className="submit btn btn-primary svg-icon svg-icon-sm svg-icon-light py-0 px-3" onClick={this.onClickToSearchPost(this.state.searchPost)}>
                                    <svg xmlns="http://www.w3.org/2000/svg" width="20.196" height="20.261" viewBox="0 0 20.196 20.261">
                                        <g id="search" transform="translate(0.25 0.25)">
                                            <path id="Path_762" data-name="Path 762" d="M30.455,29.3,25.72,24.562a8.294,8.294,0,1,0-1.162,1.162l4.735,4.731A.82.82,0,0,0,30.455,29.3ZM19.3,25.949A6.651,6.651,0,1,1,25.953,19.3,6.657,6.657,0,0,1,19.3,25.949Z" transform="translate(-11 -11)" fill="#ffffff" stroke="#ffffff" strokeWidth="1" />
                                        </g>
                                    </svg>
                                </button>
                            </div>
                        </div>
                        <input type="hidden" name="searchPostArrow" id="searchPostArrow" />
                        <div id="tagSuggestionList">
                            {
                                (searchData && searchData.length > 0) ? <div className="dropdown-menu-list">
                                    <ul className="list-unstyled navi navi-hover">
                                        {
                                            searchData.map((sItem) => <li tags={sItem} data-id={sItem} className="image-tags navi-item" onClick={this.onClickToSearchTags(sItem)}>
                                                <a className="navi-link">{sItem}</a>
                                            </li>)
                                        }

                                    </ul>
                                </div> : null
                            }
                        </div>
                    </div>
                </div>
                <a className="btn btn-icon btn-icon-mobile btn-clean d-flex align-items-center px-2 search_btn collapsed" data-bs-toggle="collapse" href="#kt_quick_search_dropdown" role="button" aria-expanded="false" aria-controls="kt_quick_search_dropdown">
                    <svg className="search_btn_svg" xmlns="http://www.w3.org/2000/svg" width="20.196" height="20.261" viewBox="0 0 20.196 20.261">
                        <g id="search" transform="translate(0.25 0.25)">
                            <path id="Path_762" data-name="Path 762" d="M30.455,29.3,25.72,24.562a8.294,8.294,0,1,0-1.162,1.162l4.735,4.731A.82.82,0,0,0,30.455,29.3ZM19.3,25.949A6.651,6.651,0,1,1,25.953,19.3,6.657,6.657,0,0,1,19.3,25.949Z" transform="translate(-11 -11)" fill="#6d7175" stroke="#6d7175" strokeWidth="0.5" />
                        </g>
                    </svg>
                    <svg className="close_btn_svg" xmlns="http://www.w3.org/2000/svg" xlink="http://www.w3.org/1999/xlink" width="32px" height="32px" viewBox="0 0 24 24" version="1.1">
                        <g transform="translate(12.000000, 12.000000) rotate(-45.000000) translate(-12.000000, -12.000000) translate(4.000000, 4.000000)" fill="#6d7175">
                            <rect x="0" y="7" width="16" height="2" rx="1"></rect>
                            <rect transform="translate(8.000000, 8.000000) rotate(-270.000000) translate(-8.000000, -8.000000) " x="0" y="7" width="16" height="2" rx="1"></rect>
                        </g>
                    </svg>
                </a>
            </div>
        </>
    }
}

export default QuickSearch;