import React from 'react'
import NoPostBlock from './noPostBlock'
import GridView from '../grid'
import ListView from '../list'
import GridSkeleton from '../../skeleton/grid'
import ListSkeleton from '../../skeleton/list'
const ModerationPost = (props) => {
    const { renderedView, loader, onShow } = props;
    return <div className="allModrtionPost scrlBarComan h-100">
        <div id="moderationinnerposts" className="h-100">
            {props.total == 0 && !props.loding ? <NoPostBlock feeds={props.feeds} /> :
            <div className="moderation_card tab-content moderationPostsX h-100" id="moderationPostsX">
                {renderedView == 1 ? loader.filterLoader ? <GridSkeleton {...props} /> : <GridView {...props} onShow={onShow}/> : renderedView == 2 ? loader.filterLoader ? <ListSkeleton {...props}  onShow={onShow}/> : <ListView {...props} /> : null}
            </div> }

        </div>

    </div>
}
export default ModerationPost