import React from 'react'
import parse from "html-react-parser";
var output = '';
for (var count = 0; count < 20; count++) {
    output += '<div class="sk_list_view">';
    output += '<div class="card card-custom gutter-b">';
    output += '<div class="sk_loader_">';
    output += '<div class="card-body d-flex align-items-center p-6">';
    output += '<div class="flex-shrink-0 mr-5 sk_img_thumb"></div>';
    output += '<div class="flex-grow-1">';
    output += '<div class="d-flex align-items-center flex-wrap row">';
    output += '<div class="m-col-9 d-flex justify-content-between align-items-center sk_author_left">';
    output += '<div class="flex-column w-100">';
    output += '<div class="d-flex align-items-center mb-4">';
    output += '<div class="d-flex align-items-center w-100">';
    output += '<div class="m_grid_author flex-shrink-0 mr-6 ">';
    output += '<div class="sk_img"></div>';
    output += '</div>';
    output += '<div class="d-flex flex-column w-25">';
    output += '<h4 class="sk_line sk_line_70 sk_line_height_12 m-0"></h4>';
    output += '<p class="sk_line sk_line_40 sk_line_height_8 mb-0"></p>';
    output += '</div>';
    output += '</div>';
    output += '</div>';
    output += '<div class="d-flex align-items-center flex-wrap justify-content-between">';
    output += '<div class="flex-grow-1 pr-5">';
    output += '<p class="sk_line sk_line_height_8 mb-0"></p>';
    output += '<p class="sk_line sk_line_height_8 mb-0"></p>';
    output += '<p class="sk_line sk_line_height_8 mb-0 sk_line_40"></p>';
    output += '</div>';
    output += '</div>';
    output += '</div>';
    output += '</div>';
    output += '<div class="col d-flex justify-content-end sk_right_btns">';
    output += '<div class="m_list_actions_wrap d-flex align-items-center flex-wrap justify-content-between flex-nowrap">';
    output += '<div class="d-flex m_bottom_actions align-items-center border-0 w-100">';
    output += '<div class="sk_btn sk_line w-25 rounded-1 my-0 mr-3"></div>';
    output += '<div class="sk_btn sk_line w-25 rounded-1 my-0"></div>';
    output += '</div>';
    output += '</div>';
    output += '</div>';
    output += '</div>';
    output += '</div>';
    output += '</div>';
    output += '</div>';
    output += '</div>';
    output += '</div>';
}
const ModerationSkeleton = () => {
    return <div className="row mt-30" id="moderationSkeleton">
        {parse(output)}
    </div>
}
export default ModerationSkeleton