import React, { Component } from 'react'
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal'
import PinToTopPosts from './elements/pinToTopPosts'
import './elements/pintotop.css'
import { connect } from 'react-redux';
import { GET_LIST_ALL_PIN_TO_TOP_POST } from '../../../../../../../reducer/keys';
import { GET_PIN_TO_TOP_LIST, GET_REORDER_PIN_TO_TOP } from '../../../../../../../actions/moderationActions';

 class index extends Component {
     state ={ newArray:[]}

     reArrangePosts =()=>{
      const { newArray} = this.state
      const postId = [];
      const indexing = [];
      for (let i = 0; i < newArray.length; i++) {
      postId.push(newArray[i].id);
      indexing.push(i + 1);
      }

      this.props.GET_REORDER_PIN_TO_TOP(postId, indexing); 
     }
     componentDidUpdate(prevProps) {
        if (prevProps.openpinToTopModal !== this.props.openpinToTopModal) {
            if(this.props.openpinToTopModal) this.props.GET_PIN_TO_TOP_LIST()
        }
    }

  render() {
    const { openpinToTopModal, closeModalPop, pinToTop} = this.props

    return (
        
        <Modal className='pintotop_modal' size="lg" dialogClassName="modal-dialog-centered" show={openpinToTopModal} onHide={closeModalPop} >
            <Modal.Header closeButton onClick={closeModalPop}>
                <Modal.Title>Change Order</Modal.Title>
            </Modal.Header>
            <Modal.Body className="p-0">
                 <PinToTopPosts pinToTop={pinToTop} setNewArray={(posts)=>this.setState({newArray:posts})}/>
            </Modal.Body>
            <Modal.Footer>
                <Button className='btn btn-primary' onClick={this.reArrangePosts}>Done</Button>
            </Modal.Footer>
        </Modal>
    )
  }
}


const mapStateToProps=state=>{
    return{
        pinToTop :state.moderation.pinToTop
    }
}


const mapDispatchToProps = dispatch => {
    return {

        GET_LIST_ALL_PIN_TO_TOP_POST: () => dispatch(GET_LIST_ALL_PIN_TO_TOP_POST()),
        GET_PIN_TO_TOP_LIST: () => dispatch(GET_PIN_TO_TOP_LIST()),
        GET_REORDER_PIN_TO_TOP: (postId, indexing) => dispatch(GET_REORDER_PIN_TO_TOP(postId, indexing))
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(index);
 
 