import React, { Component } from 'react'

import $ from 'jquery' 
import { DashboardModal, Dashboard } from '@uppy/react'  
import { Modal } from "react-bootstrap";
import { fetchPostData, DELETE_KEYS } from '../../../../../../actions/moderationActions' 
import UppyUpload from '../../../../../../utils/uppyUpload';


class EditPostModal extends Component {
  
    state = { filename: null, postauthor: null, posthandle: null, authorpicture: null, postcarousel: false, content: null, data: null, modalOpen: false, selected: "" }
    uppyUploader = new UppyUpload()
    
    componentDidMount() {
        const { postID, id } = this.props;
        fetchPostData(id, postID).then((response) => {
            DELETE_KEYS()
            const { data } = response.data;
            this.setState({ data, filename: data.postFile, postauthor: data.name, posthandle: data.username, content: data.content, authorpicture: data.picture, postcarousel: data.is_carausel })
        })
        this.uppyUploader.uppy.on('transloadit:result', (stepName, result) => {
            this.setState({ selectedFile: result.id, filename: `https://cloud.tagshop.ai/tmp/${result.id}.${result.ext}`, selected: `https://cloud.tagshop.ai/tmp/${result.id}.${result.ext}` }, () => {
                setTimeout(() => {
                    this.uppyUploader.uppy.cancelAll()
                    this.setState({ modalOpen: false })
                }, 300)
            })
        })
    }

    onChangePostAuthor = event => this.setState({ postauthor: event.target.value })
    onChangePostHandle = event => this.setState({ posthandle: event.target.value })
    onChangePicture = event => this.setState({ authorpicture: event.target.value })
    onChangeCarousel = event => this.setState({ postcarousel: event.target.checked })
    onChangeContent = event => this.setState({ content: event.target.value })
    onClickToOpen = () => {

        this.setState({ modalOpen: true })
        $(".uppy-Dashboard-close").click(() => {
            this.uppyUploader.uppy.cancelAll()
            this.setState({ modalOpen: false })
        })
    }
    onClickToClose = () => {
        this.uppyUploader.uppy.cancelAll()
        this.setState({ modalOpen: false })
    }
    componentWillUnmount() {
        this.uppyUploader.uppy.cancelAll()
        this.uppyUploader.uppy.close({ reason: 'unmount' })
        this.setState({ modalOpen: false })
    }

    onEditPost = event => {

        const { editpost, postID, id, clickToCloseEditPost, itemIndex } = this.props
        const { data, filename, postauthor, posthandle, authorpicture, postcarousel, content, selected } = this.state;
        const statuspostcarousel = postcarousel ? { postcarousel: 'on' } : {}
        let editData = { datapostFileN: filename, userPhoto: selected, content: content, newTable: 2, spostId: postID, wallId: localStorage.getItem('wallId'), postId: id, postauthor: postauthor, posthandle: posthandle, authorpicture: authorpicture, ...statuspostcarousel }
        editpost(itemIndex, editData, clickToCloseEditPost);
        this.setState({ modalOpen: false })
        this.uppyUploader.uppy.close();
    }
    doneButtonHandler = () => {
        this.uppyUploader.uppy.cancelAll()
        this.onClickToClose()
    }

    render() {
        const { open, clickToCloseEditPost, isEditing, postID, id } = this.props;
        const { data, filename, postauthor, posthandle, content, authorpicture, postcarousel, isLoading, modalOpen } = this.state;
        return data ? <>
            <Modal show={open} onHide={clickToCloseEditPost} backdrop="static" keyboard={false} centered size={"lg"}>
                <Modal.Header closeButton> <h4 className="modal-title">Edit Post</h4></Modal.Header>
                <Modal.Body>
                    <div className="add_pro_wrap">
                        <div className="row">
                            <div className="col-lg-5 post-image-section">
                                <div className="">
                                    <h5 className="text-center my-4">Post Image</h5>
                                    <div className="fileinput fileinput-new uppy_upload" data-provides="fileinput">
                                        <div className="fileinput-new thumbnail">
                                            <img className="img-fluid" src={filename} />
                                        </div>
                                        <div className="fileinput-preview fileinput-exists thumbnail">
                                            <div className="text-center" id="uppyModalOpener">
                                                <svg id="Group_4957" data-name="Group 4957" xmlns="http://www.w3.org/2000/svg" width="81.564" height="66.999" viewBox="0 0 81.564 66.999">
                                                    <path id="Path_5138" data-name="Path 5138" d="M249.771,382.533V320.066a2.268,2.268,0,0,1,2.266-2.266h77.033a2.268,2.268,0,0,1,2.266,2.266v62.468a2.268,2.268,0,0,1-2.266,2.266H252.036A2.268,2.268,0,0,1,249.771,382.533Z" transform="translate(-249.771 -317.8)" fill="#aec3ce"></path>
                                                    <path id="Path_5139" data-name="Path 5139" d="M360.247,415.382h39.327a.8.8,0,0,0,.147-.012l-18.6-32.218a1.3,1.3,0,0,0-2.267,0l-12.483,21.621-.6,1.035Z" transform="translate(-328.254 -363.757)" fill="#fff"></path>
                                                    <path id="Path_5140" data-name="Path 5140" d="M190.389,350.937h13.427l-6.591-9.574-.474-.69-.237-.345-.6,1.035Z" transform="translate(-158.396 -299.312)" fill="#aec3ce" opacity="0.2" style={{ isolation: 'isolate' }}></path>
                                                    <path id="Path_5141" data-name="Path 5141" d="M284.634,438.674h34.015l-6.591-9.574-.474-.69-8.589-12.477a1.738,1.738,0,0,0-2.58-.154,1.544,1.544,0,0,0-.124.154Z" transform="translate(-274.538 -387.05)" fill="#fff"></path>
                                                    <circle id="Ellipse_154" data-name="Ellipse 154" cx="5.826" cy="5.826" r="5.826" transform="translate(29.907 15.374)" fill="#fff"></circle>
                                                </svg>
                                            </div>
                                        </div>
                                        <div className="click_text" onClick={this.onClickToOpen}>
                                            <label className="addIcoSec text-center my-4 w-100 d-flex align-items-center justify-content-center flex-column" htmlFor="datapostFile">
                                                <div className="btn img_uploader_icon uppy-btn">
                                                    <p className="svg-icon svg-icon-lg m-0">
                                                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                                            <g id="Group_4424" data-name="Group 4424" transform="translate(10922 -4685)">
                                                                <rect id="Rectangle_2094" data-name="Rectangle 2094" width="24" height="24" transform="translate(-10922 4685)" fill="none" opacity="0"></rect>
                                                                <path id="Path_5219" data-name="Path 5219" d="M-15.382,10.935h-6.172V4.765A1.065,1.065,0,0,0-22.618,3.7a1.065,1.065,0,0,0-1.064,1.064v6.17h-6.174a1.058,1.058,0,0,0-.751.317,1.056,1.056,0,0,0-.305.756,1.068,1.068,0,0,0,1.058,1.055h6.172v6.169a1.059,1.059,0,0,0,.311.754,1.021,1.021,0,0,0,.755.313,1.055,1.055,0,0,0,.749-.309,1.059,1.059,0,0,0,.313-.756V13.063h6.175a1.066,1.066,0,0,0,1.057-1.072A1.069,1.069,0,0,0-15.382,10.935Z" transform="translate(-10887.383 4685)" fill="#545454"></path>
                                                            </g>
                                                        </svg>
                                                    </p>
                                                </div>
                                                <div className="file_uploader_card_txt">
                                                    <strong className="text-center text-muted">Click here</strong>
                                                    <span>to upload the new image</span>
                                                </div>
                                                <input type="hidden" autoComplete="off" name="datapostFileN" id="datapostFileN" className="" />
                                                <input data-validate="extension" autoComplete="off" data-extensions="jpg,png,gif" name="userPhoto" id="datapostFile" className="form-control validate" style={{ display: 'none' }} />
                                            </label>
                                        </div>
                                    </div>
                                    <i className="fa responseMessage" data-placement="left" id="responsedata>postFile"></i>
                                </div>
                            </div>
                            <div className="col-lg-7 with-image">
                                <div className="form-group">
                                    <label htmlFor="datacontent">Content<span className="required">*</span></label>
                                    <textarea name="content" rows="6" id="content" className="form-control required validate post-content" value={content} onChange={this.onChangeContent}></textarea>
                                    <span className="help-block"></span>
                                    <i className="fa responseMessage" data-placement="left" id="responsedatacontent"></i>
                                </div>
                                {/* <div className="form-group">
                                    <div className="checkbox-inline">
                                        <label className="checkbox checkbox-outline checkbox-outline-2x checkbox-primary mr-2">
                                            <input type="checkbox" name="postcarousel" id="postcarousel" checked={postcarousel} onChange={this.onChangeCarousel} /><span></span>Is Carousel
                                    </label>
                                    </div>
                                </div> */}
                                <div className="form-group">
                                    <label htmlFor="postauthor">Author Name<span className="required">*</span></label>
                                    <input type="text" className="form-control required" id="postauthor" value={postauthor} name="postauthor" onChange={this.onChangePostAuthor} />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="posthandle">Author Handle<span className="required">*</span></label>
                                    <input type="text" className="form-control required" id="posthandle" value={posthandle} name="posthandle" onChange={this.onChangePostHandle} />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="authorpicture">Author Profile<span className="required">*</span></label>
                                    <input type="text" className="form-control required" id="authorpicture" value={authorpicture} name="authorpicture" onChange={this.onChangePicture} />
                                </div>

                            </div>
                        </div>
                    </div>
                    {
                        modalOpen ? <DashboardModal animateOpenClose={true} proudlyDisplayPoweredByUppy={false} inline={false} showProgressDetails={true} height={750} browserBackButtonClose={true} showRemoveButtonAfterComplete={false} uppy={this.uppyUploader.uppy} open={true} onRequestCloseModal={this.onClickToClose} closeAfterFinish={true} doneButtonHandler={this.doneButtonHandler} plugins={[ "Instagram", "GoogleDrive", "Url", "Webcam" ]} /> : null
                    }
                </Modal.Body>
                <Modal.Footer>
                    <div className="d-flex align-items-center justify-content-end w-100">
                        <div className="card-toolbar">
                            <button type="button" className="btn btn-secondary mr-4" data-bs-dismiss="modal" onClick={clickToCloseEditPost}>Cancel</button>
                            <button type="submit" className={`btn btn-primary savePost ${isEditing ? 'disabled spinner spinner-white spinner-right' : null} `} onClick={this.onEditPost}>Save</button>
                        </div>
                    </div>
                </Modal.Footer>
            </Modal>
        </> : null
    }
}

export default EditPostModal

