import React, { Component, useEffect } from 'react'
import { Dropdown, Tab, Tabs } from "react-bootstrap";
import Swal from 'sweetalert2'
import { LoginSocialGoogle } from 'reactjs-social-login'
import GoogleTab from './googleTab';
import { CLOUDPATH, REDIRECT_URI } from '../../../../../../../actions/api';
import { useNavigate } from 'react-router-dom';
import { connect } from 'react-redux';
import { GG_APP_ID } from '../../../../../../../actions/constants';
import axios from 'axios';
import { GET_FLITER_ID, GET_LOCAL, TOAST_ERROR, TOAST_SUCCESS } from '../../../../../../../utils';
import { CREATE_FEED, FETCH_CONNECTED_ACCOUNTS } from '../../../../../../../actions/moderationActions';
import { getGoogleCode } from '../../../../../../../utils/auth';
import { SELECT_ACCOUNT, SET_GOOGLE_DATA } from '../../../../../../../actions/addFeedActions';
import { TAGGBOX_USER_HOME_DATA } from '../../../../../../../actions/sceneActions';
import withRouter from '../../../../../../routes/WithRouter';
import { addAccountFromId } from '../../../../../../../utils/addAccount';


const Googlefeed = (props) => {
    const navigation = useNavigate();
    useEffect(() => {console.log(props.connectedAcc)}, [props.connectedAcc])
    
    return <GFeed {...props} navigation={navigation} />;
}

class GFeed extends Component {
    constructor() {
        super();
        this.controller = new AbortController();
    }
    state = {
        feedText: null,
        placeName:null,
        placeId: null,
        filterId: 33,
        filterType: 'Places',
        moderation: false,
        networkId: null, 
        feedType: null,
        googleId: null,
        showError: false,
        showListError: false,
        connetedAccount: 0,
        authenticate:false,
        accountChild:0,
        authRequired:0,
        loading: false
    }
    onModerationChange = (value) => this.setState({ moderation: value })
    onFieldTextChange = (text, id, placeName) => this.setState({ feedText: text, placeId:id , placeName:placeName,  showError: false }) 
    onConnectedAccountChange = (value) => this.setState({ connetedAccount: value })
    onAccountChildChange = (value) => this.setState({ accountChild: value })
    onAuthenticateChange = (value) => this.setState({ authenticate: value })

    // handleMessage = (event) => {
    //     const { SET_GOOGLE_DATA, TAGGBOX_USER_HOME_DATA, navigate } = this.props
    //     console.log(event)
    //     const { error, message, addFeedData } = event.data;
    //     if (event.origin.includes("tagshop.ai") || event.origin.includes("localhost")) {
    //         if (!error && addFeedData) {
    //             TOAST_SUCCESS(message)
    //             SET_GOOGLE_DATA(addFeedData)
    //             TAGGBOX_USER_HOME_DATA()
    //             navigate(`/feeds/google/connect`)
    //         }
    //         else TOAST_ERROR(message)
    //     }
    // };
    handleMessage = async(event) => {
        const { FETCH_CONNECTED_ACCOUNTS } = this.props
        console.log(event)
        const networkId = GET_LOCAL('networkId')
        console.log('networkId', networkId)
        const { error, code } = event.data;
        if (event.origin.includes("tagshop.ai") || event.origin.includes("localhost")) {
            if (code) {
                await addAccountFromId(networkId, code, () => FETCH_CONNECTED_ACCOUNTS(4))
            }
        }
    };

    componentDidMount() {
        this.setState({ authRequired: this.props.feedInfo[0].authRequire })
        this.props.onFilterNameChange(this.state.filterType)
        const signal = this.controller.signal;
        window.addEventListener("message", this.handleMessage, { signal })
    }

    componentWillUnmount() {
        this.controller.abort()
     }

    clearField = (e) => this.setState({ feedText: "", moderation: 0,  showError: false }, () => this.props.onFilterNameChange(e))

    onSubmit = () => {
        localStorage.setItem("addAcc", false)

        const { authRequired, connetedAccount , feedText, accountChild} = this.state
        if(!feedText)this.setState({showError: true})
        // authRequired ? (!connetedAccount || connetedAccount == null ? console.log('this.onAddAccount()',connetedAccount) : console.log('this.onAddFeed()', connetedAccount)) : console.log('this.onAddFeed()', connetedAccount)
        authRequired ? (!connetedAccount || connetedAccount == null ? this.onAddAccount() : this.onAddFeed()) : this.onAddFeed()
    }


    onAddAccount = () => {
        const {  moderation, filterId, authRequired } = this.state
        this.setState({ loading: true })
        if (filterId == 33) {
            this.onAddFeed()
        } else {
            localStorage.setItem("authRequired", authRequired)

            var objj ={
                wallId: localStorage.getItem('wallId'),
                networkId: 4,
                filter: filterId,
                field33: "",
                field33placeId: "",
                field33placeName:"", 
                connetedAccount: "", 
                feedType: 2, 
                account: 0,
                hashtagIGTV:0,
                onlyReels:0,
                moderation: Number(moderation)   
            } 

            localStorage.setItem("createFeedObj", JSON.stringify(objj))
            localStorage.setItem("networkId", 4)

            Swal.fire({
                imageUrl: 'https://cloud.taggbox.com/taggshop/app/assets/media/square-icons/google.svg',
                title: '<strong>Google</strong>',
                html: 'Connect to google to pull the content for this feed.',
                showCloseButton: true,
                showCancelButton: false,
                focusConfirm: true,
                confirmButtonText: 'Continue with Google',
                confirmButtonAriaLabel: 'Continue with Google',
                cancelButtonText: '<i class="fa fa-thumbs-down"></i>',
                cancelButtonAriaLabel: 'Thumbs down'
            }).then((result) => {
                if (result.value) {
                    getGoogleCode()
                }
                else if (result.dismiss === "cancel") { }
                this.setState({ loading: false })
            });
        }
    }
 

    onAddFeed = () => {
        const { moderation, placeName, feedText, placeId ,filterId, authRequired, userId, connetedAccount, accountChild, showError } = this.state
        const { navigation, hideAddfeed } = this.props
        this.setState({ loading: true })

        var obj
        if (filterId == 29) {
            obj ={
                wallId: localStorage.getItem('wallId'),
                networkId: 4,
                filter: filterId,
                field33: "",
                field33placeId: "",
                field33placeName:"", 
                connetedAccount: connetedAccount, 
                feedType: 2, 
                account: accountChild,
                hashtagIGTV:0,
                onlyReels:0,
                moderation: Number(moderation)   
            }  
                CREATE_FEED(obj, navigation, true, hideAddfeed)

        }
        else {
            obj ={
                wallId: localStorage.getItem('wallId'),
                networkId: 4,
                filter: filterId,
                field33: feedText,
                field33placeId: placeId,
                field33placeName:placeName, 
                connetedAccount: 0, 
                feedType: 2, 
                account: 0,
                hashtagIGTV:0,
                onlyReels:0,
                moderation: Number(moderation)   
            }
             feedText ? CREATE_FEED(obj, navigation, true, hideAddfeed) : this.setState({showError: true})
           
        }

    }


    // componentDidUpdate(prevProps) {
    //     if (prevProps.connectedAcc !== this.props.connectedAcc) {
    //         console.log("updated")
    //         this.setState({loading: false})
    //     }
    // }

    getChildAccount = () => {
        const { connectedAcc } = this.props
        return connectedAcc && connectedAcc[0] && connectedAcc[0].ConnectedAccounts && connectedAcc[0].ConnectedAccounts.child && connectedAcc[0].ConnectedAccounts.child.length ? connectedAcc[0].ConnectedAccounts.child : ''
    }

    render() {
        const { feedInfo, navigation, ACTION_ADD_AUTH_OBJ ,connectedAcc , authenticate, SELECT_ACCOUNT} = this.props
        const { feedText, moderation, placeId , accountChild, connetedAccount, showError, loading} = this.state 
        // var  childAcc = connectedAcc && connectedAcc[0] && connectedAcc[0].ConnectedAccounts && connectedAcc[0].ConnectedAccounts.child && connectedAcc[0].ConnectedAccounts.child.length ? connectedAcc[0].ConnectedAccounts.child : ''

        return (
            <div className="form-body create_feed_wt">
                <div className="tabbable-tabdrop newRadio d-flex align-items-center justify-content-between flex-wrap feeds_details createFeedInsta personalizePopup">
                    <Tabs defaultActiveKey="Places" id="GoogleFeedTab" className="nav-tabs-line nav-tabs-bold w-100 tabDrop mb-5"  onSelect={e => this.setState({ filterType: e }, this.setState({ filterId: GET_FLITER_ID(e, feedInfo) },() => this.clearField(e)))} >
                        {feedInfo && feedInfo.length && feedInfo[0].networkTab && feedInfo[0].networkTab.length ? feedInfo[0].networkTab.map(filter =>
                            <Tab eventKey={filter.feedtype} key={filter.filter} value="1" className="nav-link className= p-0" title={<span>{filter.feedName}</span>}>
                                <GoogleTab 
                                tab={filter.feedtype} 
                                onAddAccount={this.onAddAccount} 
                                connectedAcc={connectedAcc} 
                                onModerationChange={this.onModerationChange} 
                                moderation={moderation} 
                                onFieldTextChange={this.onFieldTextChange} 
                                feedText={feedText} 
                                showError={showError}
                                placeId={placeId} 
                                onAuthenticateChange={this.onAuthenticateChange}
                                onAccountChildChange={this.onAccountChildChange}
                                onConnectedAccountChange={this.onConnectedAccountChange}
                                connetedAccount={connetedAccount}
                                authenticate={authenticate}
                                getChildAccount={this.getChildAccount}
                                SELECT_ACCOUNT={SELECT_ACCOUNT}
                                />
                            </Tab>
                        ) : null}
                    </Tabs>
                </div>
                <div className="form-actions">
                    <div className="d-flex justify-content-between align-items-center flex-wrap border-top pt-lg-8 mt-lg-10 pt-5 mt-6">
                        <div className="need_help__">
                            <p className="d-flex align-items-center flex-wrap mb-sm-0 mb-6">
                                Need help in adding feeds?
                                <a href="https://vimeo.com/912907290?share=copy" className="video_btn btn btn-text p-0 ml-3 text-dark-75 btn-clean video_support_link" data-lity>
                                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                        <g id="Group_6606" data-name="Group 6606" transform="translate(-1 -1)">
                                            <path id="Path_6626" data-name="Path 6626" d="M9.5,16.281V8.461A.545.545,0,0,1,10.34,8l6.083,3.909a.545.545,0,0,1,0,.919L10.34,16.74a.545.545,0,0,1-.84-.458Z" transform="translate(0.773 0.629)" fill="#A5A5A5"></path>
                                            <path id="Path_6627" data-name="Path 6627" d="M13,2.636A10.364,10.364,0,1,0,23.364,13,10.364,10.364,0,0,0,13,2.636ZM1,13A12,12,0,1,1,13,25,12,12,0,0,1,1,13Z" fill="#A5A5A5" fillRule="evenodd"></path>
                                        </g>
                                    </svg>
                                    <span className="ms-1">Watch Video</span>
                                </a>
                            </p>
                        </div>
                        <div className="hideforBSearch">
                            <button type="button" className="btn btn-secondary" onClick={this.props.onBack}>Back</button>
                            <span className="create_feed_hideshow">
                                <button type="button" className={`commanBtn btn btn-primary ml-4 ${loading ? 'spinner spinner-white disabled spinner-right' : ""} `} id="create_feed"  onClick={this.onSubmit}>
                                    Create Feed
                                </button>
                            </span>
                        </div>
                    </div>
                </div>



            </div>
        )
    }
}


const mapStateToProps = (state) => {
    return {
        connectedAcc: state.addFeedReducer.connectedAcc
    }
}

const mapDispatchToProps = dispatch => {
    return {
        SELECT_ACCOUNT: (obj) => dispatch(SELECT_ACCOUNT(obj)),
        SET_GOOGLE_DATA: (obj) => dispatch(SET_GOOGLE_DATA(obj)),
        TAGGBOX_USER_HOME_DATA: () => dispatch(TAGGBOX_USER_HOME_DATA()),
        FETCH_CONNECTED_ACCOUNTS: (id) => dispatch(FETCH_CONNECTED_ACCOUNTS(id))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Googlefeed)
