import React, { Component } from 'react'
import parse from 'html-react-parser';
import Iframe from 'react-iframe';
import ReactPlayer from "react-player"  
import { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO } from '../../../../../../../utils';
class Media extends Component {
state = { isVideo: false }

    onClickToPlay = event => { this.setState({ isVideo: true }) }

    render() {
        const { item } = this.props;
        const { isVideo } = this.state;
        const videoUrl = item.mediaUrl; 
        return  (item.type === 3 || item.type === 5) &&
                        (item.link.indexOf("youtube") >= 0 ||
                            item.link.indexOf("youtu.be") >= 0 ||
                            item.link.indexOf("tumblr") >= 0 ? (
                               <Iframe
                                            className="ts-detail-image-iframe"
                                            frameBorder="0"
                                            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                                            src={`${videoUrl}`}
                                            allowFullScreen="true"
                                            style={{ width: "100%!important" }}
                                            onError={(e) => { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO(e) }}
                                            data-type="video"
                                            data-stories={item.stories}
                                            data-filter-id={item.filterId}
                                            data-network={item.networkId}
                                            data-link={item.link}
                                            //data-wall-id={wall.Wall.id} 
                                            data-item-id={item.id}
                                            data-load={0}
                                        ></Iframe> 
                        ) : ((item.link.indexOf("vk") >= 0) && item.networkId == 30 || item.networkId == 29 || item.networkId == 6 || item.networkId == 18 )) ? (
                        parse(item.mediaFile)
                    ) : (item.link.indexOf("facebook") >= 0) ?  
                        <ReactPlayer
                            style={{ display: 'contents' }}
                            className="ts-detail-image-iframe"
                            url={videoUrl}
                            data-type="video"
                            data-stories={item.stories}
                            data-filter-id={item.filterId}
                            data-network={item.networkId}
                            data-link={item.link} 
                            data-item-id={item.id}
                            data-load={0}
                            config={{
                                facebook: {
                                    appId: '117029975354072'
                                }
                                }}
                            onError={(e) => { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO(e) }}
                            autoPlay
                            controls
                        />
                            : item.networkId == 1 || item.networkId == 2 || item.networkId == 18 || item.networkId == 8 ?
                                <Iframe
                                    className="ts-detail-image-iframe"
                                    frameBorder="0"
                                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                                    src={`${videoUrl}`}
                                    allowFullScreen="true"
                                    style={{ width: "100%!important" }}
                                    onError={(e) => { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO(e) }}
                                    data-type="video"
                                    data-stories={item.stories}
                                    data-filter-id={item.filterId}
                                    data-network={item.networkId}
                                    data-link={item.link} 
                                    data-item-id={item.id}
                                    data-load={0}
                                ></Iframe>
                                :
                                (<video
                                                src={videoUrl}
                                                autoPlay
                                                controls
                                                data-type="video"
                                                data-stories={item.stories}
                                                data-filter-id={item.filterId}
                                                data-network={item.networkId}
                                                data-link={item.link}
                                                onError={(e) => { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO(e) }}
                                                data-load={0}
                                                poster={item.postFileNew}
                                                data-wall-id={localStorage.getItem('wallId')} 
                                                data-item-id={item.id}
                                ></video>
                                        
                                )
                }
            
}

export default Media;