import { Component } from "react";
import { Tab, Tabs } from "react-bootstrap";
import AlertText from "./alertText";
import TwTabs from "./twTabs";
import { CREATE_FEED } from "../../../../../../../actions/moderationActions";
import Swal from "sweetalert2";
import { getTwitterCode } from "../../../../../../../utils/auth"
import { redirect, useNavigate } from 'react-router-dom'
import { SET_CREATE_FEED_OBJ } from "../../../../../../../actions/addFeedActions";
import { connect } from "react-redux";
import { GET_VIDEO_URLS } from "../constant";

const TwitterFeed = (props) => {
    const navigation = useNavigate();

    return <TFeed {...props} navigation={navigation} />;
}

class TFeed extends Component {
    state = {
        feedText: null,
        filter: null,
        filterType: 'hashtag',
        moderation: false,
        networkId: null,
        excludeRetweet: true,
        excludeReplies: true,
        feedType: null,
        multiplePhoto: true,
        listname: null,
        showError: false,
        showListError: false,
        connetedAccount: null,
        userId: null,
        listId: null,
        authenticate: false,
        authObj: {},
        authRequired: null,
        isLoading:false
    }

    getFeedTab = (filterId) => {
        switch (filterId) {
            case 2:

                break;

            default:
                break;
        }
    }

    componentDidMount() {
        this.setState({ authRequired: this.props.feedInfo[0].authRequire })
        this.props.onFilterNameChange(this.state.filterType)
    }

    onModerationChange = (value) => this.setState({ moderation: value })
    onExcRetweetChange = (value) => this.setState({ excludeRetweet: value })
    onExrepliesChange = (value) => this.setState({ excludeReplies: value })
    onFieldTextChange = (value) => this.setState({ feedText: value, showError: false })
    onMultiplePhotoChange = (value) => this.setState({ multiplePhoto: value })
    onListNameChange = (value) => this.setState({ listname: value, showListError: false })
    onConnectedAccountChange = (value) => this.setState({ connetedAccount: value })
    onUserIdChange = (value) => this.setState({ userId: value })
    onListIdChange = (value) => this.setState({ listId: value })
    onAuthenticateChange = (value) => this.setState({ authenticate: value })

    onAddAccount = () => {
        const { moderation, userId, authObj, excludeRetweet, listId, excludeReplies, feedText, multiplePhoto, filterType, connetedAccount, showError, listname, authRequired, } = this.state
        const { navigation, feedInfo, SET_CREATE_FEED_OBJ, accountId } = this.props
        localStorage.setItem("authRequired", authRequired)
        localStorage.setItem("addAcc", false)
        this.setState({isLoading: true})

        const ftr = feedInfo && feedInfo.length && feedInfo[0].networkTab && feedInfo[0].networkTab.length ? feedInfo[0].networkTab.findIndex(f => f.feedtype == filterType) : null

        if (feedText == null) {
            this.setState({ showError: true })
        }
        if (listname == null) {
            this.setState({ showListError: true })
        }
        if (feedText == null) return
        var accId = connetedAccount

        var objj = {
            wallId: localStorage.getItem('wallId'),
            networkId: 1,
            username: "",
            moderation: moderation ? 1 : 0,
            excludeRetweet: excludeRetweet ? 1 : 0,
            excludeReplies: excludeReplies ? 1 : 0,
            feedText,
            authRequire: feedInfo[0].authRequire,
            connetedAccount: 0,
            feedType: filterType,
            accountId: accId,
            // networkToken: authObj.access_token || "",
            // validity: authObj.expires_in || "",
            listname,
            multiplePhoto: multiplePhoto ? 1 : 0,
            filterType,
            filter: feedInfo[0]?.networkTab[ftr]?.filter
        }
        if ([1, 7, 4, 3].includes(feedInfo[0]?.networkTab[ftr]?.filter)) objj = { ...objj, userId }
        if (feedInfo[0]?.networkTab[ftr]?.filter == 3) objj = { ...objj, listId }
        localStorage.setItem("createFeedObj", JSON.stringify(objj))
        localStorage.setItem("networkId", 1)

        Swal.fire({
            // title: "Twitter",
            // text:'Connect to Twitter to pull the content for this feed.',
            imageUrl: 'https://cloud.taggbox.com/taggshop/app/assets/media/square-icons/twitter.svg?ver=1',
            title: '<strong>Twitter</strong>',
            //   icon: 'info',
            html:
                'Connect to Twitter to pull the content for this feed.',
            showCloseButton: true,
            showCancelButton: false,
            focusConfirm: true,
            confirmButtonText:
                'Continue with Twitter',
            confirmButtonAriaLabel: 'Continue with Twitter',
            cancelButtonText:
                '<i class="fa fa-thumbs-down"></i>',
            cancelButtonAriaLabel: 'Thumbs down'
        }).then((result) => {

            if (result.value) {
                // SET_CREATE_FEED_OBJ(objj)
                getTwitterCode()
                // navigation('/taggshop/authorization/1')

            }
            else if (result.dismiss === "cancel") { 
                this.setState({isLoading: false})
                
            }
            else if (result.dismiss === "close" || result.dismiss === "backdrop") { 
                this.setState({isLoading: false})
                
            }
        });
    }

    createFeed = () => {
        const { feedText, listname, authRequired, filterType } = this.state
        const { navigation, SET_CREATE_FEED_OBJ, hideAddfeed } = this.props
        localStorage.setItem("authRequired", authRequired)
        localStorage.setItem("addAcc", false)
        if (feedText == null) {
            this.setState({ showError: true })
        }
        if (listname == null) {
            this.setState({ showListError: true })
        }
        if (feedText == null ) return
        if (listname == null && filterType == "list" ) return
        this.setState({isLoading: true})

        const { moderation, userId, authObj, excludeRetweet, listId, excludeReplies, multiplePhoto, connetedAccount, showError } = this.state
        const { feedInfo } = this.props
        // console.log(feedInfo)
        const ftr = feedInfo && feedInfo.length && feedInfo[0].networkTab && feedInfo[0].networkTab.length ? feedInfo[0].networkTab.findIndex(f => f.feedtype == filterType) : null
        // if (feedText == "") {
        //     this.setState({ showError: true })
        // }
        // if (listname == "") {
        //     this.setState({ showListError: true })
        // }
        // if (feedText == "" || listname == "") return

        var accId = connetedAccount
        // var authReq = feedInfo[0].authRequire

        // if (authReq == 1 || !connetedAccount) {
        //     accId = authObj
        // }



        var obj = {
            wallId: localStorage.getItem('wallId'),
            networkId: 1,
            username: "",
            moderation: moderation ? 1 : 0,
            excludeRetweet: excludeRetweet ? 1 : 0,
            excludeReplies: excludeReplies ? 1 : 0,
            feedText,
            authRequire: feedInfo[0].authRequire,
            connetedAccount: 0,
            feedType: filterType,
            accountId: accId,
            // networkToken: authObj.access_token || "",
            // validity: authObj.expires_in || "",
            listname,
            multiplePhoto: multiplePhoto ? 1 : 0,
            filterType,
            filter: feedInfo[0]?.networkTab[ftr]?.filter
        }
        if ([1, 7, 4, 3].includes(feedInfo[0]?.networkTab[ftr]?.filter)) obj = { ...obj, userId }
        if (feedInfo[0]?.networkTab[ftr]?.filter == 3) obj = { ...obj, listId }
        SET_CREATE_FEED_OBJ(obj)
        localStorage.setItem("createFeedObj", JSON.stringify(obj))
        localStorage.setItem("networkId", 1)

        // navigation('/walls/authcallback')
        CREATE_FEED(obj, navigation, true, hideAddfeed)


        // console.log(obj, this.state)
        // CREATE_FEED(obj)
    }

    render() {
        const { feedInfo, networkId, accountId, onBack } = this.props
        const { moderation, excludeRetweet, authRequired, isLoading, userId, authenticate, listId, connetedAccount, excludeReplies, feedText, multiplePhoto, listname, filterType, showError, showListError } = this.state
        return (
            <>
                {feedInfo && feedInfo.length && feedInfo[0].networkTab && feedInfo[0].networkTab.length ? <div className="row">
                    <div className="col-md-12">
                        <div className="form-body create_feed_wt">
                            <div className="tabbable-tabdrop newRadio d-flex align-items-center justify-content-between flex-wrap feeds_details personalizePopup">
                                <Tabs defaultActiveKey="hashtag" value={filterType} id="CreateFeedTab" className="nav-tabs-line nav-tabs-bold w-100 tabDrop mb-5" onSelect={e => this.setState({ filterType: e, feedText: null, moderation: false, excludeRetweet: true, excludeReplies: true }, () => this.props.onFilterNameChange(e))}>
                                    {feedInfo && feedInfo.length && feedInfo[0].networkTab && feedInfo[0].networkTab.length ? feedInfo[0].networkTab.map(filter => (
                                        <Tab eventKey={filter.feedtype} key={filter.filter} className="nav-link className= p-0" title={<span>{filter.feedName}</span>}>
                                            {/* {filter.filter != 3 ? */}
                                            <TwTabs
                                                networkId={networkId}
                                                tab={filter.feedtype}
                                                filter={filter.filter}
                                                msg={`${filter.filter == 4 ? 'the twitter handle of the user to import Liked Posts from.' : filter.feedtype}`}
                                                moderation={moderation}
                                                excludeRetweet={excludeRetweet}
                                                listname={listname}
                                                excludeReplies={excludeReplies}
                                                feedText={feedText}
                                                showError={showError}
                                                showListError={showListError}
                                                multiplePhoto={multiplePhoto}
                                                userId={userId}
                                                connetedAccount={connetedAccount}
                                                listId={listId}
                                                authenticate={authenticate}
                                                onAuthenticateChange={this.onAuthenticateChange}
                                                onListIdChange={this.onListIdChange}
                                                onUserIdChange={this.onUserIdChange}
                                                onConnectedAccountChange={this.onConnectedAccountChange}
                                                onModerationChange={this.onModerationChange}
                                                onListNameChange={this.onListNameChange}
                                                onExcRetweetChange={this.onExcRetweetChange}
                                                onExrepliesChange={this.onExrepliesChange}
                                                onFieldTextChange={this.onFieldTextChange}
                                                onMultiplePhotoChange={this.onMultiplePhotoChange}
                                            />
                                            {/* : <ListTab
                                                                        listname={listname}
                                                                        moderation={moderation}
                                                                        excludeRetweet={excludeRetweet}
                                                                        multiplePhoto={multiplePhoto}
                                                                        onListNameChange={this.onListNameChange}
                                                                        onModerationChange={this.onModerationChange}
                                                                        onExcRetweetChange={this.onExcRetweetChange}
                                                                        onMultiplePhotoChange={this.onMultiplePhotoChange}

                                                                    />} */}

                                            <AlertText filterId={filter.filter} />
                                        </Tab>
                                    )) : null}
                                </Tabs>
                                <div className="form-actions w-100">
                                    <div className="d-flex justify-content-between align-items-center flex-wrap border-top pt-lg-8 mt-lg-10 pt-5 mt-6">
                                        <div className="need_help__">
                                            <p className="d-flex align-items-center flex-wrap mb-sm-0 mb-6">
                                                Need help in adding feeds?
                                                <a href='https://vimeo.com/912907290?share=copy' className="video_btn btn btn-text p-0 ml-3 text-dark-75 btn-clean video_support_link" data-lity>
                                                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                                        <g id="Group_6606" data-name="Group 6606" transform="translate(-1 -1)">
                                                            <path id="Path_6626" data-name="Path 6626" d="M9.5,16.281V8.461A.545.545,0,0,1,10.34,8l6.083,3.909a.545.545,0,0,1,0,.919L10.34,16.74a.545.545,0,0,1-.84-.458Z" transform="translate(0.773 0.629)" fill="#A5A5A5"></path>
                                                            <path id="Path_6627" data-name="Path 6627" d="M13,2.636A10.364,10.364,0,1,0,23.364,13,10.364,10.364,0,0,0,13,2.636ZM1,13A12,12,0,1,1,13,25,12,12,0,0,1,1,13Z" fill="#A5A5A5" fillRule="evenodd"></path>
                                                        </g>
                                                    </svg>
                                                    <span> Watch Video</span>
                                                </a>
                                            </p>
                                        </div>
                                        <div className="hideforBSearch">
                                            <button type="button" className="btn btn-secondary" onClick={onBack}>Back</button>
                                            <span className="create_feed_hideshow">
                                                <button type="button" className={`commanBtn btn btn-primary ml-4 ${isLoading ? 'spinner disabled spinner-white spinner-right' : ""}`} id="create_feed" onClick={
                                                    authRequired ?
                                                        accountId == null || accountId == 0 ?
                                                            this.onAddAccount : this.createFeed
                                                        : accountId == 0 ? this.onAddAccount : this.createFeed
                                                }>Create Feed</button>
                                            </span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
                    :
                    null
                }
            </>
        )
    }
}



const mapStateToProps = (state) => {
    return {
        accountId: state.addFeedReducer.selectedAccount

    }
}
const mapDispatchToProps = dispatch => {
    return {
        SET_CREATE_FEED_OBJ: (obj) => dispatch(SET_CREATE_FEED_OBJ(obj))
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(TwitterFeed)