import React, { Component } from 'react'
import SubHeaderTop from './subHeaderTop'
import SubHeaderBottom from './subHeaderBottom'
import { IS_LIVE } from '../../../../../../actions/api';
class SubHeader extends Component {
    render() {
        return <div className={`subheader subheader_main fixed_subheader subheader-transparent dual_subheader modration-header-fix`} id="kt_subheader">
            <SubHeaderTop {...this.props} />
            <SubHeaderBottom {...this.props} />
        </div>
    }
}

export default SubHeader;