import React, { useState } from "react";
import PlacesAutocomplete, {
  geocodeByAddress,
  getLatLng,
} from "react-places-autocomplete";

export default function App(props) {
  const [address, setAddress] = useState("");
  const [list, setList] = useState(false);


  const handleChange = (e) => { setAddress(e); setList(true); props.onFieldTextChange("", "") };
  // const handleSelect = (e) => { console.log("handleSelect..", e) };
  const handleSelect = address => {
    geocodeByAddress(address)
      .then(results => getLatLng(results[0]))
      .then(latLng => {
        props.onLatlanChange(`${latLng.lat},${latLng.lng}`)
      })
      .catch(error => console.error('Error', error));
  };
  const handleSelect1 = (v1, v2) => {
    props.onFieldTextChange(v1, v2)
    setAddress(v1);
    setList(false)
  };



  return (
    <div className="input-group">
      <div className="input-group-prepend">
        <span className="input-group-text">
          <i className="fas fa-globe"></i>
        </span>
      </div>
      <PlacesAutocomplete
        value={address}
        onChange={handleChange}
        onSelect={handleSelect}
      >
        {({ getInputProps, suggestions, getSuggestionItemProps, loading }) => (

          <>
            <input {...getInputProps({ placeholder: "Type address" })} className="form-control brdrLftNoRafius" />
            {list && address.length > 0 ?
              <div id="suggesstion-box">
                <ul id="google-location-list" className="google_location_list">
                  {/* {loading ? <div>...loading</div> : null} */}
                  {suggestions.map(suggestion => {
                    const style = {
                      backgroundColor: suggestion.active ? "" : ""
                    };
                    return (<li {...getSuggestionItemProps(suggestion, { style })} onClick={() => { handleSelect1(suggestion.description, suggestion.placeId); handleSelect(suggestion.description) }}> {suggestion.description}</li>);
                  })}
                </ul>
              </div>
              : ''}


          </>
        )}
      </PlacesAutocomplete>

    </div>
  );
}





