import CustomToolTip from '../../../../../containers/tooltip'
import { clickToPostPin, clickToPostHighlight, clickToGetUgc, clickToEditPost, clickToDeletePost, clickToEditCta, clickToRefreshPost, clickToBlockUser, clickToPlay, clickToPostStatus, clickToGetLink, getManualTagPopup, onClickToGetMedia, onUpdateTags, checkedPostdata, refreshPostByNetwork, GET_UGC_POST_DETAIL, ugcModal, GET_UGC_REQUEST, DELETE_KEYS, UPDATE_INSTA_POST_ACTION } from '../../../../../../../actions/moderationActions'
import CtaModal from '../../modal/cta'
