                                                                        <svg xmlns="http://www.w3.org/2000/svg" width="15" height="15" viewBox="0 0 40 40" className="me-1"> <g id="Group_5456" data-name="Group 5456" transform="translate(9565 7108)"> <rect id="Rectangle_2321" data-name="Rectangle 2321" width="40" height="40" transform="translate(-9565 -7108)" fill="none"></rect> <g id="Group_5455" data-name="Group 5455" transform="translate(-9560.712 -7106.113)"> <g id="Group_4836" data-name="Group 4836" transform="translate(0 0)"> <path id="Path_5298" data-name="Path 5298" d="M-113.5-51.344l-13.667-5.667a1.815,1.815,0,0,0-1.413,0l-13.667,5.667a1.833,1.833,0,0,0-1.137,1.7v6.9A23.708,23.708,0,0,0-128.575-20.7a1.853,1.853,0,0,0,.7.137,1.834,1.834,0,0,0,.7-.137,23.708,23.708,0,0,0,14.815-22.043v-6.9a1.833,1.833,0,0,0-1.135-1.7Z" transform="translate(143.387 57.154)" fill="#fff"></path> <path id="Path_5630" data-name="Path 5630" d="M15.943,5.655a.814.814,0,0,0-1.152,0l-4,3.988-4-4A.815.815,0,0,0,5.647,6.8l4,4-4,4A.815.815,0,0,0,6.8,15.943l4-4,4,4a.815.815,0,0,0,1.152-1.152l-4-4,4-4A.819.819,0,0,0,15.943,5.655Z" transform="translate(4.803 7.464)" fill="#d7491f"></path> </g> </g> </g> </svg>
