import React, { Component } from 'react'
import { connect } from "react-redux";
import { removeFilterSocialFeeds, removeFilterPostType, removeFilterOthers, removeFilterPostTags, removeDateRanges } from '../../../../../../../actions/moderationActions'
class FilterTab extends Component {
    onSelectFeeds = (data) => event => { this.props.removeFilterSocialFeeds(data) }
    onClickPostType = (value) => event => { this.props.removeFilterPostType(value) }
    onClickOther = (data) => event => { this.props.removeFilterOthers(data) }
    onClickRemovePostTags = (value) => event => { this.props.removeFilterPostTags(value) }
    onClickToDateRangeRemove = event => { this.props.removeDateRanges() }
    render() {
        const { filters, onClickReset, filterBody } = this.props
        return (filterBody.socialFeed.length > 0 || filterBody.postType.length > 0 || filterBody.postTags.length > 0 || filterBody.other.length > 0 || Object.keys(filterBody.dateRanges).length > 0) ? <div className="filters_tabs d-flex flex-column-fluid">
            <div className="filterStrip mb-2" id="moderationfilterStrip">
                <div id="pivot-bar" className="d-flex flex-wrap align-items-center filter_tags pivot-bar mod">
                    <span className="text-dark-75 font-weight-bold mr-2 mb-2 filter-barText">Filter By : </span>
                    <ul className="list-inline d-flex flex-wrap align-items-center m-0 pivots" id="parentFIlterUl">
                        {
                            (Object.keys(filterBody.dateRanges).length > 0) ? <><li className="label label-xl label-pill label-inline mr-2 mb-2 text-nowrap pivot remove-filter">
                                {`${filterBody.dateRanges.startDate} - ${filterBody.dateRanges.endDate}`}
                                <button className="btn p-0 ml-2 remove-pivot" type="button" onClick={this.onClickToDateRangeRemove}><i className="ki ki-close icon-xs"></i></button>
                            </li></> : null
                        }
                        {
                            filterBody.postTags && filterBody.postTags.map((sItem) => {
                                return <li className="label label-xl label-pill label-inline mr-2 mb-2 text-nowrap pivot remove-filter">
                                    {sItem}
                                    <button className="btn p-0 ml-2 remove-pivot" type="button" onClick={this.onClickRemovePostTags(sItem)}><i className="ki ki-close icon-xs"></i></button>
                                </li>
                            })
                        }
                        {
                            filterBody.socialFeed && filterBody.socialFeed.map((sItem) => {
                                return <li className="label label-xl label-pill label-inline mr-2 mb-2 text-nowrap pivot remove-filter">
                                    {sItem.key == "onsiteupload" ? "SnapUp" : sItem.key}
                                    <button className="btn p-0 ml-2 remove-pivot" type="button" onClick={this.onSelectFeeds(sItem)}><i className="ki ki-close icon-xs"></i></button>
                                </li>
                            })
                        }

                        {
                            filterBody.postType && filterBody.postType.map((sItem) => {
                                if (sItem == "1") {
                                    return <li className="label label-xl label-pill label-inline mr-2 mb-2 text-nowrap pivot remove-filter">
                                        Text Only
                                        <button className="btn p-0 ml-2 remove-pivot" type="button" onClick={this.onClickPostType(sItem)}><i className="ki ki-close icon-xs"></i></button>
                                    </li>
                                }
                                else if (sItem == "2,4") {
                                    return <li className="label label-xl label-pill label-inline mr-2 mb-2 text-nowrap pivot remove-filter">
                                        With Image
                                        <button className="btn p-0 ml-2 remove-pivot" type="button" onClick={this.onClickPostType(sItem)}><i className="ki ki-close icon-xs"></i></button>
                                    </li>
                                }
                                else if (sItem == "3,5") {
                                    return <li className="label label-xl label-pill label-inline mr-2 mb-2 text-nowrap pivot remove-filter">
                                        With Video
                                        <button className="btn p-0 ml-2 remove-pivot" type="button" onClick={this.onClickPostType(sItem)}><i className="ki ki-close icon-xs"></i></button>
                                    </li>
                                }
                            })
                        }

                        {
                            filterBody.other && filterBody.other.map((sItem) => {
                                if (sItem.key == "highlightFilter") {
                                    return <li className="label label-xl label-pill label-inline mr-2 mb-2 text-nowrap pivot remove-filter">
                                        Highlight Post
                                        <button className="btn p-0 ml-2 remove-pivot" type="button" onClick={this.onClickOther(sItem)}><i className="ki ki-close icon-xs"></i></button>
                                    </li>
                                }
                                else if (sItem.key == "pinFilter") {
                                    return <li className="label label-xl label-pill label-inline mr-2 mb-2 text-nowrap pivot remove-filter">
                                        Pin Post
                                        <button className="btn p-0 ml-2 remove-pivot" type="button" onClick={this.onClickOther(sItem)}><i className="ki ki-close icon-xs"></i></button>
                                    </li>
                                }
                                else if (sItem.key == "recentFilter") {
                                    return <li className="label label-xl label-pill label-inline mr-2 mb-2 text-nowrap pivot remove-filter">
                                        Recent Post
                                        <button className="btn p-0 ml-2 remove-pivot" type="button" onClick={this.onClickOther(sItem)}><i className="ki ki-close icon-xs"></i></button>
                                    </li>
                                }
                                else if (sItem.key == "retweetsFilter") {
                                    return <li className="label label-xl label-pill label-inline mr-2 mb-2 text-nowrap pivot remove-filter">
                                        Retweets Post
                                        <button className="btn p-0 ml-2 remove-pivot" type="button" onClick={this.onClickOther(sItem)}><i className="ki ki-close icon-xs"></i></button>
                                    </li>
                                }
                            })
                        }
                        <li className="btn btn-pill mb-2 reset_btn pivot-reset resetAction" onClick={this.props.removeAllFilterBody}>Reset</li>
                    </ul>
                </div>
            </div>
        </div> : null
    }
}

const mapStateToProps = (state) => {
    return { moderation: state.moderation }
}
const mapDispatchToProps = dispatch => {
    return {
        removeFilterSocialFeeds: (data) => dispatch(removeFilterSocialFeeds(data)),
        removeFilterPostType: (data) => dispatch(removeFilterPostType(data)),
        removeFilterOthers: (data) => dispatch(removeFilterOthers(data)),
        removeFilterPostTags: (data) => dispatch(removeFilterPostTags(data)),
        removeDateRanges: () => dispatch(removeDateRanges()),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(FilterTab);