import React, { Component } from 'react'
import { Dropdown, Tab, Tabs } from "react-bootstrap";
import { useNavigate } from 'react-router-dom';
import { CREATE_FEED } from '../../../../../../../actions/moderationActions';
import YtTabs from './ytTabs';
import { GET_VIDEO_URLS } from '../constant';


const Youtube = (props) => {
    const navigation = useNavigate();

    return <Yt {...props} navigation={navigation} />;
}
class Yt extends Component {
    state = {
        textField: "",
        manuallyApprove: false,
        keyword: "",
        error: false,
        filterType: "Channel Url",
        channelId: "",
        latlng: "",
        isLoading: false
    }

    componentDidMount() {
        this.props.onFilterNameChange(this.state.filterType)
    }

    onTextFieldChange = (value) => this.setState({ textField: value })
    onManuallyApproveChange = (value) => this.setState({ manuallyApprove: value })
    onKeywordChange = (value) => this.setState({ keyword: value })
    onChannelIdChange = (value) => this.setState({ channelId: value })
    onLatlanChange = (value) => this.setState({ latlng: value })

    clearValues = (e) => {
        this.onTextFieldChange("")
        this.onManuallyApproveChange(false)
        this.onKeywordChange("")
        this.onChannelIdChange("")
        this.setState({ error: false, filterType: e })
        this.props.onFilterNameChange(e)

    }

    onSubmit = () => {
        this.setState({isLoading: true})
        const { textField, manuallyApprove, keyword, error, filterType, channelId, latlng } = this.state
        const { feedInfo, hideAddfeed, navigation } = this.props
        const ftr = feedInfo && feedInfo.length && feedInfo[0].networkTab && feedInfo[0].networkTab.length ? feedInfo[0].networkTab.findIndex(f => f.feedtype == filterType) : null
        const filterId = feedInfo[0]?.networkTab[ftr]?.filter
        if (!textField || textField == "") this.setState({ error: true, isLoading:false })
        // if(keyword || keyword == "") this.setState({error: true})
        else {
            var obj = {
                wallId: localStorage.getItem('wallId'),
                networkId: 7,
                filter: filterId,
                moderation: manuallyApprove ? 1 : 0,
                feedText: textField,
                channelId: channelId ? channelId : "",
            }
            if(filterId == 32) obj = {...obj, keywords: keyword, latlng}
            CREATE_FEED(obj, navigation, true, hideAddfeed)
        }
    }


    render() {
        const { textField, manuallyApprove, keyword, error, channelId, isLoading, filterType } = this.state
        const { feedInfo } = this.props
        return (
            <div className="row">
                <div className="col-md-12">
                    <div className="form-body create_feed_wt">
                        <div className="tabbable-tabdrop newRadio d-flex align-items-center justify-content-between flex-wrap feeds_details personalizePopup">
                            <Tabs defaultActiveKey="Channel Url" id="YoutubeFeedTab" onSelect={e => this.clearValues(e)} className="nav-tabs-line nav-tabs-bold w-100 tabDrop mb-5">
                                {feedInfo && feedInfo.length && feedInfo[0].networkTab && feedInfo[0].networkTab.length ? feedInfo[0].networkTab.map(filter =>
                                    <Tab eventKey={filter.feedtype} key={filter.filter} className="nav-link p-0" title={<span>{filter.feedName}</span>}>
                                        <YtTabs tab={filter.feedtype} error={error} onLatlanChange={this.onLatlanChange} value={textField} onValueChange={this.onTextFieldChange} checkBox={manuallyApprove} onCheckBoxChange={this.onManuallyApproveChange} onKeywordChange={this.onKeywordChange} onChannelIdChange={this.onChannelIdChange} channelId={channelId} keyword={keyword} />
                                    </Tab>
                                ) : null}
                            </Tabs>
                            <div className="form-actions w-100">
                                <div className="d-flex justify-content-between align-items-center flex-wrap border-top pt-lg-8 mt-lg-10 pt-5 mt-6">
                                    <div className="need_help__">
                                        <p className="d-flex align-items-center flex-wrap mb-sm-0 mb-6">
                                            Need help in adding feeds?
                                            <a href='https://vimeo.com/912907290?share=copy' className="video_btn btn btn-text p-0 ml-3 text-dark-75 btn-clean video_support_link" data-lity >
                                                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                                    <g id="Group_6606" data-name="Group 6606" transform="translate(-1 -1)">
                                                        <path id="Path_6626" data-name="Path 6626" d="M9.5,16.281V8.461A.545.545,0,0,1,10.34,8l6.083,3.909a.545.545,0,0,1,0,.919L10.34,16.74a.545.545,0,0,1-.84-.458Z" transform="translate(0.773 0.629)" fill="#A5A5A5"></path>
                                                        <path id="Path_6627" data-name="Path 6627" d="M13,2.636A10.364,10.364,0,1,0,23.364,13,10.364,10.364,0,0,0,13,2.636ZM1,13A12,12,0,1,1,13,25,12,12,0,0,1,1,13Z" fill="#A5A5A5" fillRule="evenodd"></path>
                                                    </g>
                                                </svg>
                                                <span className="ms-1"> Watch Video</span>
                                            </a>
                                        </p>
                                    </div>
                                    <div className="hideforBSearch">
                                        <button type="button" className="btn btn-secondary" onClick={this.props.onBack}>Back</button>
                                        <span className="create_feed_hideshow">
                                            <button type="button" className={`commanBtn btn btn-primary ml-4 ${isLoading ? 'spinner disabled spinner-white spinner-right' : ""}`} id="create_feed" onClick={this.onSubmit}>Create Feed</button>
                                        </span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
export default Youtube;
