import React, { Component } from 'react'
import AccountPicker from '../accountPicker'
import SearchPlace from './searchPlace'
import Select from 'react-select';

export default class GoogleTab extends Component {
    state = {  accountOption: [], showgoogleBtn: false,  }

    componentDidMount() {
        this.getChildAccountData()
        setTimeout(() => {
             this.getChildAccountData() 
       }, 1000);
    }

    componentDidUpdate(prevProps) {
        if (prevProps.connectedAcc !== this.props.connectedAcc) {
            this.getChildAccountData() 
        }
    }

    getChildAccountData=()=>{
        const { getChildAccount } = this.props
        const childAcc = getChildAccount()
        if(childAcc && childAcc.length){
            var data =  childAcc.map((f, i) => { 
                if(i==0){
                    this.props.onAccountChildChange(f.ConnectedAccounts.id)
                    this.props.onConnectedAccountChange(f.ConnectedAccounts.parent) 
              }
                return ({ 'label': f.ConnectedAccounts.value2, 'value': f.ConnectedAccounts.id }) 

            })
            data.push({'label':"Other", 'value':0})
            this.setState({ accountOption: data })
        }
       
    }

    onAccountIdChange = (value) => {
        const { getChildAccount } = this.props
        const childAcc = getChildAccount()
        if(value==0){ 
            this.props.onAccountChildChange(null)
            this.props.SELECT_ACCOUNT(null)
            this.props.onConnectedAccountChange(null) 
        }
        else{
        var mainAccountId  = childAcc.filter(f=>f.ConnectedAccounts.id == value) 
        this.props.onAccountChildChange(value)
        this.props.onConnectedAccountChange( mainAccountId[0].ConnectedAccounts.parent ) 
        }

    }
    
    render() {
        const { tab, onAddAccount, connectedAcc,getChildAccount, moderation,SELECT_ACCOUNT, onConnectedAccountChange, connetedAccount, authenticate, onAuthenticateChange, onModerationChange, onFieldTextChange, feedText, placeId, showError } = this.props
        const { accountOption, defaultOption } = this.state 
        return (
            <>
                {tab == "Places" ? <div className="form-group">
                    <SearchPlace onFieldTextChange={onFieldTextChange} />
 
                    {!showError ? '' : <span id="" className="errorMessage required">This field is required</span>}
                </div>
                    :
                    <>

                        {accountOption && accountOption.length ?

                            <div className="col-md-12">
                                 
                                    <label>Select Account</label>
                                    <Select options={accountOption} defaultValue={accountOption[0]} onChange={(e) => this.onAccountIdChange(e.value)} className="addFeedGoogle"/>
                                    <br />
                            </div>

                            :
                            <div className="otherSection text-center" onClick={onAddAccount}>
                                <div className="py-5">
                                    <a className="btn btn-outline-gray btn-lg w-auto btn-clean d-inline-flex align-items-center btn-lg px-6">
                                        <span className="symbol symbol-25 mr-3">
                                            <img src="https://cloud.taggbox.com/taggshop/app/assets/media/square-icons/google.svg?ver=1" alt="" />
                                        </span>
                                        <span className="text-dark-50 font-weight-bolder font-size-base">Connect Google</span>
                                    </a>
                                </div>
                            </div>
                        }
                    </>}

                <div className="hideforBSearch">
                    <div className="checkbox-inline">
                        <label htmlFor="feedscreatemoderation" className="checkbox checkbox-outline checkbox-primary mr-2">
                            {/* <input type="hidden" id="feedscreatemoderation_" value="0" /> */}
                            <input type="checkbox" id="feedscreatemoderation" className="checkbox checkbox-outline checkbox-outline-2x checkbox-primary mr-2" checked={moderation} onChange={e => onModerationChange(e.target.checked)} />
                            <span></span>
                            Manually approve posts before making them public?
                        </label>
                    </div>
                </div>

                {
                    tab == "Places" ?
                        <div id="feed_note" className="alert alert-custom fade show mt-6 mb-0 bg-light rounded-0 p-3 align-items-start">
                            <div className="alert-icon py-1">
                                <span className="svg-icon svg-icon-sm">
                                    <svg xmlns="http://www.w3.org/2000/svg" width="13" height="13" viewBox="0 0 13 13">
                                        <path id="Path_1660" fill="#959595" data-name="Path 1660" d="M17.5,11A6.5,6.5,0,0,0,11,17.5a6.64,6.64,0,0,0,2.472,5.1A6.265,6.265,0,0,0,17.5,24a6.5,6.5,0,0,0,0-13Zm.667,9.167a.667.667,0,0,1-1.333,0V17a.667.667,0,1,1,1.333,0ZM17.5,15.5a.667.667,0,1,1,.667-.667A.667.667,0,0,1,17.5,15.5Z" transform="translate(-11 -11)" opacity="0.999"></path>
                                    </svg>
                                </span>
                            </div>
                            <div className="alert-text">
                                Initially the latest 5 user reviews of Place will be aggregated after which all the upcoming reviews will be aggregated.
                            </div>
                        </div>
                        :
                        <div id="feed_note" className="alert alert-custom fade show mt-6 mb-0 bg-light rounded-0 p-3 align-items-start">
                            <div className="alert-icon py-1">
                                <span className="svg-icon svg-icon-sm">
                                    <svg xmlns="http://www.w3.org/2000/svg" width="13" height="13" viewBox="0 0 13 13">
                                        <path id="Path_1660" fill="#959595" data-name="Path 1660" d="M17.5,11A6.5,6.5,0,0,0,11,17.5a6.64,6.64,0,0,0,2.472,5.1A6.265,6.265,0,0,0,17.5,24a6.5,6.5,0,0,0,0-13Zm.667,9.167a.667.667,0,0,1-1.333,0V17a.667.667,0,1,1,1.333,0ZM17.5,15.5a.667.667,0,1,1,.667-.667A.667.667,0,0,1,17.5,15.5Z" transform="translate(-11 -11)" opacity="0.999"></path>
                                    </svg>
                                </span>
                            </div>
                            <div className="alert-text">Initially latest 50 user reviews will be aggregated after which all the upcoming reviews will be aggregated.</div>
                        </div>
                }
            </>
        )
    }
}
