    clickToCloseAddCta = event => { this.setState({ addCta: false, ctaReferenceID: null, data: null }) }
    clickToOpenEditPost = (spostId, id, index) => event => { this.setState({ editPost: true, postID: spostId, id, itemIndex: index }) }
    clickToCloseEditPost = event => { this.setState({ editPost: false, postID: null, id: null, itemIndex: null }) }
    clickToOpenAddTag = (data, index) => event => { this.setState({ addTag: true, data, itemIndex: index }) }
    clickToCloseAddTag = event => { this.setState({ addTag: false, data: null, itemIndex: null }) }
    onRemoveTags = (index) => event => { }
    onClickToPostTags = (tags) => event => { this.props.searchTagsFilter(tags) }
    clickToActiveView = (itemId) => event => {
        const { activeView } = this.state;
        if (activeView == itemId) this.setState({ activeView: null })
        else this.setState({ activeView: itemId })
    }
    
    refreshPostLoadingChange = (state) => this.setState({refreshPostLoading: state})

    
    onRefreshPost = async(item) => {
        const { UPDATE_INSTA_POST_ACTION } = this.props
        await UPDATE_INSTA_POST_ACTION(item, this.refreshPostLoadingChange)

    }

