
import React, { Component } from 'react'
import { connect } from 'react-redux';
import { ACTION_CLEAR_DATA, SNAPUP_DETAILS_ACTION } from '../../../../../../actions/addFeedActions';
import { BASE_URL, CLOUDPATH } from '../../../../../../actions/api';
import { GET_FEEDS } from '../../../../../../actions/manageFeedsAction';
import { FETCH_CONNECTED_ACCOUNTS, GET_ADD_FEED, GET_NETWORK_INFO_BY_ID } from '../../../../../../actions/moderationActions';
import { togglePriceSection } from '../../../../../../actions/show_modal_Action';
import { TOAST_DISMISS, TOAST_LOADING } from '../../../../../../utils';
import CreateFeed from './createFeed'
import TwitterUpgradePopup from './twitter/UpgradeTwitterPopUp';
import { Link } from 'react-router-dom';

class allFeeds extends Component {
    state = {
        modalShow: true,
        create: false,
        selectedNetwork: null,
        feedInfo: null,
        isLimitExceed: false,
        twitterAlert: false
    }

    componentDidMount() {
        const { ACTION_CLEAR_DATA, authorized, totalFeed, addFeedData } = this.props
        ACTION_CLEAR_DATA()
        if (totalFeed >= authorized.userData.user_data.UserRule.feeds) this.setState({ isLimitExceed: true })
        this.props.GET_ADD_FEED(() => this.onCheckSelectedNetwork());
        this.props.GET_FEEDS();
    }

    onCheckSelectedNetwork = () => {
        const { addFeedData } = this.props
        var selected_network = localStorage.getItem('selectedNetwork') ? localStorage.getItem('selectedNetwork') : null
        selected_network = addFeedData && addFeedData.networks ? addFeedData.networks.find(n => n.networkId == selected_network) : null
        if (selected_network) this.onNetworkSelect(selected_network)
    }



    onNetworkSelect = (network) => {
        if (network.networkId == 1 && this.props.authorized.userData.user_data.userPlanDetails?.twitter_feed_access == 1) {
            this.setState({ twitterAlert: true })
        }
        else {
            TOAST_LOADING()
            GET_NETWORK_INFO_BY_ID(network.networkId).then(res => {
                const { responseData } = res.data
                this.setState({ feedInfo: responseData })
                this.props.FETCH_CONNECTED_ACCOUNTS(network.networkId)
                if (network.networkId == 18) {
                    this.props.FETCH_CONNECTED_ACCOUNTS(2)
                }
                if (network.networkId === 29) {
                    this.props.SNAPUP_DETAILS_ACTION(() => this.setState({ selectedNetwork: network }))
                }
                if (network.networkId !== 29) {
                    setTimeout(() => {
                        this.setState({ selectedNetwork: network })
                    }, 700);
                }
                TOAST_DISMISS()
            }).catch(err => {
                TOAST_DISMISS()
            })
        }
    }


    clickToToggleModal = () => {
        this.setState({
            modalShow: !this.state.modalShow
        })
    }
    render() {
        const { addFeedData, onShowAddFeed, hideAddfeed, authorized, ACTION_CLEAR_DATA } = this.props
        const { create, selectedNetwork, feedInfo, isLimitExceed, twitterAlert } = this.state
        return (
            // <ErrorFound>
            <div className="add-feed-modal" id="createFeedOption">
                {addFeedData ?
                    <div className="add-feed-modal-inner">
                        <div className="d-flex align-items-center justify-content-between border-bottom page_custom_header">
                            <div className="d-flex align-items-center flex-nowrap ">
                                {/* <button type="button" className="btn btn-icon btn-clean btn-lg btn-square back_btn mr-4" onClick={() => selectedNetwork ? this.setState({ selectedNetwork: null }, () => ACTION_CLEAR_DATA()) : hideAddfeed()}>
                                    <svg xmlns="http://www.w3.org/2000/svg" width="28" height="28" viewBox="0 0 32 32">
                                        <g id="Group_5147" data-name="Group 5147" transform="translate(-282 -85)">
                                            <rect id="Rectangle_2245" data-name="Rectangle 2245" width="28" height="28" transform="translate(282 85)" fill="none"></rect>
                                            <g id="_15" data-name="15" transform="translate(292.278 91)">
                                                <path id="Path_5609" data-name="Path 5609" d="M18.238,25.487a1.463,1.463,0,0,0,1.039-2.5l-7.755-7.74L19.277,7.5A1.469,1.469,0,0,0,17.2,5.426L8.42,14.206a1.463,1.463,0,0,0,0,2.063L17.2,25.048a1.463,1.463,0,0,0,1.039.439Z" transform="translate(-7.994 -4.996)" fill="#545454"></path>
                                            </g>
                                        </g>
                                    </svg>
                                </button> */}
                                <h5 className="feed_title m-0 text-nowrap overflow-hidden" id="exampleModalLongTitle">{selectedNetwork ? isLimitExceed ? "Upgrade" : `Create ${selectedNetwork.networkName} Feed` : 'Pick a source'}</h5>
                            </div>
                            {window.location.pathname?.includes('/content/moderation/') ?
                                <div className="d-flex align-items-center justify-content-end close_btn" onClick={() => selectedNetwork ? this.setState({ selectedNetwork: null }, () => ACTION_CLEAR_DATA()) : hideAddfeed()} >
                                    <svg xmlns="http://www.w3.org/2000/svg" xlink="http://www.w3.org/1999/xlink" width="28" height="28" viewBox="0 0 24 24" version="1.1">
                                        <g transform="translate(12.000000, 12.000000) rotate(-45.000000) translate(-12.000000, -12.000000) translate(4.000000, 4.000000)" fill="#545454">
                                            <rect x="0" y="7" width="16" height="2" rx="1"></rect>
                                            <rect transform="translate(8.000000, 8.000000) rotate(-270.000000) translate(-8.000000, -8.000000) " x="0" y="7" width="16" height="2" rx="1"></rect>
                                        </g>
                                    </svg>
                                </div> :
                                <div className="d-flex align-items-center justify-content-end close_btn">
                                    <Link to={`/content/moderation/${localStorage.getItem('wallId')}`}>
                                        <svg xmlns="http://www.w3.org/2000/svg" xlink="http://www.w3.org/1999/xlink" width="28" height="28" viewBox="0 0 24 24" version="1.1">
                                            <g transform="translate(12.000000, 12.000000) rotate(-45.000000) translate(-12.000000, -12.000000) translate(4.000000, 4.000000)" fill="#545454">
                                                <rect x="0" y="7" width="16" height="2" rx="1"></rect>
                                                <rect transform="translate(8.000000, 8.000000) rotate(-270.000000) translate(-8.000000, -8.000000) " x="0" y="7" width="16" height="2" rx="1"></rect>
                                            </g>
                                        </svg>
                                    </Link>
                                </div>}
                        </div>
                        {!selectedNetwork ?
                            <div className="add-feed-modal-body">
                                <div className="container">
                                    <div className="small_container">
                                        <div className="justify-content-center align-items-center p-5">
                                            <div className="my-5 text-center">
                                                <div className="text-center add_feed_head">
                                                    <h3 className="font-weight-bold text-dark network_title_wt">{addFeedData?.resposneMessage}</h3>
                                                    <p className='network_subtext_wt mb-0'>Select the social platform from which you want to add the feeds</p>
                                                </div>
                                                <form action="https://app.taggbox.com/widget/wall/create/4028//" method="post" className="" id="wallCreatePopUp" target="_blank">
                                                    <ul className="networks">
                                                        {addFeedData && addFeedData.networks && addFeedData.networks.map(network => {
                                                            if (network.networkId == 33) return <li className={network.networkCode} key={network.networkId}>
                                                                <Link to="/creators" className="popUp resetAction" >
                                                                    <img src={`${CLOUDPATH}taggshop/app/assets/media/square-icons/creators-collab.svg?ver=1`} alt={network.networkName} className="img-fluid" />
                                                                    <span>{network.networkName}</span>
                                                                    <span className="newBadge_addfeed">NEW</span>
                                                                </Link>  </li>

                                                            else {
                                                                return <li className={network.networkCode} key={network.networkId} onClick={() => this.onNetworkSelect(network)}>
                                                                    <a className="popUp resetAction">
                                                                        <img src={`${CLOUDPATH}taggshop/app/assets/media/square-icons/${network.networkCode}.svg?ver=1`} alt={network.networkName} className="img-fluid" />
                                                                        <span>{network.networkName}</span>
                                                                    </a>  </li>

                                                            }


                                                        }

                                                        )}
                                                    </ul>

                                                </form>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div> :
                            selectedNetwork ? isLimitExceed && selectedNetwork.networkId !== 29 ?
                                <div id="feed_content__">
                                    <div className="d-flex flex-column flex-root" style={{ minHeight: "calc(100vh - 136px)" }}>
                                        <div className="d-flex flex-row-fluid">
                                            <div className="small_container d-flex flex-row-fluid flex-column justify-content-center px-6 text-center">


                                                <div className="upgrade_succ_wrap p-10 position-relative d-flex align-items-center justify-content-center">
                                                    <img src={`${BASE_URL}/assets/media/success/Upgrade-Account.svg`} className="img-fluid position-relative" alt="" />
                                                </div>
                                                <h2 className="font-weight-boldest mb-8">Upgrade to Add More Feeds</h2>

                                                <p className="font-size-h6">Oops! Looks like you need to upgrade your plan in order to add more Feeds</p>

                                                <div className="d-flex align-items-center flex-wrap justify-content-center my-10">

                                                    <button type="button" className="btn btn-primary upgPlan" id="upgPlan" onClick={() => { this.props.togglePriceSection(true, 2) }} >Upgrade Now!</button>

                                                </div>

                                            </div>
                                        </div>
                                    </div>
                                </div>
                                : <CreateFeed network={selectedNetwork} onBack={() => this.setState({ selectedNetwork: null }, () => localStorage.setItem('selectedNetwork', null))} hideAddfeed={hideAddfeed} feedInfo={feedInfo} addFeedData={addFeedData} /> : null}


                    </div> : ""}
                {twitterAlert ? <TwitterUpgradePopup show={twitterAlert} handleClose={() => this.setState({ twitterAlert: false })} /> : null}
            </div>
            // </ErrorFound>
        )
    }
}
const mapStateToProps = (state) => {
    return {
        authorized: state.authorized,
        totalFeed: state.manageFeeds.totalFeed,
        addFeedData: state.moderation.addFeedNetworks,
        feeds: state.manageFeeds.feeds,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        FETCH_CONNECTED_ACCOUNTS: (id) => dispatch(FETCH_CONNECTED_ACCOUNTS(id)),
        ACTION_CLEAR_DATA: () => dispatch(ACTION_CLEAR_DATA()),
        togglePriceSection: (status, id) => dispatch(togglePriceSection(status, id)),
        GET_ADD_FEED: (callback) => dispatch(GET_ADD_FEED(callback)),
        GET_FEEDS: () => dispatch(GET_FEEDS()),
        SNAPUP_DETAILS_ACTION: (callback) => dispatch(SNAPUP_DETAILS_ACTION(callback)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(allFeeds);