import { Component } from "react";
import { connect } from "react-redux";
import { AUTOCOMPLETE } from "../../../../../../../actions/manageFeedsAction";
import AccountPicker from "../accountPicker";

class TwTabs extends Component {
    state = {
        handles: [],
        showHandle: false,
        showList: false,
        selectedHandle: null,
        selectedList: null,
        lists: [],
        feedTextCharError: false,
        listTextCharError: false
    }

    autoComplete = (action, value) => {
        const obj = {
            action: action,
            networkId: 1,
            handle: value,
        }
        if (action == 2) obj['userId'] = this.state.selectedHandle.id
        AUTOCOMPLETE(obj).then(res => {
            const { data } = res
            console.log(data)
            if (data.responseCode == 200) {
                this.setState({ handles: data.responseData.data })
                this.setState({ lists: data.responseDataUserList.data })
            }
            else this.setState({ handles: [], lists: [] })
        })
    }

    onInputChange = (e) => {
        const { onFieldTextChange, tab } = this.props
        onFieldTextChange(e.target.value)
        if (tab != 'hashtag') {
            this.setState({ showHandle: true })

            this.autoComplete(1, e.target.value)
        }
        // else
    }
    onListInputChange = (e) => {
        const { onListNameChange, tab } = this.props
        onListNameChange(e.target.value)
        if (tab != 'hashtag') {
            this.setState({ showList: true })

            this.autoComplete(2, e.target.value)
        }
        // else
    }

    onSelectList = (item) => {
        const { onListNameChange, tab, onListIdChange } = this.props
        onListNameChange(item.name)
        document.querySelector(`#tw-List`).value = item.name
        onListIdChange(item.id)
        this.setState({ showList: false, selectedList: item })
    }
    onSelectHandle = (item) => {
        const { onFieldTextChange, tab, onUserIdChange } = this.props
        onFieldTextChange(item.username)
        document.querySelector(`#${tab}-text`).value = item.username
        onUserIdChange(item.id)
        this.setState({ showHandle: false, selectedHandle: item })
    }

    handleFeedTextChange = (e) => {
        const { filter } = this.props
        const value = e.target.value
        // console.log("value================", typeof value, (value.includes("@") || value.includes("#")) )
        if (this.props.tab == "advanced") {
            this.props.onFieldTextChange(value)
        }
        else {
            if (value.includes("@") || value.includes("#")) {this.setState({ feedTextCharError: true })}

            else if(!(value.includes("@") || value.includes("#"))){
                // console.log("value", value, value[value.length - 1])

                if (filter == 3) this.onInputChange(e)
                else this.setState({ feedTextCharError: false }, () => this.props.onFieldTextChange(value))
            } 
        }
    }

    handleListChange = (e) => {
        const value = e.target.value
        if (value[value.length - 1] != '@' && value[value.length - 1] != "#" && !value.includes("@") && !value.includes("#")) this.setState({ listTextCharError: false }, () => this.onListInputChange(e))
        else this.setState({ listTextCharError: true })
    }

    render() {
        const { handles, showHandle, lists, showList, listTextCharError, feedTextCharError } = this.state
        const { tab, msg, moderation, excludeRetweet, excludeReplies,
            onModerationChange,
            onExcRetweetChange,
            networkId,
            connetedAccount,
            onConnectedAccountChange,
            listId,
            onListIdChange,
            authenticate,
            onAuthenticateChange,
            onExrepliesChange, feedText, onFieldTextChange, multiplePhoto, onMultiplePhotoChange, filter, listname, onListNameChange, showError, showListError, connectedAcc } = this.props
        return (
            <>
                <div className="row">
                    <div className={`${filter == 3 ? "col-md-6" : "col-md-12"}`}>
                        <div className="form-group">
                            <div className="input-group">
                                <div className="input-group-prepend">
                                    <span className="input-group-text">
                                        <i className={`fas fa-${tab == 'hashtag' ? 'hashtag' : tab == "advanced" ? 'search' : 'at'}`}></i>
                                    </span>
                                </div>
                                <input data-limit="100"
                                    placeholder={`Enter ${tab == 'favorites' ? `${msg}` : (`${msg != 'hashtag' ? msg != 'advanced' ? (msg != 'list' ? 'user' : "username") : '' : ""} ${msg != 'list' ? msg : ""}`)}`}
                                    className="autosearch mycheck form-control"
                                    id={`${tab}-text`}
                                    type="text"
                                    value={feedText?feedText: ""}
                                    autoComplete="off"
                                    onChange={this.handleFeedTextChange}
                                />
                                {filter == 3 ? <div className="handlelist" id="" style={showHandle && feedText ? { display: "block" } : { display: "none" }}>
                                    <ul className="handles-unstyled">
                                        {handles && handles.length ? handles.map((item, index) => (

                                            <li key={item.id} onClick={() => this.onSelectHandle(item)}>
                                                <div className="img-section">
                                                    <img src={item.profile_image_url} />
                                                </div>
                                                <div className="content-section">
                                                    {item.username}
                                                    <p>https://twitter.com/{item.username}</p>
                                                </div>
                                            </li>
                                        )) : null}
                                        {/* // <li */}
                                    </ul>
                                </div> : null}
                            </div>
                            {showError ? <span id="" className="errorMessage required">This field is required</span> : null}
                            {feedTextCharError ? <span id="" className="errorMessage required">@ and # not allowed</span> : null}
                            {tab == "advanced" ? <span className="help-block">Use the table below to construct your query.</span> : null}
                        </div>
                    </div>
                    {filter == 3 ?
                        <div className="col-md-6">
                            <div className="form-group">
                                <div className="input-group">
                                    <div className="input-group-prepend">
                                        <span className="input-group-text">
                                            <i className="fa fa-list"></i>
                                        </span>
                                    </div>
                                    <input placeholder="List Name" id="tw-List" className="form-control" type="text" value={listname} onChange={e => this.handleListChange(e)}
                                        onFocus={() => { this.autoComplete(2, ""); this.setState({ showList: true }) }}
                                    // onBlur={() => this.setState({ showList: false })}
                                    />
                                    <div className="handlelist" id="twitterList"
                                        style={showList ? { display: "block" } : { display: 'none' }}
                                    >
                                        <ul className="handles-unstyled">
                                            {lists && lists.length ? lists.map(list => (
                                                <li key={`hhhh_${list.id}`} onClick={() => this.onSelectList(list)}>
                                                    <div className="img-section">
                                                        <img src="https://pbs.twimg.com/profile_images/2680241957/3df5cf067ffd553dc4b6b5592db81751_normal.jpeg" />
                                                    </div>
                                                    <div className="content-section"><span>{list.name}</span>
                                                        <p>https://twitter.com/FdgnlfsSdfgsg</p>
                                                    </div>
                                                </li>
                                            ))
                                                : null}
                                        </ul>
                                    </div>
                                </div>
                                {showListError ? <span className="errorMessage required">This field is required.</span> : null}
                                {listTextCharError ? <span id="" className="errorMessage required">@ and # not allowed</span> : null}
                            </div>
                        </div>
                        : null}
                </div>

                {tab == "advanced" ?
                    <div className="w-100 my-5" style={{ height: "250px", overflowY: "auto" }}>
                        <div className="table-responsive">
                            <table className="table table-hover table-light">
                                <thead>
                                    <tr>
                                        <th style={{ width: "25%" }}><strong>Filter By:</strong></th>
                                        <th><strong>Example</strong></th>
                                        <th><strong>Targets Tweets:</strong></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <td className="bold">
                                            <i className="far fa-comment"></i>
                                            Keywords
                                        </td>
                                        <td style={{ width: "166px" }}>grapes banana</td>
                                        <td>containing both "grapes" and "banana".</td>
                                    </tr>
                                    <tr>
                                        <td><strong></strong></td>
                                        <td>"good time"</td>
                                        <td>containing the exact phrase "good time"</td>
                                    </tr>
                                    <tr>
                                        <td><strong></strong></td>
                                        <td>die OR alive</td>
                                        <td>containing either "die" or "alive" (or both)</td>
                                    </tr>
                                    <tr>
                                        <td><strong></strong></td>
                                        <td>food -drink</td>
                                        <td>containing "food" but not "drink"</td>
                                    </tr>
                                    <tr>
                                        <td><strong></strong></td>
                                        <td>#soul</td>
                                        <td>containing the hashtag "#soul"</td>
                                    </tr>
                                    <tr>
                                        <td className="bold"></td>
                                        <td>service :) </td>
                                        <td>containing "service" and with a positive attitude.</td>
                                    </tr>
                                    <tr>
                                        <td className="bold"></td>
                                        <td>service :( </td>
                                        <td>containing "service" and with a negative attitude.</td>
                                    </tr>
                                    <tr>
                                        <td className="bold"><i className="fas fa-mobile-alt"></i> Questions</td>
                                        <td>weather ?</td>
                                        <td>containing "weather" and asking a question.</td>
                                    </tr>
                                    <tr>
                                        <td className="bold"><i className="fas fa-user"></i> User</td>
                                        <td>from:taggbox</td>
                                        <td>sent from the user @taggbox</td>
                                    </tr>
                                    <tr>
                                        <td><strong></strong></td>
                                        <td>to:taggbox</td>
                                        <td>sent to the user @taggbox</td>
                                    </tr>
                                    <tr>
                                        <td><strong></strong></td>
                                        <td>@taggbox</td>
                                        <td>mentioning @taggbox</td>
                                    </tr>
                                    <tr>
                                        <td className="bold">
                                            <i className="fas fa-location-arrow"></i>
                                            Location
                                        </td>
                                        <td>#dogs geocode:34.0522342,-118.2436849,10km</td>
                                        <td>
                                            containing hashtag "#dogs" within Los Angeles Get coordinates (10km radius)
                                            <a tabIndex="-1" target="_blank" href="https://www.gps-coordinates.org/">here</a>
                                        </td>
                                    </tr>
                                    <tr style={{ display: "none" }}>
                                        <td className="bold">
                                            <i className="fas fa-mobile-alt"></i>
                                            Devices / Apps
                                        </td>
                                        <td>news source:instagramfeed</td>
                                        <td>
                                            containing "news" and entered via InstagramFeed
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>
                                            <strong>
                                                <i className="fas fa-link"></i>
                                                Content
                                            </strong>
                                        </td>
                                        <td>funny filter:links</td>
                                        <td>containing " funny" and with a URL.</td>
                                    </tr>
                                    <tr>
                                        <td className="bold"><i className="far fa-calendar-alt"></i> Dates</td>
                                        <td>champion since:2012-12-24</td>
                                        <td>containing " champion" and sent since date "2012-12-24" (year-month-day).</td>
                                    </tr>
                                    <tr>
                                        <td className="bold"></td>
                                        <td>champion until:2012-12-24</td>
                                        <td>containing " champion" and sent before the date "2012-12-24".</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div> : null
                }
                <div className="checkbox-inline mb-2">
                    <label className="checkbox checkbox-outline checkbox-outline-2x checkbox-primary mr-2" id="">
                        <input type="checkbox" checked={multiplePhoto} id="FeedMultiplePhoto" onChange={e => onMultiplePhotoChange(e.target.checked)} />
                        <span></span>
                        Add Multiple Photos
                    </label>
                </div>
                <div className="checkbox-inline mb-2">
                    <label className="checkbox checkbox-outline checkbox-outline-2x checkbox-primary mr-2" id="">
                        <input type="checkbox" checked={excludeRetweet} id="FeedExcludeRetweet" onChange={e => onExcRetweetChange(e.target.checked)} />
                        <span></span>
                        Exclude Retweets
                    </label>
                </div>
                {filter != 3 ?
                    <div className="checkbox-inline mb-2 excludeRepliesAdd">
                        <label className="checkbox checkbox-outline checkbox-outline-2x checkbox-primary mr-2" id="">
                            <input type="checkbox" checked={excludeReplies} id="FeedExcludeReplies" onChange={e => onExrepliesChange(e.target.checked)} />
                            <span></span>
                            Exclude Replies
                        </label>
                    </div>
                    : null}
                <div className="hideforBSearch">
                    <div className="checkbox-inline">
                        <label htmlFor="feedscreatemoderation" className="checkbox checkbox-outline checkbox-outline-2x checkbox-primary mr-2">
                            {/* <input type="hidden" id="feedscreatemoderation_" value="0" /> */}
                            <input type="checkbox" id="feedscreatemoderation" className="checkbox checkbox-outline checkbox-outline-2x checkbox-primary mr-2" checked={moderation} onChange={e => onModerationChange(e.target.checked)} />
                            <span></span>
                            Manually approve posts before making them public?
                        </label>
                    </div>
                </div>
                {connectedAcc && connectedAcc.length ? <AccountPicker connectedAcc={connectedAcc} networkId={networkId} onConnectedAccountChange={onConnectedAccountChange} connetedAccount={connetedAccount} onAuthenticateChange={onAuthenticateChange} authenticate={authenticate} /> : null}
            </>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        connectedAcc: state.addFeedReducer.connectedAcc
    }
}

const mapDispatchToProps = dispatch => {
    return {
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(TwTabs)