import React, { Component } from 'react'
import { Dropdown, Tab, Tabs } from "react-bootstrap";
import { useNavigate } from 'react-router';
import { CREATE_FEED } from '../../../../../../../actions/moderationActions';

const Airbnb = (props) => {
    const navigation = useNavigate();
    return <AirBnb {...props} navigation={navigation} />;
}
class AirBnb extends Component {
    state = { text: "", manuallyApprove: false, error: false, filterType: "Handle", loading: false }

    onSubmit = () => {
        const { text, manuallyApprove, error, filterType } = this.state
        const { navigation, feedInfo, hideAddfeed } = this.props

        if (!text || text == "") this.setState({ error: true })
        else {

            this.setState({ error: false, loading: true })

            var obj = { wallId: localStorage.getItem('wallId'), networkId: 23, moderation: manuallyApprove ? 1 : 0, connetedAccount: 0, feedType: 56, airbnb_page_review: text, account: 0, filter: 56, hashtagIGTV: 0, onlyReels: 0, }
            
            localStorage.setItem("createFeedObj", JSON.stringify(obj))
            localStorage.setItem("networkId", 23)

        CREATE_FEED(obj, navigation, true, hideAddfeed)
        }
    }

    render() {
        const { text, manuallyApprove, error, loading } = this.state
        const { feedInfo, onBack } = this.props
        return (
            <div className="row">
                <div className="col-md-12">
                    <div className="form-body create_feed_wt">
                        <div className="tabbable-tabdrop newRadio d-flex align-items-center justify-content-between flex-wrap feeds_details personalizePopup">
                            <Tabs defaultActiveKey="Listing Url" id="AirbnbFeedTab" className="nav-tabs-line nav-tabs-bold w-100 tabDrop mb-5">
                                {feedInfo && feedInfo.length && feedInfo[0].networkTab && feedInfo[0].networkTab.length ? feedInfo[0].networkTab.map(filter => (
                                    <Tab eventKey={filter.feedtype} key={filter.filter} className="nav-link className= p-0" title={<span>{filter.feedName}</span>}>
                                        <div className="form-group">
                                            <div className="input-group">
                                                <div className="input-group-prepend">
                                                    <span className="input-group-text">
                                                        <i className="fab fa-font-awesome-flag"></i>
                                                    </span>
                                                </div>
                                                <input placeholder="Enter Airbnb Page Url" value={text} onChange={(e) => this.setState({ text: e.target.value })} className="mycheck form-control" type="text" />
                                            </div>
                                            {error ? <span id="" className="errorMessage required">This field is required</span> : null}
                                        </div>
                                        <div className="hideforBSearch">
                                            <div className="checkbox-inline">
                                                <label htmlFor="feedscreatemoderation" className="checkbox checkbox-outline checkbox-primary mr-2">
                                                    <input type="checkbox" id="feedscreatemoderation" checked={manuallyApprove} onChange={(e) => this.setState({ manuallyApprove: e.target.checked })} className="checkbox checkbox-outline checkbox-outline-2x checkbox-primary mr-2" value="1" />
                                                    <span></span>
                                                    Manually approve posts before making them public?
                                                </label>
                                            </div>
                                        </div>
                                    </Tab>
                                )) : null}
                            </Tabs>

                            <div className="form-actions w-100">
                                <div className="d-flex justify-content-between align-items-center flex-wrap border-top pt-lg-8 mt-lg-10 pt-5 mt-6">
                                    <div className="need_help__">
                                        <p className="d-flex align-items-center flex-wrap mb-sm-0 mb-6">
                                            Need help in adding feeds?
                                            <a href="https://vimeo.com/912907290?share=copy" className="video_btn btn btn-text p-0 ml-3 text-dark-75 btn-clean video_support_link" data-lity>
                                                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                                    <g id="Group_6606" data-name="Group 6606" transform="translate(-1 -1)">
                                                        <path id="Path_6626" data-name="Path 6626" d="M9.5,16.281V8.461A.545.545,0,0,1,10.34,8l6.083,3.909a.545.545,0,0,1,0,.919L10.34,16.74a.545.545,0,0,1-.84-.458Z" transform="translate(0.773 0.629)" fill="#A5A5A5"></path>
                                                        <path id="Path_6627" data-name="Path 6627" d="M13,2.636A10.364,10.364,0,1,0,23.364,13,10.364,10.364,0,0,0,13,2.636ZM1,13A12,12,0,1,1,13,25,12,12,0,0,1,1,13Z" fill="#A5A5A5" fillRule="evenodd"></path>
                                                    </g>
                                                </svg>
                                                <span className="ms-1"> Watch Video</span>
                                            </a>
                                        </p>
                                    </div>
                                    <div className="hideforBSearch">
                                        <button type="button" className="btn btn-secondary" onClick={onBack}>Back</button>
                                        <span className="create_feed_hideshow">
                                            <button type="button" className={`commanBtn btn btn-primary ml-4 ${loading ? 'spinner spinner-white disabled spinner-right' : ""}`} id="create_feed" onClick={this.onSubmit}>Create Feed</button>
                                        </span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
export default Airbnb
