import React, { Component } from 'react'
import parse from 'html-react-parser';
import Iframe from 'react-iframe';
import ReactPlayer from "react-player"
import { noImage } from '../../../../../../../../actions/api'
import { IMG_LOAD_AFTER_COMPLETE_LAYOUT, IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO } from '../../../../../../../../utils'
import Tiktoks from './tiktok'
class Media extends Component {
state = { isVideo: false }

    onClickToPlay = event => { this.setState({ isVideo: true }) }

    render() {
        const { item, handleAllImageError } = this.props;
        const { isVideo } = this.state;
        const videoUrl = item.mediaUrl; 
        return <>  {item?.mediaClip  && !isVideo ?
          <ReactPlayer
            poster={item.postFileNew}
            className="tb-detail-image-iframe"
            url={item?.mediaClip}
            data-type="video"
            data-network={item.networkId}
            data-link={item.link} 
            data-load={0}
            config={{
              facebook: { appId: '117029975354072', attributes: { 'data-width': 'auto', } },
              file: {
                attributes: {
                  'data-type': "video", 'data-network': item.networkId, 'data-link': item.link, 
                  'data-item-id': item.id, 'data-stories':item.stories, "data-load":'0',
                  'poster':item.newMedia, 'data-wall-id':localStorage.getItem('wallId')
                }
              }
            }}
            loop={true}
            // onError={(e) => { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO(e) }}
            autoPlay={true}
            muted
            playsinline={true} 
            playing={true}
            height={'100%'}
            width={'auto'}
            onClick={this.onClickToPlay}
        //   style={videoLoaded ? { position: 'relative', opacity: 1, background: "#000" } : { opacity:1 }}
          />  : !isVideo && item.postFile != '' && item.postFile != null ? <span  onClick={this.onClickToPlay}>
                 {item.networkId ==28 ? 
                <img className="img-fluid initLazyLoad"  src={item.newMedia} data-id={item.id} id={`imgID${item.id}`} onClick={this.onClickToPlay}  />
                : 
                 <img className="img-fluid initLazyLoad" data-src={item.newMedia} src={item.newMedia}  data-original={item.newMedia} data-type={item.type} data-load="0" data-filter={item.feedFilter} data-wall-id={localStorage.getItem('wallId')} data-id={item.id} data-owner={item.createdBy} data-network={item.networkId} data-link={item.link} data-expired={item.expired} data-status={item.status} data-stories={item.stories} onClick={this.onClickToPlay} 
                 onError={handleAllImageError} 
                 /> }
                </span> : isVideo ? <>  
                {/* <div className="tb_img_holder">
                    <div className="tb_modal_img_holder_001" data-network={item.networkId}>
                   <ReactPlayer
                            style={{ display: 'contents' }}
                            className="ts-detail-image-iframe"
                            url={videoUrl}
                            data-type="video"
                            data-stories={item.stories}
                            data-filter-id={item.filterId}
                            data-network={item.networkId}
                            data-link={item.link} 
                            data-item-id={item.id}
                            data-load={0}
                            config={{
                                facebook: { appId: '117029975354072'  },
                                file: { attributes: {'id':`ts-detail-image-iframe_${item.id}` , 'data-type': "video", 'data-network': item.networkId, 'data-link': item.link, 'data-item-id': item.id, 'data-stories':item.stories, "data-load":'0', 'poster':item.newMedia, 'data-wall-id':localStorage.getItem('wallId') } }
                              }}
                            onError={(e) => { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO(e) }}
                            autoPlay
                            controls
                        />
                        </div>
                        </div> */}

                {
                    (item.type === 3 || item.type === 5) &&
                        (item.link.indexOf("youtube") >= 0 ||
                            item.link.indexOf("youtu.be") >= 0 ||
                            item.link.indexOf("tumblr") >= 0 ? (
                            <div className="tb_img_holder">
                                <div className="tb_modal_img_holder_001">
                                    <div className="tb_video_wrap">
                                        <Iframe
                                            className="ts-detail-image-iframe"
                                            frameBorder="0"
                                            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                                            src={`${videoUrl}`}
                                            allowFullScreen="true"
                                            style={{ width: "100%!important" }}
                                            onError={(e) => { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO(e) }}
                                            data-type="video"
                                            data-stories={item.stories}
                                            data-filter-id={item.filterId}
                                            data-network={item.networkId}
                                            data-link={item.link}
                                            //data-wall-id={wall.Wall.id} 
                                            data-item-id={item.id}
                                            data-load={0}
                                        ></Iframe>
                                    </div>
                                </div>
                            </div>
                        ) : ((item.link.indexOf("vk") >= 0) && item.networkId == 30 || item.networkId == 29 || item.networkId == 6 || item.networkId == 18 )) ? (
                        parse(item.mediaFile)
                    ) : (item.link.indexOf("facebook") >= 0) ? <div className="tb_img_holder"><div className="tb_modal_img_holder_001">
                        <ReactPlayer
                            style={{ display: 'contents' }}
                            className="ts-detail-image-iframe"
                            url={videoUrl}
                            data-type="video"
                            data-stories={item.stories}
                            data-filter-id={item.filterId}
                            data-network={item.networkId}
                            data-link={item.link} 
                            data-item-id={item.id}
                            data-load={0}
                            config={{
                                facebook: {
                                    appId: '117029975354072'
                                }
                                }}
                            onError={(e) => { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO(e) }}
                            autoPlay
                            controls
                        /></div></div>
                            : item.networkId == 1 || item.networkId == 2 || item.networkId == 18 || item.networkId == 8 ?
                                <Iframe
                                    className="ts-detail-image-iframe"
                                    frameBorder="0"
                                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                                    src={`${videoUrl}`}
                                    allowFullScreen="true"
                                    style={{ width: "100%!important" }}
                                    onError={(e) => { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO(e) }}
                                    data-type="video"
                                    data-stories={item.stories}
                                    data-filter-id={item.filterId}
                                    data-network={item.networkId}
                                    data-link={item.link} 
                                    data-item-id={item.id}
                                    data-load={0}
                                ></Iframe>
                                :
                                (
                                    <div className="tb_img_holder"><div className="tb_modal_img_holder_001">
                                        <div className="tb_video_wrap">
                                            <video
                                                src={videoUrl}
                                                autoPlay
                                                controls
                                                data-type="video"
                                                data-stories={item.stories}
                                                data-filter-id={item.filterId}
                                                data-network={item.networkId}
                                                data-link={item.link}
                                                onError={(e) => { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO(e) }}
                                                data-load={0}
                                                poster={item.postFileNew}
                                                data-wall-id={localStorage.getItem('wallId')} 
                                                data-item-id={item.id}
                                            ></video>
                                        </div>
                                    </div></div>
                                )
                }
            </> : null}

        </>
    }
}

export default Media;