import React, { Component } from 'react'
class Ratings extends Component {

    render() {
        const { rating } = this.props
        return <>
            {rating == 5 ? <div className="m_rating d-flex align-item-center justify-content-center py-3" ng-if="(rating == 5)">
                <span className="fas fa-star m_post_rating"></span>
                <span className="fas fa-star m_post_rating"></span>
                <span className="fas fa-star m_post_rating"></span>
                <span className="fas fa-star m_post_rating"></span>
                <span className="fas fa-star m_post_rating"></span>
            </div> : null}
            {rating == 4 ? <div className="m_rating d-flex align-item-center justify-content-center py-3" ng-if="(rating == 4)">
                <span className="fas fa-star m_post_rating"></span>
                <span className="fas fa-star m_post_rating"></span>
                <span className="fas fa-star m_post_rating"></span>
                <span className="fas fa-star m_post_rating"></span>
                <span className="far fa-star"></span>
            </div> : null}
            {rating == 3 ? <div className="m_rating d-flex align-item-center justify-content-center py-3" ng-if="(rating == 3)">
                <span className="fas fa-star m_post_rating"></span>
                <span className="fas fa-star m_post_rating"></span>
                <span className="fas fa-star m_post_rating"></span>
                <span className="far fa-star"></span>
                <span className="far fa-star"></span>
            </div> : null}
            {rating == 2 ? <div className="m_rating d-flex align-item-center justify-content-center py-3" ng-if="(rating == 2)">
                <span className="fas fa-star m_post_rating"></span>
                <span className="fas fa-star m_post_rating"></span>
                <span className="far fa-star"></span>
                <span className="far fa-star"></span>
                <span className="far fa-star"></span>
            </div> : null}
            {rating == 1 ? <div className="m_rating d-flex align-item-center justify-content-center py-3" ng-if="(rating == 1)">
                <span className="fas fa-star m_post_rating"></span>
                <span className="far fa-star"></span>
                <span className="far fa-star"></span>
                <span className="far fa-star"></span>
                <span className="far fa-star"></span>
            </div> : null}

        </>

    }
}

export default Ratings;