import React, { Component } from 'react'
import parse from 'html-react-parser'; 
import ErrorFound from '../../../../../../ErrorFound';

const SpanContainer = (props) => {
  return <span style={{ color: '#5354fc' }}><b>Read {props.id == 1 ? 'More' : 'Less'}</b></span>
}
export default class postcontent extends Component {
  state = { reactMore: false }


  render() {
    const { item } = this.props;
    const { reactMore } = this.state;
    const notMoreText = parse(item.postcontent).length > 80 ? true : false
    return (

    <> <ErrorFound>
      {notMoreText ?
          <><span className={`trimPostContent3 mb-1 ${reactMore ? 'trimPostContent2 ' : 'trimPostContent '}`} id={`gridpostContent${item.id}`}> {parse(item.postcontent)} </span>
            <span className="readMore_btn" onClick={() => this.setState({ reactMore: !reactMore })}><b> {reactMore ? 'Read Less' : '... Read More'}</b></span>
          </>
        :  <span id={`gridpostContent${item.id}`}>{parse(item.postcontent)}</span>
      }
      </ErrorFound>
      </>

    )
  }
}
