import moment from 'moment'
import React, { Component } from 'react'
import Postcontent from './postcontent'

export default class pinTopCard extends Component {
  render() {
      const { item, indexNo}= this.props
    return (
      <div className='d-flex mb-10 pt_dragable_card'>
        <div className='pt_drag_icon me-4'>
            <svg xmlns="http://www.w3.org/2000/svg" width="11" height="18" viewBox="0 0 11 18" fill="none">
                <path d="M0 1.96875C0 0.902344 0.861328 0 1.96875 0C3.03516 0 3.9375 0.902344 3.9375 1.96875C3.9375 3.07617 3.03516 3.9375 1.96875 3.9375C0.861328 3.9375 0 3.07617 0 1.96875ZM0 8.53125C0 7.46484 0.861328 6.5625 1.96875 6.5625C3.03516 6.5625 3.9375 7.46484 3.9375 8.53125C3.9375 9.63867 3.03516 10.5 1.96875 10.5C0.861328 10.5 0 9.63867 0 8.53125ZM3.9375 15.0938C3.9375 16.2012 3.03516 17.0625 1.96875 17.0625C0.861328 17.0625 0 16.2012 0 15.0938C0 14.0273 0.861328 13.125 1.96875 13.125C3.03516 13.125 3.9375 14.0273 3.9375 15.0938ZM6.5625 1.96875C6.5625 0.902344 7.42383 0 8.53125 0C9.59766 0 10.5 0.902344 10.5 1.96875C10.5 3.07617 9.59766 3.9375 8.53125 3.9375C7.42383 3.9375 6.5625 3.07617 6.5625 1.96875ZM10.5 8.53125C10.5 9.63867 9.59766 10.5 8.53125 10.5C7.42383 10.5 6.5625 9.63867 6.5625 8.53125C6.5625 7.46484 7.42383 6.5625 8.53125 6.5625C9.59766 6.5625 10.5 7.46484 10.5 8.53125ZM6.5625 15.0938C6.5625 14.0273 7.42383 13.125 8.53125 13.125C9.59766 13.125 10.5 14.0273 10.5 15.0938C10.5 16.2012 9.59766 17.0625 8.53125 17.0625C7.42383 17.0625 6.5625 16.2012 6.5625 15.0938Z" fill="#BABFC3"/>
            </svg>
        </div>
        <div className='pt_card_iamge position-relative'>
            <img src={item.newMedia} alt='' />
            <span className='pt_card_outer'>
                <span className='pt_card_number position-relative'>{indexNo+1}</span>
            </span>
        </div>
        <div className='pt_Card_content w-100 ms-3'>
            <div className='d-flex align-items-center justify-content-between pt_author_outer mb-1'>
                <div className='d-flex align-items-center m_grid_author'>
                    <div className='flex-shrink-0 mr-4'>
                        <div className='symbol symbol-circle symbol-lg-75 pt_profile_img' style={{ backgroundImage: `url('${item.picture}')`}}></div>
                    </div>
                    <div className='d-flex flex-column m_user_content'>
                        <div className='m_post_title'>{item.postname}</div>
                        <div className='d-flex align-item-center m_grid_userinfo'>
                            <span className='m_grid_uername text-nowrap'>
                                {item.postprefix}<span>{item.postname}</span>
                            </span>
                            <span className='sepratedot'></span>
                            <span className='timePost text-nowrap'>{ moment(new Date(item.createdAt * 1000)).fromNow()}</span>
                        </div>
                    </div>
                </div>
                <div className='d-flex flex-column m_social_icon'>
                <i className={`fab ${item.neticon}`}></i>
                </div>
            </div>
            <div className='m_des pintopdesc'>
                {/* <span className='trimPostContent3 mb-1 trimPostContent'> */}
                    <Postcontent item={item}/>
                {/* </span> */}
            </div>
        </div>
      </div>
    )
  }
}
