import { Component } from "react";
import { DashboardModal, Dashboard } from "@uppy/react";
import $ from "jquery";

import { Dropdown, Form } from "react-bootstrap";
import CreateTemplate from "../modal/createTemplate";
import { connect } from "react-redux";
import { DELETE_UGC_TEMPLATE } from "../../../../../../../../actions/moderationActions";
import { UGC_UPLOAD_URL, CLOUDPATH, CLOUDPATH_TAGGSHOP } from "../../../../../../../../actions/api";
import UppyUpload from "../../../../../../../../utils/uppyUpload";


class GeneralRequest extends Component {
    
    state = { postauthor: null, posthandle: null, authorpicture: null, postcarousel: false, content: null, data: null, modalOpen: false, createTemplate: false, brandFilename: null, selectedBrand: "", selectedBrandFile: null, tncFilename: null, selectedtnc: "", selectedtncFile: null, selectedTemplate: this.props.selectedTemplate, tncError: this.props.tncError, custom_message: this.props.custom_msg, isEdit: false, editData: {} };

    uppyUploader = new UppyUpload()
    uppyUploaderPdf = new UppyUpload(['application/pdf', 'application/vnd.openxmlformats-officedocument.wordprocessingml.document'])
    componentDidMount() {
        const { ugcData ,  setBrand_link, setTermslink , setTermstype, brand_link} = this.props

        this.uppyUploader.uppy.on("transloadit:result", (stepName, result) => {
            setBrand_link(`https://cloud.tagshop.ai/tmp/${result.id}.${result.ext}`)
            this.setState(
                {
                    selectedBrandFile: `${result.id}.${result.ext}`,
                    brandFilename: result.name,
                    selectedBrand: `https://cloud.tagshop.ai/tmp/${result.id}.${result.ext}`,
                },
                () => {
                    setTimeout(() => {
                        this.uppyUploader.uppy.cancelAll();
                        this.setState({ brandModalOpen: false });
                    }, 300);
                }
            );
        });

        this.uppyUploaderPdf.uppy.on("transloadit:result", (stepName, result) => {
            this.setState(
                {
                    selectedtncFile: `${result.id}.${result.ext}`,
                    tncFilename: result.name,
                    selectedtnc: `https://cloud.tagshop.ai/tmp/${result.id}.${result.ext}`,
                },
                () => {
                    setTimeout(() => {
                        this.uppyUploaderPdf.uppy.cancelAll();
                        this.setState({ tncModalOpen: false });
                    }, 300);
                }
            );
            setTermslink(`https://cloud.tagshop.ai/tmp/${result.id}.${result.ext}`)
            setTermstype(result.name)
        });

        this.onTemplateChange(this.state.selectedTemplate)
        setTimeout(() => {
        if(ugcData && ugcData.template && ugcData.template.length && !this.state.selectedTemplate) {
            this.setState({tncFilename : ugcData.template[0].custom_terms_name, selectedtnc: ugcData.template[0].custom_terms, selectedtncFile:ugcData.template[0].custom_terms })
        }     
        if(brand_link) this.setState({selectedBrandFile: brand_link}, () => console.log('brand_link', brand_link, this.state.selectedBrandFile))
        }, 500);

    }

    onClickToOpenBrand = () => {
        this.setState({ brandModalOpen: true });
        $(".uppy-Dashboard-close").click(() => {
            this.uppyUploader.uppy.cancelAll();
            this.setState({ brandModalOpen: false });
        });
    };
    onClickToCloseBrand = () => {
        this.uppyUploader.uppy.cancelAll();
        this.setState({ brandModalOpen: false });
    };

    onClickToOpenTnc = () => {
        this.setState({ tncModalOpen: true });
        $(".uppy-Dashboard-close").click(() => {
            this.uppyUploaderPdf.uppy.cancelAll();
            this.setState({ tncModalOpen: false });
        });
    };
    onClickToCloseTnc = () => {
        this.uppyUploaderPdf.uppy.cancelAll();
        this.setState({ tncModalOpen: false });
    };

    onCreateTemplate = () => {
        this.setState({ createTemplate: true })
    }

    onAddNewTemplate = (id) => {
        console.log('id',id)
        const tmp = this.getSelectedTemplate(id) 
        if(tmp) this.onTemplateChange(id)
        // if(tmp) this.setState({selectedTemplate: id})
        this.setState({ createTemplate: false, isEdit: false ,editData: {} })
    }

    onHideTemplate = () => {
        this.setState({ createTemplate: false, isEdit: false ,editData: {} })
    }
    getSelectedTemplate = (value) => {
        const { ugcData } = this.props
        const { selectedTemplate } = this.state
        const id = value ? value : selectedTemplate

        if (ugcData && ugcData.template && ugcData.template.length) {
            if (!id) { return ugcData.template[0] }
            const template = ugcData.template.filter(t => t.id == id)[0]
            return template
        }
        else return null
    }

    onNextStep = () => {
        const { acceptTnc, requestMode } = this.props
        if(requestMode == 1) {
            console.log('here')
            this.props.setStep(2)
        }
        else if (acceptTnc && requestMode == 0) {
            console.log('qwe')
            this.props.setStep(2)
        }
        else this.setState({ tncError: true }, () => this.props.setTncError(true))
    }
        //     this.setState({tncFilename : ugcData.template[0].custom_terms_name, selectedtnc: ugcData.template[0].custom_terms,
        //  selectedtncFile:ugcData.template[0].custom_terms, custom_message: ugcData.template[0].custom_message }, () => this.props.setCustom_msg(this.state.custom_message))

    onTemplateChange = (e) => {
        const tmp = this.getSelectedTemplate(e)
        if(tmp){
            this.setState({ selectedTemplate: e }, () => {
              this.setState({ custom_message: tmp.custom_message,
                tncFilename: tmp.custom_terms_name,
                selectedtnc: tmp.custom_terms,
                selectedtncFile:tmp.custom_terms,
                brandFilename: tmp.client_logo_name,
        selectedBrand: tmp.client_logo,
        selectedBrandFile: tmp.client_logo,

            });
              this.props.setCustom_msg(tmp?.custom_message);
              this.props.setSelectedTemplate(e);
            });

        }
    }

    onTemplateRemove = (data) => {
        this.props.deleteTemplate({owner: data.owner, id: data.id}, () => this.onTemplateChange(1))
    }

    onTemplateEdit = (data) => {
        this.onCreateTemplate()
        this.setState({isEdit: true, editData: data})
    }

    removeBrandLogo = () => {
        this.setState({selectedBrand: "", selectedBrandFile: null, brandFilename: null})
      }
      removeTnc = () => {
        this.setState({tncFilename: null,
            selectedtnc: null,
            selectedtncFile:null})
      }


    render() {
        const {
            brandModalOpen,
            tncModalOpen,
            createTemplate,
            selectedBrand,
            selectedBrandFile,
            tncFilename,
            selectedtncFile, selectedTemplate,
            tncError,
            custom_message,
            isEdit,
            editData,
            brandFilename
        } = this.state;

        const { ugcData, setCustom_msg, setAcceptTnc , acceptTnc, hideUgc, setBrand_link, setTermslink, setTermstype, requestMode} = this.props
            //   setBrand_link(selectedBrandFile)
            //  setTermslink(ugcData.template[0].custom_terms) 
            // setTermstype(ugcData.template[0].custom_terms_name) 

        return (
            <div className="ugc_req card card-custom shadow-none" id="myTabContentMD">
                <div id="home-md">
                    {ugcData && ugcData.template && ugcData.template.length ? 
                    <div className="ugc_steps ugc-flexgrow modal-body">
                        {requestMode==0 ? <div className="form-group ugc_right_col">
                            <p className="m-0 d-flex flex-wrap align-items justify-content-between">
                                <label>Choose From Templates</label>
                                <button type="button" className="btn btn-link create-temp p-0 mb-2" onClick={this.onCreateTemplate}>
                                    <i className="fa fa-plus"></i>
                                    Create Template
                                </button>
                            </p>
                            <div>
                                <Dropdown className="ugc-templates" onSelect={(e) => this.onTemplateChange(e)}>
                                    <Dropdown.Toggle className="btn btn-outline-secondary dropdown-toggle w-100 text-left" >
                                        <span id="temp-name">{this.getSelectedTemplate()?.name}</span> 
                                    </Dropdown.Toggle>

                                    <Dropdown.Menu className="w-100">
                                        {ugcData.template && ugcData.template.map(t => (

                                            <Dropdown.Item eventKey={t.id} key={`drp_${t.id}`}><span>{t.name}</span> {t.owner != 0 ?
                                                <span className="dropdown-option">
                                                <button type="button" className="btn p-0 edit-temp" data-id="151" onClick={() => this.onTemplateEdit(t)}>
                                                    <span className="svg-icon svg-icon-xs">
                                                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                                            <g data-name="Group 4328" transform="translate(10956 -4501)">
                                                                <rect id="Rectangle_2068" data-name="Rectangle 2068" width="24" height="24"
                                                                    transform="translate(-10956 4501)" fill="none" opacity="0"></rect>
                                                                <g id="Group_3906" data-name="Group 3906" transform="translate(-10953.868 4503.559)">
                                                                    <path id="Path_5042" data-name="Path 5042"
                                                                        d="M3.2,14.952,2.3,18.872a.985.985,0,0,0,.956,1.195,1.023,1.023,0,0,0,.206,0L7.4,19.158l7.571-7.543L10.746,7.4Z"
                                                                        transform="translate(0 -2.914)" fill="#545454"></path>
                                                                    <path id="Path_5043" data-name="Path 5043"
                                                                        d="M27.5,4.926l-2.82-2.82a.989.989,0,0,0-1.4,0L21.72,3.673l4.221,4.221,1.568-1.568a.989.989,0,0,0,0-1.4Z"
                                                                        transform="translate(-10.152 0)" fill="#545454"></path>
                                                                </g>
                                                            </g>
                                                        </svg>
                                                    </span>
                                                </button>
                                                <button type="button" className="btn p-0 del-temp" data-id="151" onClick={() => this.onTemplateRemove(t)}>
                                                    <span className="svg-icon svg-icon-xs">
                                                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                                            <g data-name="Group 4331" transform="translate(10956 -4533)">
                                                                <rect id="Rectangle_2069" data-name="Rectangle 2069" width="24" height="24"
                                                                    transform="translate(-10956 4533)" fill="none" opacity="0"></rect>
                                                                <g id="Group_4324" data-name="Group 4324" transform="translate(-10952 4536)">
                                                                    <path id="Path_5188" data-name="Path 5188"
                                                                        d="M2.722,16.328A2,2,0,0,0,4.694,18h6.612a2,2,0,0,0,1.972-1.672L14,6H2ZM9,9.5A.5.5,0,0,1,9.5,9h1a.5.5,0,0,1,.5.5v5a.5.5,0,0,1-.5.5h-1a.5.5,0,0,1-.5-.5Zm-4,0A.5.5,0,0,1,5.5,9h1a.5.5,0,0,1,.5.5v5a.5.5,0,0,1-.5.5h-1a.5.5,0,0,1-.5-.5ZM15,2H11A2,2,0,0,0,9,0H7A2,2,0,0,0,5,2H1A1,1,0,0,0,1,4H15a1,1,0,0,0,0-2Z"
                                                                        fill="#545454" fillRule="evenodd"></path>
                                                                </g>
                                                            </g>
                                                        </svg>
                                                    </span>
                                                </button>
                                            </span>
                                            : null
                                            } </Dropdown.Item>
                                        ))}
                                    </Dropdown.Menu>
                                </Dropdown>
                            </div>
                        </div> : <></>}
                        <div className="form-group ugc_right_col">
                            <h3 className="mb-2 fs-7"><span className="fw-medium">Message 
                                <strong className="text-danger">*</strong></span>
                            </h3>
                            <div className="mb-4">
                                <textarea name="data[custom_message]" value={custom_message} onChange={(e) => this.setState({custom_message : e.target.value}, () => setCustom_msg(e.target.value))} rows="4" cols="3" className="form-control form-control-solid textarea-min-row-8" id="custom_message">
                                    {this.getSelectedTemplate()?.custom_message ? this.getSelectedTemplate()?.custom_message : `We loved your creative brilliance! Can we showcase your incredible work and give you the recognition you deserve? . 
                                    Please approve our request so we can use your content in our marketing campaign.`}
                                </textarea>
                            </div>
                        </div>
                        {requestMode==0 ?
                        <>
                        <div className="ugc_right_col clearfix" id="bannerLogoDiv">
                            <div className="form-group">
                                <div className="row">
                                    <div className="col-xs-12 col-sm-12 col-md-12 col-lg-12">
                                        <label>Brand Logo</label>
                                    </div>
                                    <div className="col-12">
                                        {!selectedBrandFile ?
                                            <div className="logo-upload-section">
                                                <span className="dashed_ugc_btn ugc_upload_logo_btn_main">
                                                    <span className="fileinput-new ugc_upload_logo_btn d-flex align-items-center" onClick={() => this.onClickToOpenBrand()}>
                                                        <p style={{ textTransform: 'capitalize' }}>Upload Logo</p>
                                                        <input type="hidden" name="" autoComplete="off" className="form-control form-control-lg validate" id="brandlogoN" value="" />
                                                        <input name="" autoComplete="off" className="form-control validate" id="brandlogo" maxLength="255" type="text" />
                                                    </span>
                                                </span>
                                            </div>
                                            :
                                            <div className="logo-image-section" >
                                                <div className="brand_logo_file ugc-brandlogo-file thumbnail d-flex align-items-center justify-content-between w-100">
                                                    <div className="d-flex align-items-center justify-content-start">
                                                    <img src={this.getSelectedTemplate()?.client_logo ? this.getSelectedTemplate()?.client_logo?.includes('https://cloud.tagshop.ai') ? this.getSelectedTemplate()?.client_logo : `${CLOUDPATH}${UGC_UPLOAD_URL}/${this.getSelectedTemplate()?.client_logo}` : selectedBrand} className="brand_logo_file_img_ugcright bannerpreviewImg" />
                                                        <span className="ugc-brandimg-name">{brandFilename}</span>
                                                    </div>
                                                    <button type="button" className="btn p-0 m-2 removelogo" onClick={() => this.removeBrandLogo()}>
                                                        <i className="fa fa-times"></i>
                                                    </button>
                                                </div>
                                            </div>}
                                    </div>
                                </div>
                            </div>
                            <div className="form-group">
                                <label>Terms and Conditions</label>
                                <div className="input-group">
                                    <input name="data[Template][ugcterms]" autoComplete="off" className="form-control form-control-lg validate" id="ugcterms" type="text" />
                                    <input type="hidden" name="data[Template][custom_terms]" autoComplete="off" className="form-control validate" id="ugctermsN" value="38a4b61b4b5c4ccf897249c2cd21c9f4" />
                                    <input type="hidden" name="data[Template][custom_terms_name]" autoComplete="off" className="form-control validate" id="ugctermstype" value="Terms&amp;ConditionsforuseofUGC.pdf" />
                                    {tncFilename ? <div className="clearterms" style={{}}>
                                        <span>{tncFilename}</span>
                                        <i className="fa fa-times removeterms" onClick={() => this.removeTnc()}></i>
                                    </div> : ''}
                                    <div className="input-group-append" id="ugctermssection">
                                        <span className="input-group-text cursor-pointer" onClick={this.onClickToOpenTnc}>Attach File</span>
                                    </div>
                                </div>
                                <div className="d-flex align-items-center justify-content-between mt-2">
                                    <span id="terms_error" className="errorMessage required"></span>
                                    <div className="ugc_dummy_file_download">
                                        <a href={`${CLOUDPATH_TAGGSHOP}/app/assets/media/Terms&ConditionsforuseofUGC.docx`} target="_blank" className="btn btn-link p-0 text-nowrap" download="">
                                            Use our predefined content
                                            <span className="svg-icon svg-icon-md">
                                                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                                    <g id="Group_4422" data-name="Group 4422" transform="translate(10979 -4685)">
                                                        <rect id="Rectangle_2094" data-name="Rectangle 2094" width="24" height="24" transform="translate(-10979 4685)" fill="none" opacity="0"></rect>
                                                        <g id="Group_4410" data-name="Group 4410" transform="translate(-10979 4685)">
                                                            <path id="Path_5217" data-name="Path 5217" d="M11.291,15.4a.989.989,0,0,0,.7.283,1.108,1.108,0,0,0,.776-.331L15.9,12.209a1.016,1.016,0,1,0-1.436-1.437L13.02,12.217V4.057A1.016,1.016,0,0,0,12,3.036a1.027,1.027,0,0,0-1,1.02v8.172l-1.457-1.46a1.038,1.038,0,0,0-1.436,0,1.014,1.014,0,0,0,0,1.436Z" fill="#545454"></path>
                                                            <path id="Path_5218" data-name="Path 5218" d="M21.083,18H2.917a.99.99,0,0,0-.984.982v.006a1,1,0,0,0,.985.981H21.083a.985.985,0,0,0,0-1.969Z" fill="#545454"></path>
                                                        </g>
                                                    </g>
                                                </svg>
                                            </span>
                                        </a>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="form-group ugc_right_col">
                            <div className="checkbox-inline">
                                <label className="ugc_terms_conditions checkbox align-items-start checkbox-outline checkbox-outline-2x checkbox-primary mr-0">
                                    <input type="checkbox" name="term" id="term" value={acceptTnc} onChange={(e) => setAcceptTnc(e.target.checked)} checked={acceptTnc ? true : false}/>
                                    <span></span>
                                    <p className="mb-1">
                                        Accept our
                                        <b>
                                            <a href="https://tagshop.ai/terms-of-service/" rel="noreferrer"  target="_blank">Terms &amp; Conditions</a>
                                        </b>. Click on Generate Request to proceed to next step.
                                    </p>
                                </label>
                            </div>
                            {tncError && !acceptTnc ? <span id="term_error" className="errorMessage required">Please Select Terms and Conditions</span> : null}
                        </div>
                        </> : <></>}
                        
                    </div> : null}
                    <div className="ugc-peingdone modal-footer text-right">
                        <input className="btn btn-secondary" type="button" value="Back" onClick={() => this.props.setStep(4)}/>
                        <input className="btn btn-primary" type="button" value="Next Step" onClick={() => this.onNextStep()} />
                    </div>
                </div>

                    <DashboardModal
                        animateOpenClose={true}
                        proudlyDisplayPoweredByUppy={false}
                        inline={false}
                        showProgressDetails={true}
                        height={750}
                        browserBackButtonClose={true}
                        showRemoveButtonAfterComplete={false}
                        uppy={this.uppyUploader.uppy}
                        open={brandModalOpen}
                        onRequestCloseModal={this.onClickToCloseBrand}
                        closeAfterFinish={true}
                        doneButtonHandler={this.doneButtonHandler}
                        plugins={["Instagram", "GoogleDrive", "Url", "Webcam"]}
                    />


                    <DashboardModal
                        animateOpenClose={true}
                        proudlyDisplayPoweredByUppy={false}
                        inline={false}
                        showProgressDetails={true}
                        height={750}
                        browserBackButtonClose={true}
                        showRemoveButtonAfterComplete={false}
                        uppy={this.uppyUploaderPdf.uppy}
                        open={tncModalOpen}
                        onRequestCloseModal={this.onClickToCloseTnc}
                        closeAfterFinish={true}
                        doneButtonHandler={this.doneButtonHandler}
                        plugins={["Url", "Webcam"]}
                    />
        

                {createTemplate ? <CreateTemplate show={true} edit={isEdit} editData={editData} onHide={this.onHideTemplate} onAddNewTemplate={this.onAddNewTemplate} /> : null}

            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        // ugcData: state.moderation.ugcData,
    }
}
const mapDispatchToProps = dispatch => {
    return {
        deleteTemplate: (data, callback) => dispatch(DELETE_UGC_TEMPLATE(data, callback))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(GeneralRequest);
