import React from 'react'
import parse from "html-react-parser";
var output = '';
for (var count = 0; count < 20; count++) {
    output += '<div class="m_grid_card">';
    output += '<div class="card card-custom gutter-b home-widget-section">';
    output += '<div class="sk_loader_">';
    output += '<div class="wall_progress card-body p-0">';
    output += '<div class="d-flex align-items-center justify-content-between mb-2 sk_img_thumb sk_img_thumb_lg"></div>';
    output += '<div class="card p-2 border-0">';
    output += '<div class="d-flex align-items-center mb-2">';
    output += '<div class="d-flex align-items-center m_grid_author overflow-hidden justify-content-left w-100">';
    output += '<div class="flex-shrink-0 mr-4 mt-lg-0 mt-3">';
    output += '<div class="sk_img"></div>';
    output += '</div>';
    output += '<div class="m_user_content w-100">';
    output += '<h4 class="sk_line sk_line_70 sk_line_height_12"></h4>';
    output += '<p class="sk_line sk_line_40 sk_line_height_8 mb-0"></p>';
    output += '</div>';
    output += '</div>';
    output += '</div>';
    output += '<p class="sk_line sk_line_height_8 mb-0"></p>';
    output += '<p class="sk_line sk_line_height_8 mb-0 sk_line_90"></p>';
    output += '<p class="sk_line sk_line_height_8 mb-0 sk_line_80"></p>';
    output += '<div class="d-flex m_bottom_actions border-0 pt-2 mt-3">';
    output += '<div class="sk_btn sk_line w-50 rounded-1 mb-0 mr-1"></div>';
    output += '<div class="sk_btn sk_line w-50 rounded-1 mb-0"></div>';
    output += '</div>';
    output += '</div>';
    output += '</div>';
    output += '</div>';
    output += '</div>';
    output += '</div>';
}
const ModerationSkeleton = () => {
    return <div className="row mt-30" id="moderationSkeleton">
        {parse(output)}
    </div>
}
export default ModerationSkeleton