import React, { Component } from 'react'
import { Modal } from "react-bootstrap";
import { fetchCtaData } from '../../../../../../actions/moderationActions'
import PickColor from './pickColor';

class CtaModal extends Component {
    state = { button: "", buttonText: "", buttonUrl: "", ctaButtonColor: "", ctaTextColor: "", cta_id: null, owner: null, postId: null, status: null, wallId: null, data: null, action: "" }
    componentWillMount() {
        const { ctaReferenceID, details, itemData } = this.props;
        fetchCtaData(ctaReferenceID).then((response) => {
            const { data, ctastatus } = response.data;
            if (ctastatus == 1) {
                this.setState({
                    buttonText: data.buttonText,
                    buttonUrl: data.buttonUrl,
                    ctaButtonColor: data.ctaButtonColor,
                    ctaTextColor: data.ctaTextColor,
                    cta_id: data.cta_id,
                    owner: data.owner,
                    postId: data.postId,
                    status: data.status,
                    wallId: data.wallId,
                    data,
                    button: `Update Cta`,
                    action: "update"
                })
            }
            else {
                if (details && details?.wall && details?.wall.Personalization) { 
                    const { buttonText, buttonUrl, ctaButtonColor, ctaTextColor, cssStatus } = details?.wall.Personalization;
                    this.setState({
                        buttonText: buttonText,
                        buttonUrl: buttonUrl,
                        ctaButtonColor: ctaButtonColor ? ctaButtonColor : '#000000',
                        ctaTextColor: ctaTextColor ? ctaTextColor : '#ffffff',
                        cta_id: ctaReferenceID,
                        owner: localStorage.getItem('ownerId'),
                        postId: itemData.spostId,
                        status: cssStatus,
                        wallId: localStorage.getItem('wallId'),
                        data,
                        button: `Add Cta`,
                        action: "insert"
                    })
                }
                else this.setState({ctaTextColor: '#ffffff', ctaButtonColor: '#000000'})
            }
        })
    }
    componentDidMount() {
        setTimeout(() => {
            var self = this;
            window.$('#ctaTextColor').minicolors({ control: 'hue', defaultValue: self.state.ctaTextColor, letterCase: 'lowercase', position: 'bottom left', change: self.onTextColorChange, theme: 'bootstrap' });
            window.$('#ctaButtonColor').minicolors({ control: 'hue', defaultValue: self.state.ctaButtonColor, letterCase: 'lowercase', position: 'bottom left', change: self.onButtonColorChange, theme: 'bootstrap' });
        }, 1000)


    }
    onButtonTextChange = event => this.setState({ buttonText: event.target.value })
    onTextColorChange = (hex, opacity) => {
        if (!hex)
            return;
        this.setState({ ctaTextColor: hex })
    }
    onButtonColorChange = (hex, opacity) => {
        if (!hex)
            return;
        this.setState({ ctaButtonColor: hex })
    }
    onUrlChange = event => this.setState({ buttonUrl: event.target.value })
    onStatusChange = event => this.setState({ status: event.target.checked })

    onUpdateCTA = event => {
        const { updateCtaPost, postID, id, clickToCloseAddCta, itemIndex, postStatus, itemData, filter } = this.props
        const { data, buttonText, buttonUrl, ctaButtonColor, ctaTextColor, cta_id, owner, postId, status, wallId, action } = this.state;
        let editData = {
            id: itemData.referenceId, textColor: ctaTextColor, buttonText: buttonText, buttonColor: ctaButtonColor, url: buttonUrl, status: status ? 1 : 0, owner: localStorage.getItem('ownerId'),
            wallId: localStorage.getItem('wallId'), offset_start: 0, postStatus: "", feedId: "", planId: filter.planId,
            action
        }
        updateCtaPost(itemIndex, editData, clickToCloseAddCta);
    }
    render() {
        const { open, clickToCloseAddCta, itemData } = this.props;
        const { buttonText, buttonUrl, ctaButtonColor, ctaTextColor, cta_id, owner, postId, status, wallId, button } = this.state;

        return <Modal show={open} onHide={clickToCloseAddCta} backdrop="static" keyboard={false} size={""}>
            <Modal.Header closeButton> <h4 className="modal-title">Call To Action</h4></Modal.Header>
            <Modal.Body>
                <div className="panel-body">
                    <div className="col-md-12">
                        <div className="form-body">
                            <div className="form-group">
                                <label className="control-label">Button text
                                    <span className="required"> * </span></label>
                                <div className="">
                                    <input type="hidden" id="edit_cta_post_id" value="" />
                                    <input type="hidden" id="edit_cta_action" value="" />
                                    <input type="hidden" id="wallOwner" value="" />
                                    <input type="hidden" id="editplanId" value="<?php echo $planId; ?>" />
                                    <input name="buttontext" className="form-control" placeholder="Button Text" type="text" id="buttontext" aria-required="true" value={buttonText} onChange={this.onButtonTextChange} />
                                    <div className="help-block email-error"></div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="form-group col-md-6">
                                    <label className="control-label"> Text Color <span className="required"> * </span> </label>
                                    <div className="">
                                        {!ctaTextColor ? <input name="ctaTextColor" className="form-control miniColors init" type="text" />
                                        :
                                        <PickColor id="ctaTextColor" defaultColor={ctaTextColor} changeColor={(clr) => { this.onTextColorChange(clr) }}  />}
                                    </div>
                                </div>
                                <div className="form-group col-md-6">
                                    <label className="control-label"> Button Color <span className="required"> * </span> </label>
                                    <div className="">
                                        {!ctaButtonColor ? <input name="ctaButtonColor" className="form-control miniColors init" type="text" />
                                        :
                                        <PickColor id="ctaButtonColor" defaultColor={ctaButtonColor} changeColor={(clr) => { this.onButtonColorChange(clr) }}  />}
                                    </div>
                                </div>
                            </div>
                            <div className="form-group">
                                <label className="control-label"> Url <span className="required"> * </span> </label>
                                <div className="">
                                    <input name="buttonUrl" className="form-control" id="url" maxLength="255" type="url" required="required" value={buttonUrl} onChange={this.onUrlChange} />
                                </div>
                            </div>
                            <div className="form-group row">
                                <label className="col-md-6 control-label"> Status </label>
                                <div className="col-md-6">
                                    <div align="center" className="toggleOnBut">
                                        <span className="switch justify-content-center">
                                            <label htmlFor="CtaStatus">
                                                <input onChange={this.onStatusChange} type="checkbox" name="data[status]" id="CtaStatus" className="onoffswitch-checkbox updateCtaStatus init" data-on-color="#009385" data-off-color="#989898" value={status} checked={status} data-gtm-form-interact-field-id="0" />
                                                <span></span>
                                            </label></span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </Modal.Body>
            <Modal.Footer>
                <div className="d-flex align-items-center justify-content-end w-100">
                    <div className="card-toolbar">
                        <button type="button" className="btn btn-secondary mr-4" data-bs-dismiss="modal" onClick={clickToCloseAddCta}>Cancel</button>
                        <button type="submit" className="btn btn-primary spinner-white spinner-right CtaButton" onClick={this.onUpdateCTA}><i className="fa fa-plus"></i> {button}</button>
                    </div>
                </div>
            </Modal.Footer>
        </Modal >
    }
}
export default CtaModal