import React, { Component } from "react";
import reactImageSize from 'react-image-size';
import Draggable from 'react-draggable';

export default class ImageUrl extends Component {

  render() {
    const { ImageClass, ImageUrl, item, enableHotspot, w, h, productId, onControlledDrag, changePrdId, imgHeight, imgWidth, ctaData } = this.props
    const Image = { height: imgHeight > imgWidth ? '100%' : imgHeight + '%', width: imgWidth > imgHeight ? '100%' : imgWidth + '%' };
    return (<div className={`hotspotDiv ${ImageClass}`} id="hotspotSection" > 
      {enableHotspot == 1 && item.postproduct && item.postproduct.length > 0 && w ?
        item.postproduct.map((tagPrd, index) => {
          let lft = (w * tagPrd.hotspot_cordinates.left.replace("%", "")) / 100;
          let tp = (h * tagPrd.hotspot_cordinates.top.replace("%", "")) / 100;
          return <Draggable bounds="parent" onStop={onControlledDrag} key={`hotspot_${tagPrd.ugc_product}`} onStart={() => changePrdId(tagPrd.ugc_product)} defaultPosition={{ x: lft, y: tp }}>
            <div data-post={tagPrd.ugc_product} className="change-hotspot-position ui-draggable ui-draggable-handle" onChange={(e) => { console.log(e) }}>
              {ctaData?.hotspot_type == 1 ?
                <div className="blob_002" style={{ background: ctaData?.hotspot_color, color: ctaData?.hotspot_type_color }}> {index+1}</div>
                : <div className="blob_001" style={{ background: ctaData?.hotspot_color }}></div>
              }
              <div className="hotspot-summary-section" id="hotspot-summary-227390">
                <div className="hotspot-summary">
                  <a href="https://www.adidas.co.uk/ultraboost-19-shoes/F35284.html" target="_blank">{tagPrd.product && tagPrd.product.product_title ? tagPrd.product.product_title :''}</a>
                  <h3>{tagPrd?.product?.price_currency_symbol}{(tagPrd.product.product_price == tagPrd.product.product_discount ? tagPrd.product.product_price :  tagPrd.product.product_discount > 0 ?  tagPrd.product.product_discount :  tagPrd.product.product_price)}</h3>
                </div>
              </div>
            </div>
          </Draggable>
        }) : ''}
    </div>
    );
  }
}

