import React from 'react'
import { CLOUDPATH } from '../../../../../../../actions/api'
import { Link } from 'react-router-dom'
const NoPostBlock = ({feeds}) => {
    return <>
        <div className="noMobPosts-block h-100" id="noPosts" >
            <div className="text-center h-100">
                <div className="no_products h-100 d-flex align-items-center flex-column justify-content-center">
                    <img src={`${CLOUDPATH}widget/assets/media/products/product-empty.svg`} className="mb-10 img-fluid" />
                    {feeds && feeds.length ? 
                    <h4 className="h1 mb-4">No Posts!</h4>
                    :
                    <>
                    <h4 className="h1 mb-4">Whoops! You have not created a feed</h4>
                            <div className="buttonUpgred pb-5">
                                <Link className="btn btn-primary mt-7" to={`/content/addfeed/${localStorage.getItem('wallId')}`}>Create feed</Link>
                            </div>
                    </>

                }
                </div>
            </div>
        </div>
    </>
}
export default NoPostBlock