import { Component } from "react";

class AlertText extends Component {

    getAlertText = (id) => {
        switch (id) {
            case 2:
                return (
                    <div id="feed_note" className="alert alert-custom fade show mt-6 mb-0 bg-light rounded-0 p-3 align-items-start">
                        <div className="alert-icon py-1">
                            <span className="svg-icon svg-icon-sm">
                                <svg xmlns="http://www.w3.org/2000/svg" width="13" height="13" viewBox="0 0 13 13">
                                    <path id="Path_1660" fill="#959595" data-name="Path 1660" d="M17.5,11A6.5,6.5,0,0,0,11,17.5a6.64,6.64,0,0,0,2.472,5.1A6.265,6.265,0,0,0,17.5,24a6.5,6.5,0,0,0,0-13Zm.667,9.167a.667.667,0,0,1-1.333,0V17a.667.667,0,1,1,1.333,0ZM17.5,15.5a.667.667,0,1,1,.667-.667A.667.667,0,0,1,17.5,15.5Z" transform="translate(-11 -11)" opacity="0.999"></path>
                                </svg>
                            </span>
                        </div>
                        <div className="alert-text">
                            Initially hashtag posts from the last 30 days will be aggregated after which all the upcoming posts will be aggregated.
                        </div>
                    </div>
                )

            case 1:
                return (
                    <div id="feed_note" className="alert alert-custom fade show mt-6 mb-0 bg-light rounded-0 p-3 align-items-start">
                        <div className="alert-icon py-1">
                            <span className="svg-icon svg-icon-sm">
                                <svg xmlns="http://www.w3.org/2000/svg" width="13" height="13" viewBox="0 0 13 13">
                                    <path id="Path_1660" fill="#959595" data-name="Path 1660" d="M17.5,11A6.5,6.5,0,0,0,11,17.5a6.64,6.64,0,0,0,2.472,5.1A6.265,6.265,0,0,0,17.5,24a6.5,6.5,0,0,0,0-13Zm.667,9.167a.667.667,0,0,1-1.333,0V17a.667.667,0,1,1,1.333,0ZM17.5,15.5a.667.667,0,1,1,.667-.667A.667.667,0,0,1,17.5,15.5Z" transform="translate(-11 -11)" opacity="0.999"></path>
                                </svg>
                            </span>
                        </div>
                        <div className="alert-text">
                            Initially the latest 100 posts on the Twitter handle will be aggregated after which all the upcoming posts will be aggregated.
                        </div>
                    </div>
                )
            
            case 7:
                return(
                    <div id="feed_note" className="alert alert-custom fade show mt-6 mb-0 bg-light rounded-0 p-3 align-items-start">
                        <div className="alert-icon py-1">
                            <span className="svg-icon svg-icon-sm">
                                <svg xmlns="http://www.w3.org/2000/svg" width="13" height="13" viewBox="0 0 13 13">
                                    <path id="Path_1660" fill="#959595" data-name="Path 1660" d="M17.5,11A6.5,6.5,0,0,0,11,17.5a6.64,6.64,0,0,0,2.472,5.1A6.265,6.265,0,0,0,17.5,24a6.5,6.5,0,0,0,0-13Zm.667,9.167a.667.667,0,0,1-1.333,0V17a.667.667,0,1,1,1.333,0ZM17.5,15.5a.667.667,0,1,1,.667-.667A.667.667,0,0,1,17.5,15.5Z" transform="translate(-11 -11)" opacity="0.999"></path>
                                </svg>
                            </span>
                        </div>
                        <div className="alert-text">
                            Initially mentions of the last 7 days will be aggregated after which all the upcoming mentions will be aggregated.
                        </div>
                    </div>
                )

            case 3:
                return <></>

            case 4:
                return <></>

            case 5:
                return <></>

            default:
                return (<>error</>)
        }
    }

    render() {
        const { filterId } = this.props
        return (
            this.getAlertText(filterId)
        )
    }
}

export default AlertText