import React, { Component } from 'react'
import parse from 'html-react-parser'; 


export default class postcontent extends Component {
  state = { reactMore: false }


  render() {
    const { item } = this.props;
    const { reactMore } = this.state;
    const notMoreText = parse(item.postcontent).length > 80 ? true : false
    return (

      <>{notMoreText ?
        <><span className={` mb-1 trimPostContent ${reactMore ? 'active ' : ' '}`} id={`gridpostContent${item.id}`}>
          {/* {reactMore ? parse(item.postcontent) : parse(item.postcontent).slice(0,90) }   */}
          {parse(item.postcontent)}
          <span className="readMore_btn" onClick={() => this.setState({ reactMore: !reactMore })}><b> {reactMore ? 'Read Less' : '... Read More'}</b></span>
        </span>
          </>
        :
        <span id={`gridpostContent${item.id}`}>{parse(item.postcontent)}</span>
      }
      </>

    )
  }
}
