import React, { Component } from 'react'
import { YOUTUBE_AUTOCOMPLETE } from '../../../../../../../actions/addFeedActions'
import SearchPlace from './SearchPlace'
export default class YtTabs extends Component {

    state = {
        list: []
    }


    onYoutubeChange = query => {
        const { onValueChange, tab } = this.props
        if(["Play List", "Channel Url", "Shorts"].includes(tab)){
            try {
                const type = tab == "Play List" ? "playlist" : "channel"
                onValueChange(query)
                YOUTUBE_AUTOCOMPLETE(query, type).then(res => {
                    const { data, status } = res
                    if (status == 200) {
                        this.setState({ list: data.items })
                    }
                })
            } catch (error) {
                console.log(error)
            }
        }
        else onValueChange(query)
    }

    onListClick = (item) => {
        try {
            const { onValueChange, tab, onChannelIdChange, } = this.props
            onValueChange(tab == "Play List" ? item.snippet.title : item.snippet.channelTitle)
            onChannelIdChange(`${tab == "Play List" ? item.id.playlistId : tab == "Shorts" ? item.id.channelId.replace("UC", "UUSH") : item.id.channelId}`)
            // if(document.querySelector('#feedsfield1'))
            document.getElementById(`feedsfield-${tab}`).value = `https://www.youtube.com/${tab == "Play List" ? "playlist?list=" : "channel/"}${tab == "Play List" ? item.id.playlistId : tab == "Shorts" ? item.id.channelId.replace("UC", "UUSH") : item.id.channelId}`
            this.setState({list: []})
        } catch (error) {
            console.log("error", error)
        }
    }



    render() {
        const {
            tab,
            value,
            onValueChange,
            checkBox,
            onCheckBoxChange,
            onKeywordChange,
            keyword,
            error,
            channelId,
            onChannelIdChange,
            onLatlanChange
        } = this.props

        const { list } = this.state

        return (
            <>
                <div className="row">
                    <div className={`col-md-${tab == "Location" ? '6' : '12'}`}>
                        <div className="form-group">
                            {tab == "Location" ?
                                <SearchPlace onFieldTextChange={onValueChange} onLatlanChange={onLatlanChange} />
                                :
                                <div className="input-group">
                                    <div className="input-group-prepend">
                                        <span className="input-group-text">
                                            <i className={`fas fa-${tab == "Shorts" || tab == "Channel Url" ? 'at' : tab == "Play List" ? 'list' : tab == "Keywords" ? "font" : tab == "Location" ? "globe" : ""}`}></i>
                                        </span>
                                    </div>
                                    <input placeholder={`${tab == "Location" ? "" : "Enter"} ${tab == "Shorts" || tab == "Channel Url" ? "channel url" : tab == "Play List" ? "Play List ID" : tab == "Keywords" ? "keyword" : ""}`} className="autosearch mycheck form-control " id={`feedsfield-${tab}`} type="text"  onChange={e => this.onYoutubeChange(e.target.value)} />
                                    {list && list.length ? <div className='handlelist' style={{ display: "block" }}>
                                        <ul className="list-unstyled">
                                            {list.map(item => (
                                                <li data-handle="FdgnlfsSdfgsg" key={item.etag} onClick={() => this.onListClick(item)}>
                                                    <div className="img-section">
                                                        <img src={item.snippet.thumbnails.default.url}/>
                                                    </div>
                                                    <div className="content-section"><span>{item.snippet.channelTitle}</span>
                                                        <p>{`https://www.youtube.com/${tab == "Play List" ? "playlist?list=" : "channel/"}${tab == "Play List" ? item.id.playlistId : item.id.channelId}`}</p>
                                                    </div>
                                                </li>
                                            ))}

                                        </ul>
                                    </div> : null}
                                </div>
                            }
                            {error ? <span id="feedsfield1error" className="errorMessage required">This field is required</span> : null}
                        </div>
                    </div>
                    {tab == "Location" ?
                        <div className="col-md-6">
                            {/* <SearchPlace onFieldTextChange={onKeywordChange}/> */}
                            <div className="form-group">
                                <div className="input-group">
                                    <div className="input-group-prepend">
                                        <span className="input-group-text">
                                            <i className="fas fa-font"></i>
                                        </span>
                                    </div>
                                    <input placeholder="Enter keywords" id="" value={keyword} onChange={e => onKeywordChange(e.target.value)} className="form-control" type="text" />
                                </div>
                            </div>
                        </div>
                        : null
                    }

                </div>
                <div className="hideforBSearch">
                    <div className="checkbox-inline">
                        <label htmlFor={`${tab}_checkbox`} className="checkbox checkbox-outline checkbox-primary mr-2">
                            <input type="checkbox" checked={checkBox} onChange={e => onCheckBoxChange(e.target.checked)} id={`${tab}_checkbox`} className="checkbox checkbox-outline checkbox-outline-2x checkbox-primary mr-2" value="1" />
                            <span></span>
                            Manually approve posts before making them public?
                        </label>
                    </div>
                </div></>
        )
    }
}
