import { Modal } from "react-bootstrap";
import { CLOUDPATH_TAGGSHOP } from "../../../../../../../actions/api";

const ErrorModal = ({ show, onHide }) => {
  return (
    <>
      <Modal show={show} onHide={onHide}>
        <Modal.Header>
          <button className="border-0" onClick={onHide}><i aria-hidden="true" className="ki ki-close"></i></button>
        </Modal.Header>
        <Modal.Body>
          <div className="text-center">
            <img className="img-fluid" src={`${CLOUDPATH_TAGGSHOP}/app/assets/media/error/bulk-request.png`}/>
            <p className="text-center my-5">
              To send UGC Rights request in bulk, make sure you choose posts from a single user.
            </p>
            <button type="button" data-bs-dismiss="modal" aria-label="Close" className="btn btn-primary btn-min-w refresh-username" onClick={onHide}>OK</button>
          </div>
        </Modal.Body>
      </Modal>
    </>
  );
};

export default ErrorModal;
