/* eslint-disable eqeqeq */
import React, { PureComponent } from 'react'
import { Tab, Tabs } from 'react-bootstrap'
import { connect } from 'react-redux'
import { useNavigate } from 'react-router-dom'
import Swal from 'sweetalert2'
import { SELECT_ACCOUNT, SET_CREATE_FEED_OBJ } from '../../../../../../../actions/addFeedActions'
import { clickToRefresh, CREATE_FEED } from '../../../../../../../actions/moderationActions'
import { getFbCode } from '../../../../../../../utils/auth'
import FacebookTab from './facebookTab'
import { GET_VIDEO_URLS } from '../constant'
import { BASE_URL } from '../../../../../../../actions/api'

const Facebookfeed = (props) => {
    const navigation = useNavigate();
    return <FBfeed {...props} navigation={navigation} />;
}
class FBfeed extends PureComponent {
    state = { textField: "", account: null, album: '65', filterType: 'Page', authenticate: false, connetedAccount: null, authRequired: 1, moderation: false, error: false, isLoading: false, }

    componentDidMount() { this.props.onFilterNameChange(this.state.filterType) }

    onTextFieldChange = value => this.setState({ textField: value })
    onAccountChange = (value, parent) => {
        if (["Page Reviews", "Page Mentions"].includes(this.state.filterType)) {
            if (value == 0) this.props.SELECT_ACCOUNT(null)
            else this.props.SELECT_ACCOUNT(value)
        }
        if (parent && parent != null) { this.setState({ connetedAccount: parent }) }
        this.setState({ account: value });
    }
    onAlbumChange = value => this.setState({ album: value })
    onModerationChange = value => this.setState({ moderation: value })
    onConnectedAccountChange = (value) => this.setState({ connetedAccount: value })
    onAuthenticateChange = (value) => this.setState({ authenticate: value })

    onSubmit = () => {
        this.setState({ isLoading: true })
        this.createFeedObj(true)
        localStorage.setItem("addAcc", false)
    }

    createFeedObj = (navigate) => {
        const { textField, moderation, filterType, album, connetedAccount, account } = this.state
        const { navigation, feedInfo, SET_CREATE_FEED_OBJ, hideAddfeed } = this.props

        const ftr = feedInfo && feedInfo.length && feedInfo[0].networkTab && feedInfo[0].networkTab.length ? feedInfo[0].networkTab.findIndex(f => f.feedtype == filterType) : null
        const filterId = feedInfo[0]?.networkTab[ftr]?.filter

        localStorage.setItem("addAcc", false)

        if ((!textField || textField == "") && [8, 67].includes(filterId)) {
            this.setState({ error: true, isLoading: false })
        }
        else {
            this.setState({ error: false })

            var obj = {
                wallId: localStorage.getItem('wallId'),
                networkId: 3,
                moderation: moderation ? 1 : 0,
                connetedAccount: ![67, 65, 8, 1].includes(filterId) ? connetedAccount : 0,
                feedType: filterId == 67 ? parseInt(album) : parseInt(filterId),
                account: [62, 55].includes(filterId) ? account : connetedAccount,
                field: textField,
                filter: filterId == 67 ? parseInt(album) : parseInt(filterId),
                hashtagIGTV: 0,
                onlyReels: 0,
            }
            SET_CREATE_FEED_OBJ(obj)
            localStorage.setItem("createFeedObj", JSON.stringify(obj))
            localStorage.setItem("networkId", 3)

            if (navigate) {
                CREATE_FEED(obj, navigation, true, hideAddfeed)

            }
        }
    }



    onAddAccount = () => {
        localStorage.setItem("networkId", 3)
        localStorage.setItem("addAcc", false)
        localStorage.setItem("isV2", true)
        localStorage.setItem("selectedNetwork", 3)

        Swal.fire({
            imageUrl: 'https://cloud.taggbox.com/taggshop/app/assets/media/square-icons/facebook.svg?ver=1',
            title: '<strong>Facebook</strong>',
            html:
                'Connect to Facebook to pull the content for this feed.',
            showCloseButton: true,
            showCancelButton: false,
            focusConfirm: true,
            confirmButtonText:
                'Continue with Facebook',
            confirmButtonAriaLabel: 'Continue with Facebook',
            cancelButtonText:
                '<i class="fa fa-thumbs-down"></i>',
            cancelButtonAriaLabel: 'Thumbs down'
        }).then((result) => {
            if (result.value) {
                this.createFeedObj(false)
                getFbCode(3)

            }
            else if (result.dismiss === "cancel") { }
        });
    }
    render() {
        const { feedInfo, accountId, connectedAcc, onBack, onFilterNameChange } = this.props
        const { textField, account, album, moderation, filterType, authenticate, connetedAccount, error, isLoading } = this.state

        return (
            <div className="row">
                <div className="col-md-12">
                    <div className="form-body create_feed_wt">
                        <div className="tabbable-tabdrop newRadio d-flex align-items-center justify-content-between flex-wrap feeds_details personalizePopup">
                            <Tabs defaultActiveKey="Page" value={filterType} id="CreateFeedTab" className="nav-tabs-line nav-tabs-bold w-100 tabDrop mb-5" onSelect={e => this.setState({ filterType: e }, () => onFilterNameChange(e))}>
                                {feedInfo && feedInfo.length && feedInfo[0].networkTab && feedInfo[0].networkTab.length ? feedInfo[0].networkTab.map(filter =>
                                    <Tab eventKey={filter.feedtype} key={filter.filter} className="nav-link className= p-0" title={<span>{filter.feedName}</span>}>
                                        <>
                                            {connectedAcc && connectedAcc.length ? <FacebookTab
                                                onAddAccount={() => this.onAddAccount()}
                                                connetedAccount={connetedAccount}
                                                authenticate={authenticate}
                                                onAuthenticateChange={this.onAuthenticateChange}
                                                onConnectedAccountChange={this.onConnectedAccountChange}
                                                tab={filter.feedtype}
                                                text={textField}
                                                error={error}
                                                connectedAcc={connectedAcc}
                                                onTextChange={this.onTextFieldChange}
                                                account={account}
                                                onAccountChange={this.onAccountChange}
                                                onModerationChange={this.onModerationChange}
                                                onAlbumChange={this.onAlbumChange}
                                                album={album}
                                                moderation={moderation} />
                                                :
                                            <div className="row">
                                                <div className={`${filter == 3 ? "col-md-6" : "col-md-12"}`}>
                                                    <div className="form-group">
                                                        <div className="py-5 text-center" onClick={this.onAddAccount}>
                                                            <div className="btn btn-outline-gray btn-lg w-auto btn-clean d-inline-flex align-items-center btn-lg px-6">
                                                                <span className="symbol symbol-25 mr-3">
                                                                    <img src="https://cloud.taggbox.com/taggshop/app/assets/media/square-icons/facebook.svg?ver=1" alt='connect-facebook' />
                                                                </span>
                                                                <span className="text-dark-50 font-weight-bolder font-size-base">Connect Facebook</span>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                            }
                                        </>
                                    </Tab>
                                ) : null}
                            </Tabs>
                            <div className="form-actions w-100">
                                <div className="d-flex justify-content-between align-items-center flex-wrap border-top pt-lg-8 mt-lg-10 pt-5 mt-6">
                                    <div className="need_help__">
                                        <p className="d-flex align-items-center flex-wrap mb-sm-0 mb-6">
                                            Need help in adding feeds?
                                            <a href='https://vimeo.com/912907290?share=copy' className="video_btn btn btn-text p-0 ml-3 text-dark-75 btn-clean video_support_link" data-lity data-target="https://taggbox.com/support/how-to-create-embed-shoppable-gallery-on-website/">
                                                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                                    <g id="Group_6606" data-name="Group 6606" transform="translate(-1 -1)">
                                                        <path id="Path_6626" data-name="Path 6626" d="M9.5,16.281V8.461A.545.545,0,0,1,10.34,8l6.083,3.909a.545.545,0,0,1,0,.919L10.34,16.74a.545.545,0,0,1-.84-.458Z" transform="translate(0.773 0.629)" fill="#A5A5A5"></path>
                                                        <path id="Path_6627" data-name="Path 6627" d="M13,2.636A10.364,10.364,0,1,0,23.364,13,10.364,10.364,0,0,0,13,2.636ZM1,13A12,12,0,1,1,13,25,12,12,0,0,1,1,13Z" fill="#A5A5A5" fillRule="evenodd"></path>
                                                    </g>
                                                </svg>
                                                <span> Watch Video</span>
                                            </a>
                                        </p>
                                    </div>
                                    <div className="hideforBSearch">
                                        <button type="button" className="btn btn-secondary" onClick={onBack}>Back</button>
                                        {connectedAcc && connectedAcc.length ?
                                        <span className="create_feed_hideshow">
                                            <button type="button" className={`commanBtn btn btn-primary ml-4 ${isLoading ? 'spinner disabled spinner-white spinner-right' : ""}`} onClick={
                                                connetedAccount && accountId ?
                                                    this.onSubmit
                                                    :
                                                    this.onAddAccount
                                            }>Create Feed</button>
                                        </span>: <></>}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        accountId: state.addFeedReducer.selectedAccount,
        connectedAcc: state.addFeedReducer.connectedAcc

    }
}
const mapDispatchToProps = dispatch => {
    return {
        SET_CREATE_FEED_OBJ: (obj) => dispatch(SET_CREATE_FEED_OBJ(obj)),
        SELECT_ACCOUNT: (obj) => dispatch(SELECT_ACCOUNT(obj)),
        clickToRefresh: () => dispatch(clickToRefresh()),

    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Facebookfeed)