import { Component } from "react";
import { Modal } from "react-bootstrap"; 
import { DashboardModal, Dashboard } from "@uppy/react"; 
import $ from "jquery"; 
import { ADD_UGC_TEMPLATE } from "../../../../../../../../actions/moderationActions";
import { connect } from "react-redux";
import { UGC_UPLOAD_URL, CLOUDPATH, BASE_URL, CLOUDPATH_TAGGSHOP } from "../../../../../../../../actions/api"; 
import UppyUpload from "../../../../../../../../utils/uppyUpload";

 

class CreateTemplate extends Component {
  uppyUploader = new UppyUpload()
  uppyUploaderPdf = new UppyUpload(['application/pdf', 'application/vnd.openxmlformats-officedocument.wordprocessingml.document'])

  state = {
    postauthor: null,
    posthandle: null,
    authorpicture: null,
    postcarousel: false,
    content: null,
    data: null,
    modalOpen: false,
    createTemplate: false,
    brandFilename: this.props.editData.client_logo,
    selectedBrand: this.props.editData.client_logo_name,
    selectedBrandFile: this.props.editData.client_logo,
    tncFilename: this.props.editData.custom_terms,
    selectedtnc: this.props.editData.custom_terms_name,
    selectedtncFile: this.props.editData.custom_terms,
    selectedTemplate: null,
    acceptTnc: false,
    tncError: false,
    templateName: this.props.editData.name,
    custom_msg: this.props.editData.custom_message,
    tncerrorMsg:false,
    nameerrorMsg:false,
    msgerrorMsg:false,
  };

  componentDidMount() {
    this.uppyUploader.uppy.on("transloadit:result", (stepName, result) => { 
      this.setState(
        {
          selectedBrandFile: `https://cloud.tagshop.ai/tmp/${result.id}.${result.ext}`,
          brandFilename: result.name,
          selectedBrand: `https://cloud.tagshop.ai/tmp/${result.id}.${result.ext}`,
        },
        () => {
          setTimeout(() => {
            this.uppyUploader.uppy.cancelAll();
            this.setState({ brandModalOpen: false });
          }, 300);
        }
      );
    });

    this.uppyUploaderPdf.uppy.on("transloadit:result", (stepName, result) => {
      this.setState(
        {
          selectedtncFile: `https://cloud.tagshop.ai/tmp/${result.id}.${result.ext}`,
          tncFilename: result.name,
          selectedtnc: `https://cloud.tagshop.ai/tmp/${result.id}.${result.ext}`,
        },
        () => {
          setTimeout(() => {
            this.uppyUploaderPdf.uppy.cancelAll();
            this.setState({ tncModalOpen: false });
          }, 300);
        }
      );
    });

    // this.props.ugcData
  }
  

  onClickToOpenBrand = () => {
    this.setState({ brandModalOpen: true });
    $(".uppy-Dashboard-close").click(() => {
      this.uppyUploader.uppy.cancelAll();
      this.setState({ brandModalOpen: false });
    });
  };
  onClickToCloseBrand = () => {
    this.uppyUploader.uppy.cancelAll();
    this.setState({ brandModalOpen: false });
  };

  onClickToOpenTnc = () => {
    this.setState({ tncModalOpen: true });
    $(".uppy-Dashboard-close").click(() => {
      this.uppyUploaderPdf.uppy.cancelAll();
      this.setState({ tncModalOpen: false });
    });
  };
  onClickToCloseTnc = () => {
    this.uppyUploaderPdf.uppy.cancelAll();
    this.setState({ tncModalOpen: false });
  };

  removeBrandLogo = () => {
    this.setState({selectedBrand: "", selectedBrandFile: null, brandFilename: null})
  }
  removeTnc = () => {
    this.setState({tncFilename: null,
      selectedtnc: "",
      selectedtncFile: null,})
  }

  onSaveTemplate = () => {
    const { createTemplate, selectedBrand, tncFilename, brandFilename,  selectedtnc, selectedtncFile, selectedTemplate, templateName, selectedBrandFile, custom_msg} = this.state;

  if(!custom_msg || !templateName || !tncFilename){
    if(!custom_msg) this.setState({msgerrorMsg:true})
    if(!templateName) this.setState({nameerrorMsg:true})
    if(!tncFilename) this.setState({tncerrorMsg:true})
  }
    else{
    var obj = {
      owner: localStorage.getItem('ownerId'),
      name: templateName,
      custom_message: custom_msg,
      custom_terms: selectedtnc,
      custom_terms_name: tncFilename,
      client_logo: selectedBrand,
      client_logo_name: brandFilename
    }
    if(this.props.edit) obj = { ...obj, id: this.props.editData.id}
   this.props.ADD_UGC_TEMPLATE(obj,this.props.onAddNewTemplate)
  }
  }



  render() {
    const { show, onHide, saveTemplate, edit } = this.props;
    const {
      brandModalOpen,
      tncModalOpen,
      createTemplate,
      selectedBrand,
      tncFilename,
      selectedtncFile, selectedTemplate, acceptTnc,
      tncError,
      selectedBrandFile,
      templateName,
      custom_msg,
      brandFilename, tncerrorMsg, nameerrorMsg, msgerrorMsg 
    } = this.state;


    return (
      <>
        <Modal size="lg" show={show} onHide={onHide} className="createTemplateModal">
          <Modal.Header closeButton>
            <Modal.Title>{edit ? 'Edit' : `Create New`} Template</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <div id="TemplateCreateForm">
              <div id="createWallBlock">
                <div className="panel-body">
                  <div className="col-md-12">
                    <div className="form-body">
                      <div className="form-group">
                        <label htmlFor="templatesname">
                          Template Name
                          <span className="required">*</span>
                        </label>
                        <input
                          placeholder="enter your template name"
                          autoComplete="off"
                          className="form-control validate"
                          id="templatesname"
                          value={templateName}
                          onChange={(e) => this.setState({templateName: e.target.value})}
                          maxLength="255"
                          type="text"
                        />
                        <span
                          id="templatesnameerror"
                          className="errorMessage required"
                        >{nameerrorMsg ? "Name is required" :'' }</span>
                      </div>

                      <div className="form-group">
                        <label
                          htmlFor="templatescustom_message"
                          className="control-label"
                        >
                          Custom Message
                          <span className="required">*</span>
                        </label>
                        <textarea
                          className="form-control validate"
                          id="templatescustom_message"
                          maxLength="255"
                          cols="30"
                          rows="6"
                          value={custom_msg}
                          onChange={(e) => this.setState({custom_msg: e.target.value})}
                        ></textarea>
                        <span
                          id="templatescustom_messageerror"
                          className="errorMessage required"
                        >{ msgerrorMsg ? "Custom message is required" :'' }</span>
                      </div>
                      <div className="form-group row">
                        <div className="col-xs-12 col-sm-12 col-md-12 col-lg-12">
                          <label>Brand Logo</label>
                        </div>
                        {! selectedBrandFile ? 
                        <div className="col-5 temp-logo-upload">
                          <span className="dashed_ugc_btn ugc_upload_logo_btn_main">
                            <span className="fileinput-new ugc_upload_logo_btn d-flex align-items-center" onClick={() => this.onClickToOpenBrand()}>
                              <p>Upload Logo</p>
                              <input
                                type="hidden"
                                autoComplete="off"
                                className="form-control validate"
                                id="brandlogoN1"
                              />
                              <input
                                autoComplete="off"
                                className="form-control validate"
                                id="brandlogo1"
                                maxLength="255"
                                type="text"
                              />
                            </span>
                          </span>
                        </div>
                        :
                        <div className="col-md-7 temp-logo-image">
                          <div className="brand_logo_file thumbnail d-flex align-items-center justify-content-between">
                            <div className="brand_logo_file thumbnail d-flex align-items-center justify-content-between">
                              <img
                                 src={selectedBrandFile?.includes('https://cloud.tagshop.ai/') ? selectedBrandFile : `${CLOUDPATH}${UGC_UPLOAD_URL}/${selectedBrandFile}`}
                                className="brand_logo_file_img_ugcright bannerpreviewImg"
                              />
                              <span>{brandFilename}</span>
                            </div>
                            <button
                              type="button"
                              className="btn p-0 removelogo m-2"
                              onClick={this.removeBrandLogo}
                            >
                              <i className="fa fa-times"></i>
                            </button>
                          </div>
                        </div>
  }
                      </div>
                      <div className="form-group">
                        <label>Terms and Conditions</label>
                        <div className="input-group">
                          <input
                            autoComplete="off"
                            className="form-control validate"
                            id="ugcterms"
                            type="text"
                          />
                          <input
                            type="hidden"
                            autoComplete="off"
                            className="form-control validate"
                            id="ugctermsN1"
                          />
                          <input
                            type="hidden"
                            autoComplete="off"
                            className="form-control validate"
                            id="ugctermstype1"
                          />
                          {selectedtncFile ? <div className="clearterms clstrm" style={{}}>
                                        <span>{tncFilename}</span>
                                        <i className="fa fa-times removeterms" onClick={this.removeTnc}></i>
                                    </div> : ''}
                          <div
                            className="input-group-append"
                            id="ugctermssection1"
                          >
                            <span className="input-group-text cursor-pointer"  onClick={() => this.onClickToOpenTnc()}>
                              Attach File
                            </span>
                          </div>
                        </div>
                        <div className="d-flex align-items-center justify-content-between mt-2">
                          <span
                            id="templatescustom_termserror"
                            className="errorMessage required"
                          >{tncerrorMsg ? "Terms & Conditions is required" :''}</span>
                          <div className="ugc_dummy_file_download">
                            <a
                              href={`${CLOUDPATH_TAGGSHOP}/app/assets/media/Terms&ConditionsforuseofUGC.docx`}
                              target="_blank"
                              className="btn btn-link p-0 text-nowrap"
                              download=""
                            >
                              Use our predefined content
                              <span className="svg-icon svg-icon-md">
                                <svg
                                  xmlns="http://www.w3.org/2000/svg"
                                  width="24"
                                  height="24"
                                  viewBox="0 0 24 24"
                                >
                                  <g
                                    id="Group_4422"
                                    data-name="Group 4422"
                                    transform="translate(10979 -4685)"
                                  >
                                    <rect
                                      id="Rectangle_2094"
                                      data-name="Rectangle 2094"
                                      width="24"
                                      height="24"
                                      transform="translate(-10979 4685)"
                                      fill="none"
                                      opacity="0"
                                    ></rect>
                                    <g
                                      id="Group_4410"
                                      data-name="Group 4410"
                                      transform="translate(-10979 4685)"
                                    >
                                      <path
                                        id="Path_5217"
                                        data-name="Path 5217"
                                        d="M11.291,15.4a.989.989,0,0,0,.7.283,1.108,1.108,0,0,0,.776-.331L15.9,12.209a1.016,1.016,0,1,0-1.436-1.437L13.02,12.217V4.057A1.016,1.016,0,0,0,12,3.036a1.027,1.027,0,0,0-1,1.02v8.172l-1.457-1.46a1.038,1.038,0,0,0-1.436,0,1.014,1.014,0,0,0,0,1.436Z"
                                        fill="#545454"
                                      ></path>
                                      <path
                                        id="Path_5218"
                                        data-name="Path 5218"
                                        d="M21.083,18H2.917a.99.99,0,0,0-.984.982v.006a1,1,0,0,0,.985.981H21.083a.985.985,0,0,0,0-1.969Z"
                                        fill="#545454"
                                      ></path>
                                    </g>
                                  </g>
                                </svg>
                              </span>
                            </a>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="modal-footer mt-8">
                <button
                  type="button"
                  className="btn btn-secondary mr-4"
                  data-bs-dismiss="modal"
                  onClick={onHide}
                >
                  Cancel
                </button>
                <button className="btn btn-primary" onClick={this.onSaveTemplate}>
                  Save Template
                </button>
              </div>
            </div>
          </Modal.Body>
          <DashboardModal
            animateOpenClose={true}
            proudlyDisplayPoweredByUppy={false}
            inline={false}
            showProgressDetails={true}
            height={750}
            browserBackButtonClose={true}
            showRemoveButtonAfterComplete={false}
            uppy={this.uppyUploader.uppy}
            open={brandModalOpen}
            onRequestCloseModal={this.onClickToCloseBrand}
            closeAfterFinish={true}
            doneButtonHandler={this.doneButtonHandler}
            plugins={["Instagram", "GoogleDrive", "Url", "Webcam"]}
          />
          <DashboardModal
            animateOpenClose={true}
            proudlyDisplayPoweredByUppy={false}
            inline={false}
            showProgressDetails={true}
            height={750}
            browserBackButtonClose={true}
            showRemoveButtonAfterComplete={false}
            uppy={this.uppyUploaderPdf.uppy}
            open={tncModalOpen}
            onRequestCloseModal={this.onClickToCloseTnc}
            closeAfterFinish={true}
            doneButtonHandler={this.doneButtonHandler}
            plugins={["Url", "Webcam"]}
          />

        </Modal>
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return {
      // ugcData: state.moderation.ugcData,
  }
}
const mapDispatchToProps = dispatch => {
  return {
    ADD_UGC_TEMPLATE: (data, callback) => dispatch(ADD_UGC_TEMPLATE(data, callback))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(CreateTemplate);
