import React, { Component } from 'react'
import { Dropdown, Tab, Tabs } from "react-bootstrap";
import { GET_FLITER_ID, GET_LOCAL } from '../../../../../../../utils';
import VimeoTab from './vimeoTab';
import Swal from "sweetalert2";
import { getVimeoCode } from '../../../../../../../utils/auth';
import { connect } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { CREATE_FEED, FETCH_CONNECTED_ACCOUNTS } from '../../../../../../../actions/moderationActions';
import { BASE_URL } from '../../../../../../../actions/api';
import { addAccountFromId } from '../../../../../../../utils/addAccount';


const Vimeofeed = (props) => {
    const navigation = useNavigate();
    return <VimFeed {...props} navigation={navigation} />;
}

class VimFeed extends Component {
    constructor() {
        super();
        this.controller = new AbortController();

    }
    state = {
        feedText: "",
        manuallyApprove: false,
        error: false,
        filterType:'Hashtag',
        filterId:2,
        authRequired: null,
        connetedAccount: null,
        authenticate:false
    }

    componentDidMount(){
        this.setState({authRequired: this.props.feedInfo[0].authRequire})
        this.props.onFilterNameChange(this.state.filterType)
        const signal = this.controller.signal;
        window.addEventListener("message", this.handleMessage, { signal })
    }

    onTextChange = (value) => this.setState({ feedText: value })
    onManuallyApproveChange = (value) => this.setState({ manuallyApprove: value })
    onConnectedAccountChange = (value) => this.setState({ connetedAccount: value })
    onAuthenticateChange = (value) => this.setState({ authenticate: value })

    clearValues = (e) => {
        this.onTextChange("")
        this.onManuallyApproveChange(false)
        this.props.onFilterNameChange(e)
    }

    onSubmit = () => {
        localStorage.setItem("addAcc", false)
        const { feedText, manuallyApprove,  error , authRequired, authenticate , connetedAccount} = this.state
        if (!feedText || feedText == "") this.setState({ error: true })
        else { 
            this.setState({ error: false })
               authRequired ? (connetedAccount == null ? this.onAddAccount() : this.onAddFeed() ): this.onAddFeed()
            }
    }

    handleMessage = async(event) => {
        const { FETCH_CONNECTED_ACCOUNTS } = this.props
        console.log(event)
        const networkId = GET_LOCAL('networkId')
        console.log('networkId', networkId)
        const { error, code } = event.data;
        if (event.origin.includes("tagshop.ai") || event.origin.includes("localhost")) {
            if (code) {
                await addAccountFromId(networkId, code, () => FETCH_CONNECTED_ACCOUNTS(networkId))
            }
        }
    };


    componentWillUnmount() {
        this.controller.abort()
     }


    onAddAccount = () => {
        const { feedText, manuallyApprove, filterId, authRequired, userId , connetedAccount} = this.state
        const { feedInfo} = this.props 
        localStorage.setItem("authRequired", authRequired)
        // this.setState({ loading: true })

        var objj = {
            wallId: localStorage.getItem('wallId'),
            networkId: 8, 
            filter: filterId,
            moderation: manuallyApprove ? 1 : 0, 
            feedText,   
            accountId:""
        }

        localStorage.setItem("createFeedObj", JSON.stringify(objj))
        localStorage.setItem("networkId", 8)

        Swal.fire({
            imageUrl: 'https://cloud.taggbox.com/taggshop/app/assets/media/square-icons/vimeo.svg',
            title: '<strong>Vimeo</strong>', 
            html: 'Connect to vimeo to pull the content for this feed.',
            showCloseButton: true,
            showCancelButton: false,
            focusConfirm: true,
            confirmButtonText: 'Continue with Vimeo',
            confirmButtonAriaLabel: 'Continue with Vimeo',
            cancelButtonText: '<i class="fa fa-thumbs-down"></i>',
            cancelButtonAriaLabel: 'Thumbs down'
        }).then((result) => {
            if (result.value) {
                getVimeoCode()
            }
            else if (result.dismiss === "cancel") { 
                this.setState({ loading: false })

            }
        });
    }


    onAddFeed=()=>{
        const { feedText, manuallyApprove, filterId, authRequired, userId , connetedAccount} = this.state
        const { navigation, hideAddfeed } = this.props
        this.setState({ loading: true })
        var obj = {
            wallId: localStorage.getItem('wallId'),
            networkId: 8,
            filter: filterId,
            moderation: manuallyApprove,
            feedText: feedText,
            accountId: connetedAccount,
        }

        CREATE_FEED(obj, navigation, true, hideAddfeed)
    }


    render() {
        const {feedText, manuallyApprove, error , filterType, filterId, authenticate, connetedAccount, loading} = this.state
        const { feedInfo , connectedAcc, } = this.props
        return (
            <div className="form-body create_feed_wt">
                <div className="tabbable-tabdrop newRadio d-flex align-items-center justify-content-between flex-wrap feeds_details createFeedInsta personalizePopup">
                    <Tabs defaultActiveKey="Hashtag" id="PinterestFeedTab"  value={filterType} className="nav-tabs-line nav-tabs-bold w-100 tabDrop mb-5"  onSelect={e => this.setState({ filterType: e }, this.setState({ filterId: GET_FLITER_ID(e,feedInfo) }, () => this.clearValues(e)))}>
                        {feedInfo && feedInfo.length && feedInfo[0].networkTab && feedInfo[0].networkTab.length ? feedInfo[0].networkTab.map(filter =>
                            <Tab eventKey={filter.feedtype} key={filter.filter} className="nav-link className= p-0" title={<span>{filter.feedName}</span>}>
                                <VimeoTab
                                    tab={filter.feedtype}
                                    textValue={feedText}
                                    onTextChange={this.onTextChange}
                                    checkBox={manuallyApprove}
                                    onCheckBoxChange={this.onManuallyApproveChange}
                                    connectedAcc={connectedAcc}
                                    onConnectedAccountChange={this.onConnectedAccountChange}
                                    onAuthenticateChange={this.onAuthenticateChange}
                                    connetedAccount={connetedAccount}
                                    authenticate={authenticate}
                                    error={error} />
                            </Tab>
                        ) : null}
                         
                    </Tabs>
                </div>
                <div className="form-actions w-100">
                    <div className="d-flex justify-content-between align-items-center flex-wrap border-top pt-lg-8 mt-lg-10 pt-5 mt-6">
                        <div className="need_help__">
                            <p className="d-flex align-items-center flex-wrap mb-sm-0 mb-6">
                                Need help in adding feeds?
                                <a href='https://vimeo.com/912907290?share=copy' className="video_btn btn btn-text p-0 ml-3 text-dark-75 btn-clean video_support_link" data-lity data-target="https://taggbox.com/support/how-to-create-embed-shoppable-gallery-on-website/">
                                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                        <g id="Group_6606" data-name="Group 6606" transform="translate(-1 -1)">
                                            <path id="Path_6626" data-name="Path 6626" d="M9.5,16.281V8.461A.545.545,0,0,1,10.34,8l6.083,3.909a.545.545,0,0,1,0,.919L10.34,16.74a.545.545,0,0,1-.84-.458Z" transform="translate(0.773 0.629)" fill="#A5A5A5"></path>
                                            <path id="Path_6627" data-name="Path 6627" d="M13,2.636A10.364,10.364,0,1,0,23.364,13,10.364,10.364,0,0,0,13,2.636ZM1,13A12,12,0,1,1,13,25,12,12,0,0,1,1,13Z" fill="#A5A5A5" fillRule="evenodd"></path>
                                        </g>
                                    </svg>
                                    <span className="ms-1"> Watch Video</span>
                                </a>
                            </p>
                        </div>
                        <div className="hideforBSearch">
                            <button type="button" className="btn btn-secondary" onClick={this.props.onBack}>Back</button>
                            <span className="create_feed_hideshow">
                                <button type="button" className={`commanBtn btn btn-primary ml-4 ${loading ? 'spinner spinner-white disabled spinner-right' : ""}`} id="create_feed" onClick={this.onSubmit}>Create Feed</button>
                            </span>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}


const mapStateToProps = (state) => {
    return {
        connectedAcc: state.addFeedReducer.connectedAcc
    }
}

const mapDispatchToProps = dispatch => {
    return {
        FETCH_CONNECTED_ACCOUNTS: (id) => dispatch(FETCH_CONNECTED_ACCOUNTS(id))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Vimeofeed)