import React, { Component } from 'react'
import { Dropdown, Tab, Tabs } from "react-bootstrap";
import { useNavigate } from 'react-router-dom';
import { CREATE_FEED } from '../../../../../../../actions/moderationActions';
import InputGroup from './inputGroup';


const Rss = (props) => {
    const navigation = useNavigate();

    return <RSSFeed {...props} navigation={navigation} />;
}
class RSSFeed extends Component {
    state = {
        displayName: "",
        url: "",
        nameError: false,
        urlError: false,
        checkBox: false
    }

    onNameChange = (value) => this.setState({ displayName: value })
    onUrlChange = (value) => this.setState({ url: value })

    onSubmit = () => {
        const { url, displayName, nameError, urlError, checkBox } = this.state
        const { navigation, hideAddfeed } = this.props

        if (!displayName || displayName == "") this.setState({ nameError: true })
        else this.setState({ nameError: false })
        if (!url || url == "") this.setState({ urlError: true })
        else {
            this.setState({ urlError: false })

            var obj = {
                wallId: localStorage.getItem('wallId'),
                networkId: 12,
                filter: 13,
                fieldtitle: displayName,
                field: url,
                connetedAccount: 0,
                feedType: 2,
                account: 0,
                hashtagIGTV: 0,
                onlyReels: 0,
                moderation: checkBox ? 1 : 0
            }

            CREATE_FEED(obj, navigation, true, hideAddfeed)
        }
    }

    render() {
        const { url, displayName, nameError, urlError, checkBox } = this.state
        return (
            <div className="row">
                <div className="col-md-12">
                    <div className="form-body create_feed_wt">
                        <div className="tab-content mt-7">
                            <div className="tab-pane fade active show" id="createPopUp13">
                                <InputGroup title={'Display Name'} icon={'font'} value={displayName} setValue={this.onNameChange} error={nameError} />
                                <InputGroup title={'Feed Url'} icon={'link'} value={url} setValue={this.onUrlChange} error={urlError} />

                                <span className="help-section d-block text-right">
                                    <button id="sample_xml" className="border-0" style={{ background: "none", color: "#5354fc" }}>Download Sample Format</button>
                                </span>
                            </div>
                            <div className="hideforBSearch">
                                <div className="checkbox-inline">
                                    <label htmlFor={`checkbox_rss`} className="checkbox checkbox-outline checkbox-primary mr-2">
                                        <input type="checkbox" id={`checkbox_rss`} checked={checkBox} onChange={(e) => this.setState({ checkBox: e.target.checked })} className="checkbox checkbox-outline checkbox-outline-2x checkbox-primary mr-2" />
                                        <span></span>
                                        Manually approve posts before making them public?
                                    </label>
                                </div>
                            </div>
                        </div>
                        <div className="form-actions">
                            <div className="d-flex justify-content-between align-items-center flex-wrap border-top pt-lg-8 mt-lg-10 pt-5 mt-6">
                                <div className="need_help__"></div>
                                <div className="hideforBSearch">
                                    <button type="button" className="btn btn-secondary" onClick={this.props.onBack}>Back</button>
                                    <span className="create_feed_hideshow">
                                        <button type="button" className="commanBtn submit_create_feed btn btn-primary ml-4 " id="create_feed" onClick={this.onSubmit}>Create Feed</button>
                                    </span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Rss
