import React, { Component } from 'react'
import { Splide, SplideSlide, SplideTrack, Options } from '@splidejs/react-splide';
import '@splidejs/splide/css';
import moment from 'moment';
//import { Options } from '@splidejs/splide';



export default class CardPreview extends Component {
  constructor(props) {
    super(props);
    this.mainRef = React.createRef();
  }

  handleThumbs = (id) => {
    if (this.mainRef.current) {
       this.mainRef.current.go(id);
    }
  };

  render() {

    const {ugcData} = this.props

    const mainOptions = {
      type      : 'loop',
      perPage   : 1,
      perMove   : 1,
      gap       : '1rem',
      pagination: false,
      height    : '10rem',
      navigation: false,
      
    };

    if(ugcData.data && ugcData.data.length == 1) mainOptions.arrows= false


    const thumbsOptions = {
      type        : 'slide',
      rewind      : true,
      gap         : 10,
      pagination  : false,
      fixedWidth  : 110,
      fixedHeight : 70,
      cover       : true,
      focus       : 'center',
      isNavigation: true,
    };


    
    

    return(
      <>
      {ugcData.data && ugcData.data.length > 1 ?
      <><Splide options={ thumbsOptions }  aria-labelledby="thumbnail-slider-example" className="thumbnail_slider">
          <SplideTrack>
          {ugcData.data && ugcData.data.length ? ugcData.data.map((item,index)=>{
          return <SplideSlide key={`spliP${index}`} >
            <img className="" src={item.postFile} width="80" onClick={() => this.handleThumbs(index)} />
          </SplideSlide>}): null}
          </SplideTrack>
          </Splide>
          </> : null}
          <Splide hasTrack={false} ref={ this.mainRef }  options={mainOptions} >
        <SplideTrack>

       {ugcData.data && ugcData.data.length ? ugcData.data.map((item,index)=>{
        return <SplideSlide key={`spli${index}`}>
         <div id="ugcPostSlider" className="carousel slide" data-bs-ride="carousel">
        <div className="carousel-inner">
          <div className="carousel-item active">
            <div className="card_slider">
              <div className="m_card_style">
                <div className="m_card_img">
                  <div className="post-content-image">
                    <div className="m_media_holder position-relative">
                      <img className="img-fluid ex-coverimg" src={item.postFile}/>
                    </div>
                    <div className="m_card_cont">
                      <div className="d-flex align-items-center justify-content-between mb-4">
                          <div className="d-flex align-items-center m_grid_author">
                              <div className="flex-shrink-0 me-4 mt-lg-0 mt-3">
                                  <div className="symbol symbol-circle symbol-lg-75 m_profile_img" style={{backgroundImage: `url('${item.picture}')`}}></div>                                                                         
                              </div>                                                                         
                              <div className="d-flex flex-column m_user_content">                                                                             
                                  <div className="m_post_title postAuthorName">{item.name}</div>                                                                             
                                  <div className="d-flex align-item-center m_grid_userinfo">                                                                                 
                                      <span className="m_grid_uername text-nowrap">@<span>{item.username}</span></span>                                                                                 
                                      <span className="sepratedot"></span>                                                                                 
                                      <span className="timePost text-nowrap">{moment(new Date(item.createdAt * 1000)).fromNow()}</span>                                                                             
                                  </div>                                                                         
                              </div>                                                                     
                          </div>                                                                     
                          <div className="d-flex flex-column m_social_icon" data-network="">                                                                         
                          <i className={`fab ${item.icon}`}></i>                                                                 
                          </div>                                                                 
                      </div>                                                                 
                      <div className="postedCon setFont font5">                                                                     
                          <p className="m-0" style={{lineHeight: '1.5', wordBreak: 'break-all'}}>
                              {item.content}
                          </p>                                                                 
                      </div>                                                             
                    </div>
                  </div>   
                </div>
              </div>
            </div>
          </div>  
        </div>
      </div>
         </SplideSlide> 
       }): null}
       </SplideTrack>
       {ugcData.data && ugcData.data.length == 1 ? null : <div className="splide__arrows">
            <div className=" splide__arrow--prev tb_ms_slider_pre_arrow-prev">
              <button className="carousel-control-prev ugc_arrow_btn" type="button" data-bs-target="#ugcPostSlider" data-bs-slide="prev">
                <span className="ugc_arrow_ico d-flex" aria-hidden="true">                                                     
                  <i className="fas fa-angle-left"></i>                                                 
                </span>                                             
              </button>
              
            </div>

            <div className=" splide__arrow--next tb_ms_slider_next_arrow-next">
              <button className="carousel-control-next ugc_arrow_btn" type="button" data-bs-target="#ugcPostSlider" data-bs-slide="next"> 
                <span className="ugc_arrow_ico d-flex" aria-hidden="true">
                  <i className="fas fa-angle-right"></i>
                </span>
              </button>
            </div>
          </div>}
      </Splide></>

    )
  }
}
