import React, { useState } from 'react'
import { useDispatch } from 'react-redux'
import { GET_UGC_POST_DETAIL, ugcModal, ugcPopup } from '../../../../../../actions/moderationActions'
import CustomToolTip from '../../../../containers/tooltip'
import AddTag from '../modal/addTag'
import ModerationPagination from '../pagination'
import ErrorModal from '../view/UGC/errorModal'
import PermissionError from '../view/UGC/permissionError'


const SubHeaderBottom = (props) => {
    const [addStatus, setAddTag] = useState(false)
    const UserRuleManualTag = "";
    const UserRuleEnableAI = "";
    const categories = [{ label: "50", value: 50 }, { label: "100", value: 100 }, { label: "200", value: 200 }]
    const [permissionError, setPermissionError] = useState(false)
    const [ugcError, setUgcError] = useState(false)
    const [fromSnapup, setFromSnapup] = useState(false)
    const { moderation, activeTab } = props
    const dispatch = useDispatch()
    const updateLimit = event => { props.limitUpdate(parseInt(event.target.value)) }
    const refreshPost = () => { props.clickToRefresh(); }
    const addTag = () => { setAddTag(true) }
    const clickToCloseAddTag = event => { setAddTag(false) }
    const getRights = () => {
        console.log(moderation, activeTab)
        const { check_uncheck_all } = moderation
        let userId = null
        var flag = false
        console.log("check_uncheck_all", check_uncheck_all.data)
        check_uncheck_all.data.map(post => {
            if (post.ugcstatus == 1 && !flag) {
                setPermissionError(true)
                flag = true
            }
            if(post.networkId == 29) {
                setPermissionError(true)
                setFromSnapup(true)
                flag = true
            }
            if (userId == null) userId = post.username
            else if (userId != null && userId != post.username && !flag) {
                setUgcError(true)
                flag = true
            }
        }) 
        if(!flag){
            const id = check_uncheck_all.data.map(item => item.id) 
            dispatch(GET_UGC_POST_DETAIL({ is_carausel: 0, postId : id, id, }))
            dispatch(ugcModal(true))
        }

    } 
    return <div className="sub_header_bottom">
        <div className="container-fluid">
            <div className="d-flex align-items-center justify-content-between flex-wrap">
                <div className="d-flex align-content-end align-items-center flex-wrap">
                    <div className="d-flex align-content-end align-items-center my-2 select_action">
                        <div className="m_select_all mr-3">
                            <label className="checkbox m_checkbox">
                                <input type="checkbox" className="check_uncheck_all clicktoHide" name="select_all" onChange={props.clickToCheckAll(moderation.check_uncheck_all.status)} checked={moderation.check_uncheck_all.status} />
                                <span></span>
                            </label>
                        </div>
                        <div className="m_refresh mr-3">
                            <CustomToolTip placement="top" overlayText="Refresh">
                                <button type="button" id="refresh_posts" className="btn btn-link p-0" data-toggle="tooltip" data-theme="dark" title="Refresh" data-placement="right" onClick={() => refreshPost()}>
                                    <span className="svg-icon svg-icon-lg svg-icon-dark-75">
                                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                            <g id="Group_4474" data-name="Group 4474" transform="translate(10951 -4712)">
                                                <rect id="Rectangle_2094" data-name="Rectangle 2094" width="24" height="24" transform="translate(-10951 4712)" fill="none" opacity="0" />
                                                <g id="Group_1" data-name="Group 1" transform="translate(-10950 4715)">
                                                    <path id="Path_17" data-name="Path 17" d="M17.617,40.968a6.864,6.864,0,0,1-3.843,1.155,6.966,6.966,0,0,1-6.929-6.059H8a1.027,1.027,0,0,0,.869-.474.9.9,0,0,0,0-.967L6.656,30.974a1,1,0,0,0-.869-.474h0a1.027,1.027,0,0,0-.869.474c-.778,1.25-1.933,3.277-2.122,3.556a1.344,1.344,0,0,0-.189.566.916.916,0,0,0,.961.967H4.814a8.994,8.994,0,0,0,8.962,7.993,8.833,8.833,0,0,0,4.9-1.438.955.955,0,0,0,.283-1.344,1.02,1.02,0,0,0-1.342-.307Z" transform="translate(-2.605 -26.138)" fill="#545454" />
                                                    <path id="Path_18" data-name="Path 18" d="M42.874,19.993h-1.25A8.993,8.993,0,0,0,32.666,12a8.833,8.833,0,0,0-4.9,1.438.971.971,0,1,0,1.061,1.626,6.864,6.864,0,0,1,3.843-1.155A6.966,6.966,0,0,1,39.6,19.969H38.444a1.027,1.027,0,0,0-.869.474.9.9,0,0,0,0,.967l2.213,3.654a1,1,0,0,0,.869.474h0a1.027,1.027,0,0,0,.869-.474c.778-1.25,1.933-3.277,2.122-3.556a1.344,1.344,0,0,0,.189-.566.923.923,0,0,0-.963-.949Z" transform="translate(-21.494 -12)" fill="#545454" />
                                                </g>
                                            </g>
                                        </svg>
                                    </span>
                                </button>
                            </CustomToolTip>
                        </div>
                        <div className="d-flex flex-nowrap mr-3">

                            <div className="modSelectNo" style={moderation.check_uncheck_all && moderation.check_uncheck_all.data.length > 0 ? {} : { display: `none` }}>
                                <span className="label label-xl label-light-danger label-pill label-inline text-nowrap">
                                    <span id="total_checked">{moderation.check_uncheck_all.data.length}</span>&nbsp;&nbsp;Posts Selected
                                        <a className="btn p-0 ml-2 btn-link cancel_all_checked" onClick={() => props.clickToUncheckAll()}><i className="ki ki-close icon-xs"></i></a>
                                </span>
                            </div>
                        </div>
                    </div>

                    <div className="d-flex justify-content-between align-items-center flex-wrap moderation_action">
                        <div className="filter-bar" style={moderation.check_uncheck_all && moderation.check_uncheck_all.data.length > 0 ? {} : { display: `none` }}>
                            <div className="d-flex align-items-center">
                                {activeTab == 0 || activeTab == 2 ? <button id="approve_btn" data-message="approve" type="button" className="btn btn-dark-50 text-nowrap public_btn mr-3 ml-3" onClick={props.onClickToFilterUpdateStatus(1)}>
                                    <span className="svg-icon">
                                        <svg xmlns="http://www.w3.org/2000/svg" width="20.796" height="15.81" viewBox="0 0 20.796 15.81">
                                            <path fill="#545454" data-name="Path 5034" d="M267.531,188.23l-11.355,11.335a1.873,1.873,0,0,1-1.313.543v.767l-.017-.767a1.871,1.871,0,0,1-1.32-.566l-5.729-5.927a1.855,1.855,0,0,1,2.673-2.574l4.416,4.569,10.019-10a1.855,1.855,0,1,1,2.626,2.621Z" transform="translate(-247.278 -185.066)"></path>
                                        </svg>
                                    </span>
                                        Public
                                    </button> : null}
                                {activeTab == 0 || activeTab == 1 ? <button id="disapprove_btn" data-message="disapprove" type="button" className="btn btn-dark-50 text-nowrap private_btn mr-3" onClick={props.onClickToFilterUpdateStatus(2)}>
                                    <span className="svg-icon">
                                        <svg xmlns="http://www.w3.org/2000/svg" fill="#545454" data-name="Layer 1" width="16.811" height="16.988" viewBox="0 0 16.811 16.988">
                                            <path className="cls-1" d="M16.432,14.11a1.689,1.689,0,0,0-.12-.12l-5.5-5.5L16.3,3.007A1.7,1.7,0,0,0,14,.515,1.056,1.056,0,0,0,13.911.6L8.4,6.1,2.911.61A1.684,1.684,0,0,0,1.733.064,1.663,1.663,0,0,0,.513.512a1.7,1.7,0,0,0-.1,2.4c.03.031.06.062.082.082l5.505,5.5-5.5,5.488A1.7,1.7,0,0,0,1.6,16.918a1.7,1.7,0,0,0,1.216-.452c.034-.031.067-.064.084-.083l5.507-5.5,5.5,5.49a1.684,1.684,0,0,0,1.171.563l.1,0a1.7,1.7,0,0,0,1.262-2.83Z"></path>
                                        </svg>
                                    </span>
                                        Private
                                    </button> : null}
                                <button id="deleteAll_btn" data-message="delete" type="button" className="btn btn-dark-50 text-nowrap btn-warning delete_btn mr-3" onClick={props.onClickToFilterDeleteAll}>
                                    <span className="svg-icon">
                                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                            <g data-name="Group 4331" transform="translate(10956 -4533)">
                                                <rect id="Rectangle_2069" data-name="Rectangle 2069" width="24" height="24" transform="translate(-10956 4533)" fill="none" opacity="0"></rect>
                                                <g id="Group_4324" data-name="Group 4324" transform="translate(-10952 4536)">
                                                    <path id="Path_5188" data-name="Path 5188" d="M2.722,16.328A2,2,0,0,0,4.694,18h6.612a2,2,0,0,0,1.972-1.672L14,6H2ZM9,9.5A.5.5,0,0,1,9.5,9h1a.5.5,0,0,1,.5.5v5a.5.5,0,0,1-.5.5h-1a.5.5,0,0,1-.5-.5Zm-4,0A.5.5,0,0,1,5.5,9h1a.5.5,0,0,1,.5.5v5a.5.5,0,0,1-.5.5h-1a.5.5,0,0,1-.5-.5ZM15,2H11A2,2,0,0,0,9,0H7A2,2,0,0,0,5,2H1A1,1,0,0,0,1,4H15a1,1,0,0,0,0-2Z" fill="#545454" fillRule="evenodd"></path>
                                                </g>
                                            </g>
                                        </svg>
                                    </span>
                                      Delete
                                    </button>
                                {
                                    !(UserRuleManualTag || UserRuleEnableAI) ? <button onClick={() => addTag()} id="multitpleTagspost_btn" data-post="1" data-message="delete" type="button" className="btn btn-dark-50 text-nowrap mr-ms-5 mr-3 btn-info">
                                        <span className="svg-icon">
                                            <svg xmlns="http://www.w3.org/2000/svg" width="13.016" height="13.016" viewBox="0 0 13.016 13.016">
                                                <path data-name="Path 5150" d="M9.187,2.4a1.358,1.358,0,0,0-.96-.4H3.358A1.358,1.358,0,0,0,2,3.358V8.227a1.358,1.358,0,0,0,.4.96l5.431,5.431a1.358,1.358,0,0,0,1.92,0l4.869-4.869a1.358,1.358,0,0,0,0-1.92L9.187,2.4ZM5.394,6.752A1.358,1.358,0,1,1,6.753,5.394,1.358,1.358,0,0,1,5.394,6.752Z" transform="translate(-2 -2)" fill="#545454"></path>
                                            </svg>
                                        </span>
                                            Add Tag
                                        </button> : null

                                }

                                <button id="getrights_btn" type="button" className="btn btn-dark-50 text-nowrap getrights_btn get_rights" onClick={() => getRights()}>
                                    <span className="svg-icon getrights-svgicon">
                                        <svg xmlns="http://www.w3.org/2000/svg" width="18.337" height="21.9" viewBox="0 0 18.337 21.9">
                                            <path id="Subtraction_13" data-name="Subtraction 13" d="M8.671,20.9a1.051,1.051,0,0,1-.392-.078A13.555,13.555,0,0,1,0,8.234V4.295a1.046,1.046,0,0,1,.636-.973L8.275.082a.985.985,0,0,1,.787,0l7.64,3.24a1.045,1.045,0,0,1,.635.973V8.234a13.547,13.547,0,0,1-8.28,12.588A1.049,1.049,0,0,1,8.671,20.9ZM5.873,8.822a.936.936,0,0,0-.621.244,1.1,1.1,0,0,0-.11,1.5L7.2,12.781a.929.929,0,0,0,1.372,0l3.677-3.973a1.084,1.084,0,0,0,0-1.609.946.946,0,0,0-.631-.246.9.9,0,0,0-.727.361l-3,3.244L6.494,9.066A.934.934,0,0,0,5.873,8.822Z" transform="translate(0.5 0.5)" fill="#545454" stroke="rgba(0,0,0,0)" strokeMiterlimit="10" strokeWidth="1"></path>
                                        </svg>
                                    </span>
                                        Get Rights
                                    </button>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="d-flex justify-content-between align-items-center my-2 mod_pagination modPaginationRight">
                    <div className="dataTables_length mx-lg-3" id="feed_datatable_length"><label className="m-0"><span className="d-none d-sm-inline">Show</span>

                        <select name="feed_datatable_length" aria-controls="feed_datatable" className="custom-select custom-select-sm form-control form-control-sm mx-2" onChange={updateLimit}>
                            {
                                categories && categories.map((item, index) => <option label={item.label} key={index} value={item.value} selected={moderation.filter.limit == item.value ? `selected` : ``}></option>)
                            }
                        </select>


                        <span className="d-none d-sm-inline">entries</span></label></div>
                    <ModerationPagination {...props} />
                    {/* <dir-pagination-controls max-size="5" direction-links="true" boundary-links="false" on-page-change="getData(newPageNumber)"></dir-pagination-controls> */}
                </div>
            </div>
        </div>
        {addStatus == true ? <AddTag tags={props.moderation.filter} {...props} onUpdateTags={props.onUpdateTags} onDeleteTags={props.onDeleteTags} data={props.moderation.listData} open={addStatus} clickToCloseAddTag={clickToCloseAddTag} /> : null}
        <PermissionError show={permissionError} fromSnapup={fromSnapup} onHide={() => setPermissionError(false)} />
        <ErrorModal show={ugcError} onHide={() => setUgcError(false)} />
    </div>

}
export default SubHeaderBottom;