import React from 'react'

const ModerationPagination = (props) => {
    const { total, perPageCount, activePage, moderation, onNextNavigateClick, onPrevNavigateClick } = props;
    return <>

        {moderation.paginateData && Object.keys(moderation.paginateData).length > 0 ? <ul className="pagination ng-scope">
            <li className={moderation.paginateData.currentPage == 1 ? `disabled` : ``} onClick={moderation.paginateData.currentPage == 1 ? null : onPrevNavigateClick}>
                <a className={moderation.paginateData.currentPage == 1 ? `disabled` : ``}><i className="ki ki-arrow-back"></i></a></li>
            {
                (moderation.paginateData.pages && moderation.paginateData.pages.length > 0) ? moderation.paginateData.pages.map((item, pagesIndex) => {

                    return (parseInt(pagesIndex) < 1) ? <li key={`lIi${pagesIndex}`} className={moderation.paginateData.currentPage == item ? `ng-scope active` : ``} onClick={props.onChangePage(item)}><a>{item}</a></li> :


                        (((moderation.paginateData.currentPage <= 3) && (parseInt(pagesIndex) === 2 || parseInt(pagesIndex) === 1)) || (moderation.paginateData.currentPage >= parseInt(moderation.paginateData.pages.length) - 2 && (parseInt(pagesIndex) >= parseInt(moderation.paginateData.pages.length) - 3))) ?
                            <>
                                {pagesIndex === parseInt(moderation.paginateData.pages.length) - 3 && parseInt(moderation.paginateData.pages.length) > 4 ?
                                    <li className="ng-scope disabled"><a  >...</a></li> :
                                    null
                                }
                                <li className={moderation.paginateData.currentPage == item ? `ng-scope active` : ``} onClick={props.onChangePage(item)}><a>{item}</a></li>


                            </>
                            :
                            (parseInt(moderation.paginateData.pages.length) - 3 == pagesIndex) ?
                                (moderation.paginateData.currentPage > 3 && moderation.paginateData.currentPage < moderation.paginateData.pages.length - 2) ?
                                    <>
                                        <li className="ng-scope disabled"><a  >...</a></li>
                                        <li className={`ng-scope active`} onClick={props.onChangePage(item)}><a ng-click="setCurrent(pageNumber)">{moderation.paginateData.currentPage}</a></li>
                                        <li className="ng-scope disabled"><a  >...</a></li>
                                    </>
                                    : <li className="ng-scope disabled"><a  >...</a></li>

                                : (parseInt(moderation.paginateData.pages.length) - 1 == pagesIndex) ? <li className={moderation.paginateData.currentPage == item ? `ng-scope active` : ``} onClick={props.onChangePage(item)}><a ng-click="setCurrent(pageNumber)">{item}</a></li>
                                    : null
                }) : null

            }

            <li className={moderation.paginateData.currentPage == moderation.paginateData.totalPages ? `disabled` : ``} onClick={moderation.paginateData.currentPage == moderation.paginateData.totalPages ? null: onNextNavigateClick}><a className={moderation.paginateData.currentPage == moderation.paginateData.totalPages ? `disabled` : ``}><i className="ki ki-arrow-next"></i></a></li>
        </ul>
            : null}
    </>
}
export default ModerationPagination;
