import React, { Component } from 'react'
import AccountPicker from '../accountPicker'

export default class VimeoTab extends Component {
    state={feedTextCharError: false}

    handleFeedTextChange = (e) => {
        const value = e.target.value
        if(value[value.length-1] != '@' && value[value.length-1] != "#" && !value.includes("@") && !value.includes("#")) this.setState({feedTextCharError: false}, () =>this.props.onTextChange(value))
        else this.setState({feedTextCharError : true})
    }

    render() {
        const { tab, textValue, onTextChange, checkBox, onCheckBoxChange,  error , connectedAcc, connetedAccount, onConnectedAccountChange , onAuthenticateChange, authenticate} = this.props
        return (
            <>
                <div className="form-group">
                    <div className="input-group">
                        <div className="input-group-prepend">
                            <span className="input-group-text">
                                <i className={`fas fa-${tab == "Hashtag" ? "hashtag" : "at"}`}></i>
                            </span>
                        </div>
                        <input autoComplete="off" value={textValue?textValue:""} onChange={(e) => this.handleFeedTextChange(e)} placeholder={`Enter ${tab == "Hashtag" ? "hashtag" : "user handle"}`} className="autosearch mycheck form-control" id="" type="text" />
                    </div>
                    {this.state.feedTextCharError ? <span id="" className="errorMessage required">@ and # not allowed</span> : null}
                    {error ? <span id="feedsfield2error" className="errorMessage required">This field is required</span> : null}
                </div>
                <div className="hideforBSearch">
                    <div className="checkbox-inline">
                        <label htmlFor={`checkbox_${tab}`} className="checkbox checkbox-outline checkbox-primary mr-2">
                            <input type="checkbox" id={`checkbox_${tab}`} checked={checkBox} onChange={(e) => onCheckBoxChange(e.target.checked)} className="checkbox checkbox-outline checkbox-outline-2x checkbox-primary mr-2" value="1" />
                            <span></span>
                            Manually approve posts before making them public?
                        </label>
                    </div>
                </div>

                {connectedAcc && connectedAcc.length ? <AccountPicker connectedAcc={connectedAcc} networkId={'8'} onConnectedAccountChange={onConnectedAccountChange} connetedAccount={connetedAccount} onAuthenticateChange={onAuthenticateChange} authenticate={authenticate} /> : null}
            </>
        )
    }
}
