import { Component } from "react";  
import { connect } from "react-redux"; 
import { TOAST_ERROR } from "../../../../../../../../utils";

class ChooseMethod extends Component {

    state = {
    };

    componentDidMount() {
        
    }

    onNextStep = () => {
        const { requestMode } = this.props
        if (requestMode != null) {
            this.props.setStep(1)
        }
        else TOAST_ERROR('Please select the mode for Right Request')
    }


    render() {
        const { setRequestMode, requestMode } = this.props

        return (
            <div className="ugc_req card card-custom shadow-none" id="myTabContentMD">
                <div id="home-md">
               
                    <div className="ugc_steps ugc-flexgrow modal-body">
                                <h4>Choose Method</h4>
                        <div className="form-group ugc_right_col">
                            <div>
                                <div className="p-lg-6 py-3">
                                    <div className="m-auto generalrequest-tabContent">
                                        <h3 className="mb-1 fs-7 pt-1">Select the mode for Right Request</h3>
                                        <p>Tagshop offers two distinct modes to request rights. Comment Request and DM Request. </p>
                                        <div className="mb-3 d-flex flex-column">
                                            <div role="group" className="flex-column btn-group">
                                                <input className="btn-check" name="options" type="radio" autoComplete="off" id="rightsVia-2" value={1}  />
                                                <label tabindex="0" for="rightsVia-2" className={`mb-2 d-flex bg-transparent justify-content-between py-4  btn btn-outline-secondary ${requestMode == 1 ? 'border-success' : ''}`} onClick={() => setRequestMode(1)}>
                                                    <div className="d-flex me-3 fw-normal">
                                                        <div className="svg-icon">
                                                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
                                                                <path d="M6.45455 19L2 22.5V4C2 3.44772 2.44772 3 3 3H21C21.5523 3 22 3.44772 22 4V18C22 18.5523 21.5523 19 21 19H6.45455ZM5.76282 17H20V5H4V18.3851L5.76282 17ZM8 10H16V12H8V10Z"></path>
                                                            </svg>
                                                        </div>
                                                        <span className="text-start">
                                                            <p className="h6 mb-0 fw-medium">Comment Request</p>
                                                            <span className="fs-10">Request rights by commenting under the post you desire the rights for! </span>
                                                        </span>
                                                    </div>
                                                    <input name="rights-via" type="radio" className="d-none form-check-input" />
                                                    <span className="w-20px">
                                                        <div className={`ugcCheck_icon ${requestMode == 1 ? 'text-success' : ''}`}>
                                                            <div className="ts__icon ts-check"></div>
                                                        </div>
                                                    </span>
                                                </label>
                                                <input className="btn-check" name="options" type="radio" autoComplete="off" id="rightsVia-3" value={0}/>
                                                <label tabindex="0" for="rightsVia-3" className={`mb-2 d-flex bg-transparent justify-content-between py-4  btn btn-outline-secondary ${requestMode == 0 ? 'border-success' : ''}`} onClick={() => setRequestMode(0)}>
                                                    <div className="d-flex me-3 fw-normal">
                                                        <div className="svg-icon">
                                                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
                                                                <path d="M14 22.5L11.2 19H6C5.44772 19 5 18.5523 5 18V7.10256C5 6.55028 5.44772 6.10256 6 6.10256H22C22.5523 6.10256 23 6.55028 23 7.10256V18C23 18.5523 22.5523 19 22 19H16.8L14 22.5ZM15.8387 17H21V8.10256H7V17H11.2H12.1613L14 19.2984L15.8387 17ZM2 2H19V4H3V15H1V3C1 2.44772 1.44772 2 2 2Z"></path>
                                                            </svg>
                                                        </div>
                                                        <span className="text-start">
                                                            <p className="h6 mb-0 fw-medium">DM Request</p>
                                                            <span className="fs-10">Send a Rights request directly into the DM of the creators! </span>
                                                        </span>
                                                    </div>
                                                    <input name="rights-via" type="radio" className="d-none form-check-input" />
                                                    <span className="w-20px">
                                                        <div className={`ugcCheck_icon ${requestMode == 0 ? 'text-success' : ''}`}>
                                                            <div className="ts__icon ts-check"></div>
                                                        </div>
                                                    </span>
                                                </label>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                
                            </div>
                        </div>
                    </div>
                    <div className="ugc-peingdone modal-footer text-right">
                        <input className="btn btn-secondary" type="button" value="Cancel" onClick={() => this.props.hideUgc()}/>
                        <input className="btn btn-primary" type="button" value="Next Step" onClick={() => this.onNextStep()} />
                    </div>
                </div>

            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        // ugcData: state.moderation.ugcData,
    }
}
const mapDispatchToProps = dispatch => {
    return {
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ChooseMethod);
