/* eslint-disable eqeqeq */
import React, { Component } from 'react'
import { Tab, Tabs } from 'react-bootstrap'
import { connect } from 'react-redux'
import { useNavigate } from 'react-router-dom'
import { SET_CREATE_FEED_OBJ } from '../../../../../../../actions/addFeedActions'
import { BASE_URL } from '../../../../../../../actions/api'
import { CREATE_FEED, FETCH_CONNECTED_ACCOUNTS } from '../../../../../../../actions/moderationActions'
import { GET_FLITER_ID, GET_LOCAL, TOAST_ERROR, TOAST_SUCCESS } from '../../../../../../../utils'
import { getFbCode } from '../../../../../../../utils/auth'
import AlertText from './alertText'
import InstaTabs from './instaTabs'
import { GET_VIDEO_URLS } from '../constant'
import Swal from 'sweetalert2'
import { addAccountFromId } from '../../../../../../../utils/addAccount'

const Instagramfeed = (props) => {
    const navigation = useNavigate();

    return <InstaFeed {...props} navigation={navigation} />;
}
class InstaFeed extends Component {
    constructor() {
        super();
        this.controller = new AbortController();

    }
    state = {
        feedText: null,
        personalAcc: null,
        filter: null,
        filterType: 'hashtag',
        moderation: false,
        networkId: null,
        hashtagOnly: true,
        excludeVideos: false,
        feedType: null,
        multiplePhoto: true,
        listname: null,
        showError: false,
        showListError: false,
        authenticate: false,
        connetedAccount: null,
        selectedChildAccount: null,
        onlyReels: false,
        loading: false,
        authRequired: null,
        flag: true,
    }

    handleMessage = async(event) => {
        const { FETCH_CONNECTED_ACCOUNTS } = this.props
        console.log(event)
        const networkId = GET_LOCAL('networkId')
        console.log('networkId', networkId)
        const { error, code } = event.data;
        if (event.origin.includes("tagshop.ai") || event.origin.includes("localhost")) {
            if (code) {
                await addAccountFromId(networkId, code, () => {FETCH_CONNECTED_ACCOUNTS(18); FETCH_CONNECTED_ACCOUNTS(18)})
            }
        }
    };
    
     componentWillUnmount(){
        // window.removeEventListener("message", this.handleMessage);
        this.controller.abort()
    }

    componentDidMount() {
        this.setState({ authRequired: this.props.feedInfo[0].authRequire })
        this.props.onFilterNameChange(this.state.filterType)
        const signal = this.controller.signal;
        window.addEventListener("message", this.handleMessage, { signal })
        setTimeout(() => {
            this.setState({flag: false})
        }, 1000);
    }


    onModerationChange = (value) => this.setState({ moderation: value })
    onHashtagOnlyChange = (value) => this.setState({ hashtagOnly: value })
    onExcludeVideosChange = (value) => this.setState({ excludeVideos: value })
    onFieldTextChange = (value) => this.setState({ feedText: value, showError: false })
    onMultiplePhotoChange = (value) => this.setState({ multiplePhoto: value })
    onListNameChange = (value) => this.setState({ listname: value, showListError: false })
    onConnectedAccountChange = (value) => this.setState({ connetedAccount: value })
    onAuthenticateChange = (value) => this.setState({ authenticate: value })
    onOnlyReelsChange = (value) => this.setState({ onlyReels: value })
    onPersonalAccChange = (value) => this.setState({ personalAcc: value })
    onSelectedChildAccountChange = (value) => this.setState({ selectedChildAccount: value })

    clearValues = (e) => {
        this.onFieldTextChange("")
        this.onModerationChange(false)
        this.setState({ filterType: e, hashtagOnly: false, excludeVideos: false, filter: GET_FLITER_ID(this.state.filterType, this.props.feedInfo) })
        this.props.onFilterNameChange(e)
    }

    onAddAccount = () => {
        const { filterType } = this.state
        const { feedInfo } = this.props
        // this.setState({ loading: true })
        localStorage.setItem("addAcc", false)
        localStorage.setItem("selectedNetwork", 18)

        localStorage.setItem("networkId", 18)
        const ftr = feedInfo && feedInfo.length && feedInfo[0].networkTab && feedInfo[0].networkTab.length ? feedInfo[0].networkTab.findIndex(f => f.feedtype == filterType) : null
        const filterId = feedInfo[0]?.networkTab[ftr]?.filter
        Swal.fire({
            imageUrl: `https://cloud.taggbox.com/taggshop/app/assets/media/square-icons/${filterId != 1 ? 'facebook' : 'instagram'}.svg?ver=1`,
            title: `<strong> ${filterId != 1 ? 'Facebook' : 'Instagram'}</strong>`,
            html:
                `Connect to ${filterId != 1 ? 'Facebook' : 'Instagram'} to pull the content for this feed.`,
            showCloseButton: true,
            showCancelButton: false,
            focusConfirm: true,
            confirmButtonText:
                `Continue with ${filterId != 1 ? 'Facebook' : 'Instagram'}`,
            confirmButtonAriaLabel: `Continue with ${filterId != 1 ? 'Facebook' : 'Instagram'}`,
            cancelButtonText:
                '<i class="fa fa-thumbs-down"></i>',
            cancelButtonAriaLabel: 'Thumbs down'
        }).then((result) => {
            if (result.value) {
                this.setState({ loading: true })
                localStorage.setItem("isV2", true)
                if (filterId == 1) {
                    const url = `https://api.instagram.com/oauth/authorize?client_id=${'480810795949098'}&redirect_uri=${`${BASE_URL}/feeds/authCallback/`}&scope=user_profile,user_media&response_type=code`
                    this.onSubmit(false, url)
                }
                else {
                    this.onSubmit(false)
                    getFbCode(18)
                }

            }
            else if (result.dismiss === "cancel") { }
            else if (result.dismiss === "close" || result.dismiss === "backdrop") {
                this.setState({ loading: false })

            }
        });
    }

    onSubmit = (reactNavigation, url) => {
        this.setState({ loading: true })
        localStorage.setItem("addAcc", false)

        try {
            const {
                feedText,
                moderation,
                filterType,
                connetedAccount,
                selectedChildAccount,
                hashtagOnly,
                excludeVideos,
                onlyReels,
                personalAcc,
            } = this.state
            const { navigation, feedInfo, SET_CREATE_FEED_OBJ, hideAddfeed } = this.props

            const ftr = feedInfo && feedInfo.length && feedInfo[0].networkTab && feedInfo[0].networkTab.length ? feedInfo[0].networkTab.findIndex(f => f.feedtype == filterType) : null
            const filterId = feedInfo[0]?.networkTab[ftr]?.filter

            if ((!feedText || feedText == "") && ![34, 24, 25, 66, 1].includes(filterId)) {
                this.setState({ showError: true, loading: false })
            }
            else {

                this.setState({ showError: false })



                var obj = {
                    wallId: localStorage.getItem('wallId'),
                    networkId: filterId == 1 ? 2 : 18,
                    moderation: moderation ? 1 : 0,
                    account: 0,
                    feedText: [34, 24, 25, 66].includes(filterId) ? selectedChildAccount && selectedChildAccount.ConnectedAccounts.value2 : filterId == 1 ? personalAcc ? personalAcc.ConnectedAccounts.accountName : "" : feedText.includes("#") ? feedText.replace("#", "") : feedText,
                    hashtagCaption: hashtagOnly ? 1 : 0,
                    filter: filterId,
                    accountId: connetedAccount,
                    hashtagIGTV: excludeVideos ? 1 : 0,
                    onlyReels: onlyReels ? 1 : 0,
                }

                if ([34, 24, 25, 66].includes(filterId)) {
                   
                    obj['accountId'] = selectedChildAccount ? selectedChildAccount.ConnectedAccounts.id : ""
                }
                if (filterId == 1) {
                     obj['accountId'] = personalAcc ? personalAcc.ConnectedAccounts.id : ""
                   

                }

                SET_CREATE_FEED_OBJ(obj)
                localStorage.setItem("createFeedObj", JSON.stringify(obj))
                localStorage.setItem("networkId", 18)

                if (reactNavigation) {
                    // navigation('/walls/authcallback')
                    CREATE_FEED(obj, navigation, true, hideAddfeed)

                }
                else if (url) {
                    window.location.href = url
                }
            }

        } catch (error) { 
            this.setState({ loading: false })

        }
    }

    onCreateFeed = () => {
        this.setState({ loading: true })

        // authRequired ?
        //     accountId == null ?
        //         this.onAddAccount() : this.onSubmit(true)
        //     : ['hashtag', 'handle'].includes(filterType) ? this.onSubmit(true) :  accountId == null ? this.onSubmit(true) : this.onAddAccount()
       setTimeout(() => {
         if (this.state.authRequired) {
             if (this.props.accountId == null || this.props.accountId == 0) this.onAddAccount()
             else this.onSubmit(true)
         }
         else {
             if (['hashtag', 'handle'].includes(this.state.filterType)){
                if (this.props.accountId == 0) this.onAddAccount()
                 else this.onSubmit(true)
             }
             else {
                 if (this.props.accountId == null) this.onAddAccount()
                 else this.onSubmit(true)
             }
         }
       }, 200);
    }

    render() {
        const { feedInfo, ACTION_ADD_AUTH_OBJ, connectedAcc, onBack,} = this.props
        const { moderation, onlyReels, authenticate, personalAcc, loading, hashtagOnly, excludeVideos, selectedChildAccount, connetedAccount, feedText, multiplePhoto, listname, filterType, showError, showListError,authRequired} = this.state


        return (
            <div className="row">
                <div className="col-md-12">
                    <div className="form-body create_feed_wt">
                        <div className="tabbable-tabdrop newRadio d-flex align-items-center justify-content-between flex-wrap feeds_details personalizePopup">
                            <Tabs defaultActiveKey="hashtag" id="CreateFeedTab" onSelect={(e) => this.clearValues(e)} className="nav-tabs-line nav-tabs-bold w-100 tabDrop mb-5">
                                {feedInfo &&!this.state.flag && feedInfo.length && feedInfo[0].networkTab && feedInfo[0].networkTab.length ? feedInfo[0].networkTab.map(filter =>
                                    <Tab eventKey={filter.feedtype} key={filter.filter} className="nav-link className= p-0" title={<span>{filter.feedName}</span>}>
                                        { (connectedAcc && connectedAcc.length) || filter.filter == 1 || !authRequired ? 
                                        <>
                                        <InstaTabs
                                            tab={filter.feedtype}
                                            filter={'hashtag'}
                                            moderation={moderation}
                                            hashtagOnly={hashtagOnly}
                                            listname={listname}
                                            excludeVideos={excludeVideos}
                                            feedText={feedText}
                                            showError={showError}
                                            connetedAccount={connetedAccount}
                                            showListError={showListError}
                                            multiplePhoto={multiplePhoto}
                                            authenticate={authenticate}

                                            selectedChildAccount={selectedChildAccount}
                                            onlyReels={onlyReels}
                                            personalAcc={personalAcc}
                                            onAddAccount={this.onAddAccount}
                                            onPersonalAccChange={this.onPersonalAccChange}
                                            ACTION_ADD_AUTH_OBJ={ACTION_ADD_AUTH_OBJ}
                                            onSelectedChildAccountChange={this.onSelectedChildAccountChange}
                                            onAuthenticateChange={this.onAuthenticateChange}
                                            onModerationChange={this.onModerationChange}
                                            onListNameChange={this.onListNameChange}
                                            onHashtagOnlyChange={this.onHashtagOnlyChange}
                                            onExcludeVideosChange={this.onExcludeVideosChange}
                                            onFieldTextChange={this.onFieldTextChange}
                                            onMultiplePhotoChange={this.onMultiplePhotoChange}
                                            onConnectedAccountChange={this.onConnectedAccountChange}
                                            onOnlyReelsChange={this.onOnlyReelsChange}
                                        />
                                        <AlertText filterId={filter.filter} /> 
                                        </>
                                        :
                                            <div className="row">
                                                <div className={`${filter == 3 ? "col-md-6" : "col-md-12"}`}>
                                                    <div className="form-group">
                                                        <div className="py-5 text-center" onClick={this.onAddAccount}>
                                                            <div className="btn btn-outline-gray btn-lg w-auto btn-clean d-inline-flex align-items-center btn-lg px-6">
                                                                <span className="symbol symbol-25 mr-3">
                                                                    <img src="https://cloud.taggbox.com/taggshop/app/assets/media/square-icons/instagram-business.svg?ver=1" alt='connect-instagram' />
                                                                </span>
                                                                <span className="text-dark-50 font-weight-bolder font-size-base">Connect Instagram</span>
                                                            </div>
                                                        </div>
                                                    </div>

                                                    <div id="feed_note" className="alert alert-custom fade show mt-6 mb-0 bg-light rounded-0 p-3 align-items-start">
                                                        <div className="alert-icon py-1">
                                                            <span className="svg-icon svg-icon-sm">
                                                                <svg xmlns="http://www.w3.org/2000/svg" width="13" height="13" viewBox="0 0 13 13">
                                                                    <path id="Path_1660" fill="#959595" data-name="Path 1660" d="M17.5,11A6.5,6.5,0,0,0,11,17.5a6.64,6.64,0,0,0,2.472,5.1A6.265,6.265,0,0,0,17.5,24a6.5,6.5,0,0,0,0-13Zm.667,9.167a.667.667,0,0,1-1.333,0V17a.667.667,0,1,1,1.333,0ZM17.5,15.5a.667.667,0,1,1,.667-.667A.667.667,0,0,1,17.5,15.5Z" transform="translate(-11 -11)" opacity="0.999"></path>
                                                                </svg>
                                                            </span>
                                                        </div>
                                                        <div className="alert-text">
                                                        Important Note: Please add your Instagram Business account.
                                                        </div>
                                                    </div>
                                                    
                                                </div>
                                            </div>
                                        }
                                    </Tab>
                                ) : null}


                            </Tabs>
                            <div className="form-actions w-100">
                                <div className="d-flex justify-content-between align-items-center flex-wrap border-top pt-lg-8 mt-lg-10 pt-5 mt-6">
                                    <div className="need_help__">
                                        <p className="d-flex align-items-center flex-wrap mb-sm-0 mb-6">
                                            Need help in adding feeds?
                                            <a href="https://vimeo.com/907703540" className="video_btn btn btn-text p-0 ml-3 text-dark-75 btn-clean video_support_link" data-target="https://taggbox.com/support/how-to-create-embed-shoppable-gallery-on-website/" data-lity>
                                                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                                    <g id="Group_6606" data-name="Group 6606" transform="translate(-1 -1)">
                                                        <path id="Path_6626" data-name="Path 6626" d="M9.5,16.281V8.461A.545.545,0,0,1,10.34,8l6.083,3.909a.545.545,0,0,1,0,.919L10.34,16.74a.545.545,0,0,1-.84-.458Z" transform="translate(0.773 0.629)" fill="#A5A5A5"></path>
                                                        <path id="Path_6627" data-name="Path 6627" d="M13,2.636A10.364,10.364,0,1,0,23.364,13,10.364,10.364,0,0,0,13,2.636ZM1,13A12,12,0,1,1,13,25,12,12,0,0,1,1,13Z" fill="#A5A5A5" fillRule="evenodd"></path>
                                                    </g>
                                                </svg>
                                                <span> Watch Video</span>
                                            </a>
                                        </p>
                                    </div>
                                    <div className="hideforBSearch">
                                        <button type="button" className="btn btn-secondary" onClick={onBack}>Back</button>
                                        {(connectedAcc && connectedAcc.length) || filterType == 'personal' || !authRequired ? <span className="create_feed_hideshow">
                                            {
                                                connectedAcc && connectedAcc.length
                                                    ? <button type="button" className={`commanBtn btn btn-primary ml-4 ${loading ? 'spinner spinner-white disabled spinner-right' : ""} `}
                                                        onClick={() =>
                                                            this.onCreateFeed()
                                                        }>Create Feed</button>
                                                    :
                                                    !["stories", "mentions", "tagged", "videos"].includes(filterType) ?
                                                        <button type="button" className={`commanBtn btn btn-primary ml-4 ${loading ? 'spinner spinner-white disabled spinner-right' : ""} `}
                                                            onClick={() =>
                                                                this.onCreateFeed()
                                                            }>Create Feed</button>
                                                        :
                                                        null
                                            }
                                        </span> : <></>}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        accountId: state.addFeedReducer.selectedAccount,
        connectedAcc: state.addFeedReducer.connectedAcc,
        accountStatus: state.addFeedReducer.accountStatus,

    }
}
const mapDispatchToProps = dispatch => {
    return {
        SET_CREATE_FEED_OBJ: (obj) => dispatch(SET_CREATE_FEED_OBJ(obj)),
        FETCH_CONNECTED_ACCOUNTS: (id) => dispatch(FETCH_CONNECTED_ACCOUNTS(id))
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Instagramfeed)
