import React, { Component } from 'react'
import { Modal } from 'react-bootstrap'
import { connect } from 'react-redux'
import { togglePriceSection } from '../../../../../../../actions/show_modal_Action'

 class twitterUpgradePopup extends Component {
    onUpgrade = () => {
        // window.showpricing(2,'Taggshop','taggshop')
        this.props.handleClose()
    }
    render() {
        const { handleClose, show } = this.props
        return (
 

            <Modal show={show} onHide={handleClose} size="lg" centered >
                <div className='modal-header justify-content-end px-5 border-0 pb-0'>
                             <button type='button' className='close' data-bs-dismiss='modal' aria-hidden='true' onClick={handleClose}>
                                 <i className='ki ki-close' style={{fontSize: '16px'}}></i>
                             </button>
                         </div>
                <Modal.Body className='modal-body'>
                <div className='row'>
                                 <div className='col-lg-12'>
                                     <div className='upgredeMain text-center'>
                                         <div className='Errorimage'>
                                             <img src='https://cloud.taggbox.com/taggshop/app/assets/media/products/twitter_upgrade.png' className='img-fluid' alt="twitter_upgrade" />
                                         </div>
                                         <div className='permissionMsg mt-4 px-md-4'>
                                             <h2 className='ErrorMsg'>Can't Create Twitter Feed</h2>
                                             <p className='msgSec'>Sorry, we are not providing Tweet aggregation with this plan. Kindly upgrade to a higher plan to avail this feature.</p>
                                         </div>
                                         <div className='buttonUpgred mb-sm-5 mb-3'>
                                             <div className='commanBtn btn btn-primary d-inline-block upgPlan' id="upgPlan"  data-product-id="2" onClick={()=>{this.props.dispatch(togglePriceSection(true, 2))}} >
                                                 Upgrade Now
                                             </div>
                                         </div>
                                     </div>
                                 </div>
                             </div>
                </Modal.Body>
            </Modal>
        )
    }
}


const mapDispatchToProps = dispatch => ({ 
    dispatch                
 })

export default connect(null, mapDispatchToProps)(twitterUpgradePopup);

