import React from 'react'
const Tabs = (props) => {
    const { totalpost, publicpost, privatepost, upcomingpost, activeTab } = props.details
    return <ul className="nav nav-tabs nav-tabs-space-lg flex-nowrap align-content-end nav-tabs-line nav-tabs-bold top_nav_tabs" role="tablist">
        <li className="nav-item mr-3 flex-sm-nowrap all_posts_wt" id="allPostsTb">
            <a className={`nav-link postsData all_posts ${props.activeTab==0 ? 'active' :''}`} data-value='' data-bs-toggle="tab" href="#mod_all" onClick={props.clickToTab(0)}>
                <span className="nav-text text-nowrap">All Posts <small id="all_posts">{totalpost}</small></span>
            </a>
        </li>
        
        <li className="nav-item mr-3 flex-sm-nowrap public_posts_wt">
            <a className={`nav-link postsData public-data ${props.activeTab==1 ? 'active' :''}`}  data-value='1' data-bs-toggle="tab" href="#public" onClick={props.clickToTab(1)}>
                <span className="nav-text text-nowrap">Public <small id="public_posts">{publicpost}</small></span>
            </a>
        </li>

        <li className="nav-item flex-sm-nowrap private_posts_wt">
            <a className={`nav-link postsData private-data ${props.activeTab==2 ? 'active' :''}`} data-value='2' data-bs-toggle="tab" href="#private" onClick={props.clickToTab(2)}>
                <span className="nav-text text-nowrap">Private <small id="private_posts">{privatepost}</small></span>
            </a>
        </li>
        {
            upcomingpost && upcomingpost > 0 ? <li id="nav_tab4" className="nav-item flex-sm-nowrap" >
                <a href="#tab4" data-bs-toggle="tab" className="nav-link postsData" data-value='3'>
                    <span className="nav-text text-nowrap">Upcoming <small id="upcoming_posts">{upcomingpost}</small></span>
                </a>
            </li> : <li id="nav_tab4" className="nav-item flex-sm-nowrap" style={{ display: `none` }}>
                    <a href="#tab4" data-bs-toggle="tab" className="nav-link postsData" data-value='3'>
                        <span className="nav-text text-nowrap">Upcoming <small id="upcoming_posts">{upcomingpost}</small></span>
                    </a>
                </li>
        }

    </ul>
}

export default Tabs;

