import React, { Component } from 'react'
import { HOST_URL, HOST_URL_TAGGSHOP } from '../../../../../../../../actions/api'
import { TOAST_ERROR } from '../../../../../../../../utils'

export default class copyUrl extends Component {

    state = { openSteps: false, copied: false, hash: 'yes', accountDetails: {} }
    componentDidMount() {
        // if(this.props.networkId == parseInt(JSON.parse(localStorage.getItem('instagram_ugc'))?.networkId)){
        //     this.setState({ accountDetails: JSON.parse(localStorage.getItem('instagram_ugc')) })
        //     this.props.setUserName(JSON.parse(localStorage.getItem('instagram_ugc'))?.username)
        //   }
        //   else this.setState({accountDetails: null})
        //   console.log(this.state.accountDetails, JSON.parse(localStorage.getItem('instagram_ugc')))
        this.copyText();
    }

    sendRequest = () => {
        const { onRaiseRequest, setUserName } = this.props
        // console.log('this.state.accountDetails', this.state.accountDetails)
        // setUserName(this.state.accountDetails.username)
        onRaiseRequest(this.state.hash)
    }

    copyText = () => {
        const { ugcData, custom_msg, requestMode } = this.props
        if(requestMode == 0) navigator.clipboard.writeText(custom_msg + ugcData.bitlyurl)
        else navigator.clipboard.writeText(`${custom_msg}. Just reply with #${this.state.hash}  if you're up for it, and you can find all the nitty-gritty details in our bio. Thanks a bunch!`)
        this.setState({ copied: true })
    }

    raiseReq = () => {
        const { ugcData, custom_msg, requestMode } = this.props

        if(this.state.hash == '' || !this.state.hash) {TOAST_ERROR('Hashtag for UGC Rights is required!');return;}
        this.sendRequest()
        if(requestMode == 0) navigator.clipboard.writeText(custom_msg + ' Post : ' + ugcData.data[0].link + ' Form Link : ' + ugcData.bitlyurl);
        else navigator.clipboard.writeText(`${custom_msg} Just reply with #${this.state.hash}  if you're up for it, and you can find all the nitty-gritty details in our bio. Thanks a bunch!`)
        if ((ugcData.data[0].networkId == 18 || ugcData.data[0].networkId == 2) && requestMode == 0) setTimeout(() => window.open(`https://ig.me/m/${ugcData.data[0].username}`, '_blank'), 400)
        else setTimeout(() => window.open(ugcData.data[0].link, '_blank'), 400)
        setTimeout(() => {
            window.location.replace(`${HOST_URL_TAGGSHOP}content/moderation/${localStorage.getItem('wallId')}`)
        }, 1000);
    }


    render() {
        const { ugcData, custom_msg, setStep, requestMode } = this.props
        const { openSteps, copied, hash } = this.state;
        return (
            <div className="" id="contact-md">
                <div className="ugc_steps ugc_modal_pending_second_inner_box flex-grow-1 ugc-pending-url modal-body">
                    <div className="ugc-req-title">
                        <h2>Your UGC Request is Ready</h2>
                        {/* <span>Copy and paste link in the comments or original post</span> */}
                    </div>
                    <div className="form-group">
                        <textarea row="5" cols="7" readOnly="" value={requestMode == 0 ? custom_msg : `${custom_msg} Just reply with #${hash}  if you're up for it, and you can find all the nitty-gritty details in our bio. Thanks a bunch!` || ""}></textarea>
                    </div>
                    {requestMode == 1 ? <>
                        <h3 className="mb-2 fs-7 mt-3">
                            <span className="fw-medium">Enter Hashtag for UGC Rights
                                <strong className="text-danger">*</strong>
                            </span>
                        </h3>
                        <div className="mb-4">
                            <input type="text" id="ugc_rights_hash_value" className="form-control form-control-lg" defaultValue={hash} onChange={e => this.setState({ hash: e.target.value })} />
                        </div>
                    </> : <></>}
                    {requestMode == 0 ? <div className="form-group ugc_link_share_copy_box ugc_modal_pending_link my-4">
                        <div className="input-group input-group-lg copy_txt">
                            <div className="input-group-prepend">
                                <span className="input-group-text">
                                    <i className="fa fa-link" aria-hidden="true"></i>
                                </span>
                            </div>
                            <span className="form-control ugcBityUrl">{ugcData.bitlyurl}</span>
                        </div>
                        <div className="my-6">
                            <div className="ugc-copryurl">
                                <input type="button" className="btn btn-primary ugc_copy_text_button w-100" onClick={() => this.copyText()} value="Copy URL" />
                            </div>
                            {copied ? <span id="ugc-copy-success-alert1" className="ugc_copy_success_alertt text-success">Copied to clipboard</span> : null}
                        </div>
                    </div> : <></>}
                    <div className="ugc_modal_pending_link"></div>
                    {/* <a href="" className="btn btn-outline-primary w-100 download_ugc_link">
                    <i className="fa fa-download"></i>
                    Download
                </a> */}
                    <div className="ugc-stepstoproceed">
                        <a href="javascript:void(0);" className="ugc-togglesteps " onClick={() => this.setState({ openSteps: !openSteps })}>
                            Steps to proceed
                            <i className="fas fa-chevron-down" style={openSteps ? { transform: 'rotate(180deg)' } : {}}></i>
                        </a>
                        <ul className={`list-unstyled`} style={openSteps ? { display: "block" } : {}}>
                            <li>You have successfully generated a "UGC Request" for this post.</li>
                            <li>Quickly review your Request before sending it to the owner.</li>
                            <li>Click on the "Raise Request" button located in the right bottom corner of the screen and then you will be redirected to the user's original post.</li>
                            <li>Paste the link of "UGC Request" in the comments section of the original post.</li>
                            <li>You will get UGC Rights asap post owner accept your request and allow you to use their post.</li>
                        </ul>
                    </div>
                </div>
                <div className="ugc-peingdone modal-footer text-right">
                    <input className="btn btn-secondary mr-3" type="button" value="Back" onClick={() => setStep(2)} />
                    <input className="btn btn-primary" type="button" value="Raise Request" onClick={() => this.raiseReq()} />
                </div>
            </div>
        )
    }
}

