import React, { Component } from 'react'
import { toast } from 'react-toastify';
import moment from 'moment'
import { Modal } from "react-bootstrap";
import { fetchPostData, autoCompleteSearch, onUpdateTags } from '../../../../../../actions/moderationActions'
import { checkIfStringHasSpecialChar, checkWordCount } from '../../../../../../utils'
import { HOST_URL } from '../../../../../../actions/api'
import ProfileIMG from '../view/grid/profileImg'
import { noImage } from '../../../../../../actions/api'
import './edittags.css'

class EditTags extends Component {
    state = { searchData: [], searchPost: "", selected: "", submitTags: [], isLoading: false, isRequest: false, isRemoved: 0, postTypes: "0", postTagAction: "1", totalCountTag: "0", tagRemoveAction: "0" }
    componentDidMount() {
        const { data } = this.props;
        let submitTags = [];
        if (data && String(data.tags).split(",").length) {
            String(data.tags).split(",").map((iTag) => {
                if (iTag && iTag.length > 0) {
                    submitTags.push(iTag)
                }
            })
            this.setState({ submitTags: submitTags })
        }
        this.onSearchText()
        this.setState({ isRemoved: 0 })
    }
    onLoad = () => {
        const { searchPost } = this.state;
        if (searchPost.length > 0) {
            this.setState({ isLoading: true })
            autoCompleteSearch(searchPost).then((response) => {
                const { tags, status } = response.data;
                if (status) this.setState({ searchData: tags, isLoading: false })
                else this.setState({ searchData: [], isLoading: true })

            })
        }
        else { this.setState({ searchData: [], isLoading: false }) }
    }
    onSearchText = () => {
        let textInput = document.getElementById('searchTagpost');
        let timeout = null;
        textInput.addEventListener('input', (e) => {
            clearTimeout(timeout);
            timeout = setTimeout(() => {
                this.setState({ searchPost: textInput.value }, () => this.onLoad())
            }, 1000);
        });
    }
    onClickToSearch = (selectSearch) => event => {
        let textInput = document.getElementById('searchTagpost');
        textInput.value = selectSearch;
        this.setState({ searchPost: selectSearch, selected: selectSearch, searchData: [] })
    }
    onClickToAddTags = event => {
        let { submitTags, selected, searchPost, totalCountTag } = this.state;
        let updateTag = selected && selected.length > 0 ? selected : searchPost;
        //console.log("onClickToAddTags", submitTags, updateTag)
        if (checkWordCount(updateTag)) {
            if (!checkIfStringHasSpecialChar(updateTag)) {
                if (!submitTags.includes(updateTag)) {
                    submitTags.push(updateTag)
                    let textInput = document.getElementById('searchTagpost');
                    textInput.value = "";
                    totalCountTag = parseInt(totalCountTag) + 1;
                    this.setState({ selected: "", searchPost: "", submitTags, searchPost: "", totalCountTag })
                }
                else { toast.error('Tag already exists.'); }
            }
            else { toast.error('Special character not allow.'); }
        }
        else { toast.error('Please enter minimum 3 characters.'); }
        this.onClickToSave() 
    }
    onClickToRemoveTags = (index) => event => {
        let { submitTags, isRemoved, tagRemoveAction } = this.state;
        submitTags.splice(index, 1)
        tagRemoveAction = parseInt(tagRemoveAction) + 1;
        this.setState({ submitTags, isRemoved: 1, tagRemoveAction },()=>this.onClickToSave());
    }
    onClickToSave = event => {
        let { selected, searchPost, submitTags, isRemoved, tagRemoveAction, postTagAction, postTypes, totalCountTag } = this.state;
        if (selected || searchPost || parseInt(totalCountTag) > 0 || parseInt(tagRemoveAction) > 0) {
            this.setState({ isRequest: true })
            const { data, filter } = this.props
            let saveObj = { selectedTags: submitTags.join(), postId: data.id, postTypes: postTypes, wallId: String(localStorage.getItem('wallId')), postTagAction: postTagAction, totalCountTag: String(totalCountTag), tagRemoveAction: tagRemoveAction, tags: submitTags.join(), db_table: filter.db_table, };
            this.props.onUpdateTags(saveObj, () => {
                this.setState({ isRequest: false })
              //  this.props.clickToCloseAddTag()
            });
        }
        else { toast.error('Please enter tags.'); }
    }

    render() {
        const { open, clickToCloseAddTag, data } = this.props
        const { searchData, submitTags, isLoading, searchPost, isRequest } = this.state;
        return data && Object.keys(data).length > 0 ? <Modal show={open} onHide={clickToCloseAddTag} backdrop="static" keyboard={false} centered className="tagModal addTagModal">
            <div classname="addTagModal_head">
                <div className="addTagModal_header">
                    <div className="addTagModal_headerLabel">Add Tags</div>
                    <div className="addTagModal_closeBtn" onClick={()=>clickToCloseAddTag()}>
                        <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 20 20" fill="none">
                            <path d="M19.457 18.8906C19.1055 19.2422 18.4609 19.2422 18.1094 18.8906L10.375 11.0977L2.58203 18.8906C2.23047 19.2422 1.58594 19.2422 1.23438 18.8906C0.882812 18.5391 0.882812 17.8945 1.23438 17.543L9.02734 9.75L1.23438 2.01562C0.882812 1.66406 0.882812 1.01953 1.23438 0.667969C1.58594 0.316406 2.23047 0.316406 2.58203 0.667969L10.375 8.46094L18.1094 0.667969C18.4609 0.316406 19.1055 0.316406 19.457 0.667969C19.8086 1.01953 19.8086 1.66406 19.457 2.01562L11.6641 9.75L19.457 17.543C19.8086 17.8945 19.8086 18.5391 19.457 18.8906Z" fill="white"></path>
                        </svg>
                    </div>
                </div>
            </div>
            <Modal.Body className="p-0">
                <div className="d-flex flex-wrap addTag_modal_inner">
                    <div className="addTagModal_alignLeft">
                        <div className="addTag_image">
                            <div className="addTag_media_holder">
                                <div className="addTag_image_holder">
                                    {data.postFile ? <img className="img-fluid completeLazyLoad addTagPostImage" src={data.postFile} id="tag_post_image" alt="tag post image" onError={event => {
                                        event.target.src = noImage
                                    }} /> : null}
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="addTagModal_alignRight" id="multipleTagSection">
                        <div className="addTag_form_main mt-lg-0 mt-5">
                            <div className="form-group d-flex">
                                <div className="w-100 ">
                                    <div className={`position-relative spinner-right addTag_formArea ${isLoading ? `spinner` : ``}`}>
                                        <input type="text" className="form-control postTagFilter" id="searchTagpost" autoComplete="off" placeholder="Enter your tag." />
                                        <div className="autoSuggestionList addtag_postTagsSugg">
                                            {
                                                (searchData && searchData.length > 0) ? <div className="dropdown-menu-list">
                                                    <ul className="list-unstyled navi navi-hover">
                                                        {
                                                            searchData.map((sItem, indexItem) => <li key={indexItem} tags={sItem} data-id={sItem} className="image-tags navi-item" onClick={this.onClickToSearch(sItem)}>
                                                                <a className="navi-link">{sItem}</a>
                                                            </li>)
                                                        }

                                                    </ul>
                                                </div> : null
                                            }
                                        </div>
                                    </div>
                                </div>
                                <div className="ml-3 d-flex align-items-center">
                                    <button type="button" className={`addTags_btn btn btn-primary ${String(searchPost).length > 0 ? '' : 'disabled'}`} onClick={this.onClickToAddTags}><i className="fa fa-plus"></i></button>
                                </div>
                            </div>
                            <div className="showTagsNote" style={submitTags && submitTags.length > 0 ? { display: 'none' } : {}}>
                                <ul>
                                    <li>Add tags using keywords relevant to the content and click on the + Button.</li>
                                    <li>Tags will help you improve content filtering and ease moderation process.</li>
                                </ul>
                            </div>
                            <div className="addTags-list">
                                <ul className="addTags-list_postTags">
                                    {
                                        submitTags && submitTags.length > 0 && submitTags.map((tag, tagKey) => {
                                            return (tag && tag.length > 0) ? (tag !== 'Tagalog' && tag !== '') ? <li className={tagKey}><span>{tag}</span><a className="removeSelectedTag" href="javascript:void(0)" alt={tagKey} wallId={localStorage.getItem('wallId')} postId={data.id} removeTag={tag} onClick={this.onClickToRemoveTags(tagKey)}><i className="ki ki-close"></i></a></li> : null : null
                                        })
                                    }

                                </ul>
                            </div>
                        </div>

                        <div className="addTags_card_cont">
                            <div className="d-flex align-items-center justify-content-between mb-4">
                                <div className="d-flex align-items-center m_grid_author">
                                    <div className="flex-shrink-0 mr-3 mt-1">
                                        <ProfileIMG item={data} cssClass="symbol symbol-circle symbol-lg-75 addTags_profile_img" />
                                    </div>
                                    <div className="d-flex flex-column addTags_user_content">
                                        <a href={`${HOST_URL}widget`} target="_blank" className="addTags_post_title">
                                            {data.postname}
                                        </a>
                                        <div className="d-flex align-item-center addTags_grid_userinfo">
                                            <span className="addTags_grid_uername text-nowrap"> @ {data.username} </span>
                                            <span className="sepratedot"></span>
                                            <span className="addTags_timePost text-nowrap">{ moment(new Date(data.createdAt * 1000)).fromNow() }</span>
                                        </div>
                                    </div>
                                </div>
                                <div className="d-flex flex-column tag_social_icon">
                                    <i className={`fab ${data.neticon}`}></i>
                                </div>
                            </div>
                            <div className="addTags_des">
                                {data.postcontent}
                            </div>
                        </div>

                    </div>
                </div>
            </Modal.Body>
            {/* <Modal.Footer>
                <button type="button" className="btn btn-secondary deleteAllPost px-5 d-none">Delete All</button>
                <button type="submit" className={`btn btn-primary savePost px-5 btn btn-primary savePost px-5 ${isRequest ? `spinner spinner-white spinner-right disabled` : ``}`} onClick={this.onClickToSave}>Save</button>
            </Modal.Footer> */}
        </Modal> : null
    }
}


